var util = { };

const dataUrlRegex =  /^data:([a-z]+\/[a-z0-9-+.]+(;[a-z0-9-.!#$%*+.{}|~`]+=[a-z0-9-.!#$%*+.{}()_|~`]+)*)?(;base64)?,([a-z0-9!$&',()*+;=\-._~:@\/?%\s<>]*?)$/i;

let coreUtil = null;

util.generateUUID = function() {
    var d = new Date().getTime();//Timestamp
    var d2 = (window.performance && window.performance.now && (window.performance.now()*1000)) || 0;//Time in microseconds since page-load or 0 if unsupported
    return 'xxxxxxxx-xxxx-4xxx-yxxx-xxxxxxxxxxxx'.replace(/[xy]/g, function(c) {
        var r = Math.random() * 16;//random number between 0 and 16
        if(d > 0){//Use timestamp until depleted
            r = (d + r)%16 | 0;
            d = Math.floor(d/16);
        } else {//Use microseconds since page-load if supported
            r = (d2 + r)%16 | 0;
            d2 = Math.floor(d2/16);
        }
        return (c === 'x' ? r : (r & 0x3 | 0x8)).toString(16);
    });
}

util.getBase64FromImage = (img, format) => {
    let data = null;
    if (!!img.src && img.src.startsWith('data:')) {
        if (dataUrlRegex.test(img.src)) {
            data = img.src;
        }
    }
    if (data == null) {
        const canvas = document.createElement('canvas');
        const ctx = canvas.getContext('2d');
        canvas.width = img.width;
        canvas.height = img.height;
        ctx.drawImage(img, 0, 0);
        data = canvas.toDataURL();
    }
    if (data.indexOf('base64,') >= 0) {
        data = data.substring(data.indexOf('base64,') + 7)
    }
    return data;
}

util.mouseLocation = (event) => {
  var x = event.pageX;
    var y = event.pageY;
    if (x == null || y == null || typeof x == "undefined" || typeof y == "undefined") {
        if (event.changedTouches && event.changedTouches.length > 0) {
            var touch = event.changedTouches[0];
            x = touch.pageX;
            y = touch.pageY;
        }
    }
    if (x == null || y == null || typeof x == "undefined" || typeof y == "undefined") {
        return null;
    }
    return {x: x, y: y};
}

util.hasTouch = () => {
    var hasTouchScreen = false;
    if ("maxTouchPoints" in navigator) {
        hasTouchScreen = navigator.maxTouchPoints > 0;
    } else if ("msMaxTouchPoints" in navigator) {
        hasTouchScreen = navigator.msMaxTouchPoints > 0;
    } else {
        var mQ = window.matchMedia && matchMedia("(pointer:coarse)");
        if (mQ && mQ.media === "(pointer:coarse)") {
            hasTouchScreen = !!mQ.matches;
        } else if ('orientation' in window) {
            hasTouchScreen = true; // deprecated, but good fallback
        } else {
            // Only as a last resort, fall back to user agent sniffing
            var UA = navigator.userAgent;
            hasTouchScreen = (
                /\b(BlackBerry|webOS|iPhone|IEMobile)\b/i.test(UA) ||
                /\b(Android|Windows Phone|iPad|iPod)\b/i.test(UA)
            );
        }
    }
    return hasTouchScreen;
}

util.loadScript = async (path) => {
    return new Promise((resolve, reject) => {
        const script = document.createElement("script");
        script.src = path;
        script.async = true;
        script.onload = () => {
            resolve();
        }
        script.onerror = (err) => {
            console.log(err)
            reject(err);
        }
        document.body.appendChild(script);
    });
}

util.getModifiersFromEvent = (e) => {
    let value = 0;
    if (e.shiftKey) {
        value = value | 1;
    }
    if (e.ctrlKey) {
        value = value | 2;
    }
    if (e.altKey) {
        value = value | 4;
    }
    if (e.metaKey) {
        value = value | 16;
    }
    return value;
}

util.setModifiersFromEvent = (e, pe) => {
    let value = util.getModifiersFromEvent(e);
    if (value != 0) {
        let modifiers = new window.Module.avabodh_lekh_event_Modifiers()
        modifiers.value = value;
        pe.setModifiers(modifiers)
    }
}

util.fontStr = (style) => {
    let str = '';
    if (style.effectiveFontStyle() == window.Module.avabodh_lekh_Style_FontStyle.FontStyleItalic) {
        str += 'italic ';
    }
    if (style.effectiveFontWeight() == window.Module.avabodh_lekh_Style_FontWeight.FontWeightBold) {
        str += 'bold ';
    }

    let fz = style.effectiveFontSize();
    let mult = style.effectiveFontMultiplier();
    if (mult >= 0 && mult != 1.0) {
        fz = fz * mult;
    }
    str += fz + 'px'
    let ff = style.effectiveFontFamily();
    if (ff != '') {
        str += ' ' + ff + ',';
    }
    str +=  " sans-serif";
    return str;
}
 
util.colorStr = (cl, disabled) => {
    if (disabled) {
        if (coreUtil == null) {
            coreUtil = window.Module.avabodh_lekh_app_CoreUtil_instance();
        }
        cl = coreUtil.disabledColor(cl);
    }
    return`rgba(${cl.red}, ${cl.green}, ${cl.blue}, ${cl.alpha / 255})`;
}

