util.readFile = (rootType, path) => {
    let resp = {errorCode: -1, content: ''};
    if (rootType != window.Module.avabodh_lekh_FileInfo_RootType.Document) {
        return resp;
    }
    if (!path.startsWith('settings/')) {
        return resp;
    }

    let content = localStorage.getItem('files:' + path)
    if (!!content) {
        resp.content = content;
        resp.errorCode = 0;
    }
    
    return resp;
}

util.writeFile = (rootType, path, content) => {
    if (rootType != window.Module.avabodh_lekh_FileInfo_RootType.Document) {
        return -1;
    }
    if (!path.startsWith('settings/')) {
        return -1;
    }

    localStorage.setItem('files:' + path, content);
    return 0;
}

util.resizeImage = (imgToResize, size) => {
    const canvas = document.createElement("canvas");
    const context = canvas.getContext("2d");

    canvas.width = size.width;
    canvas.height = size.height;

    context.drawImage(
        imgToResize,
        0,
        0,
        size.width,
        size.height
    );
    let img = new Image();
    img.src = canvas.toDataURL();
    return img;
}

export default util;
  