import React, { useState } from "react";
import { AiOutlineClose, AiOutlinePlus } from "react-icons/ai";
import Dialog from "@material-ui/core/Dialog";
import InputField from "../../components/InputField/InputField";
import Select from "react-select";
import FullLoader from "../../components/Loaders/FullLoader";
import userInfo from "../../utils/UserInfo";
import { Link, NavLink, useHistory, useLocation } from "react-router-dom";
import docsManager from "../../utils/DocsManager";
import { useEffect } from "react";

export const NewBoard = ({ state, close }) => {
  const [type, setType] = useState("board");
  const [name, setName] = useState("");
  const [loading, setLoading] = useState(false);
  const [error, setError] = useState(null);
  // const history = useHistory();

  useEffect(() => {
    setType("board");
    setName("");
    setLoading(false);
    setError(null);
  }, [close]);

  const handleSubmit = e => {
    e.preventDefault();
    setLoading(true);
    docsManager
      .createDoc(docsManager.parent || userInfo?.data?.userid, type, name)
      .then(res => {
        close();
      })
      .catch(err => {
        if (err?.response?.data?.code === "doc_name_exists") {
          setError("Name exists");
        } else {
          setError(err?.response?.data?.message);
        }
      })
      .finally(() => {
        setLoading(false);
      });
  };

  const options = [
    { value: "board", label: "Board" },
    { value: "folder", label: "Folder" }
    // { value: "diagram", label: "Diagram" },
  ];

  return (
    <Dialog open={state} aria-labelledby="New Document" className="px-5">
      {loading && <FullLoader />}
      <form
        action=""
        className="py-3 w-64 lg:w-96 bg-white "
        onSubmit={handleSubmit}
      >
        <div className="flex justify-between items-center mb-5 px-2">
          <div />
          <h4 className="text-center text-xl m-0">Create New</h4>

          <AiOutlineClose className="text-xl cursor-pointer" onClick={close} />
        </div>
        <div className="px-3 lg:px-5">
          <Select
            options={options}
            className="mb-3"
            onChange={e => setType(e.value)}
            defaultValue="board"
            placeholder="Board"
          />
          <InputField
            label="Name"
            placeholder="Name"
            handleChange={e => setName(e.target.value)}
            value={name}
          />
          {error && !loading && (
            <span className="-mt-3 block italic text-red-500 text-sm">
              {error}
            </span>
          )}

          <div className="text-right">
            <button
              type="submit"
              className="text-white font-medium lg:text-lg px-3 py-1 bg-lkhRed outline-none border-none border-0 mx-auto"
            >
              Create
            </button>
          </div>
        </div>
      </form>
    </Dialog>
  );
};

const DashSidebar = ({ close }) => {
  const location = useLocation();
  const [openNew, setOpenNew] = useState(false);
  const [docsState, setDocsState] = React.useState(docsManager.state);
  const history = useHistory();

  React.useEffect(() => {
    var docsManagerCb = () => { setDocsState(docsManager.state) }
    docsManager.event.addListener(docsManagerCb);
    return () => {
      docsManager.event.removeListener(docsManagerCb);
    };
  }, []);

  return (
    <aside
      className="w-60 fixed top-0 left-0 bg-gray-100 h-screen px-6 py-5 transition-all"
      // ref={sideRef}
    >
      <NewBoard state={openNew} close={() => setOpenNew(false)} />
      <div className="text-right m-0 mb-0 lg:hidden">
        <AiOutlineClose className="text-2xl" onClick={close} />
      </div>

      <Link
        to="/docs/mydocs"
        className="flex items-center justify-center mb-10"
      >
        <img
          src={require("../../assests/icons/Logo.svg").default}
          className="w-8/12"
          alt="Logo"
        />
      </Link>
      <div className="w-full flex justify-center">
        <button
          className={`flex items-center justify-center text-white font-medium text-lg px-6 py-1 bg-lkhRed outline-none border-none border-0 mx-auto ${
            !docsManager.canCreateNewDoc()
              ? "cursor-not-allowed bg-opacity-30 mx-auto"
              : ""
          }`}
          onClick={() => {
            if (docsManager.canCreateNewDoc()) {
              setOpenNew(true);
            } else {
              return;
            }
          }}
        >
          <AiOutlinePlus className="mr-2 font-semibold" />{" "}
          <span className="m-0">New</span>
        </button>
      </div>

      <NavLink
        to="/docs/mydocs"
        className="block text-center mt-10 font-semibold text-lg"
        activeClassName="bg-white py-2"
      >
        My Documents
      </NavLink>

      <NavLink
        className="cursor-pointer block text-center mt-5 font-medium text-base "
        activeClassName="bg-white py-2"
        onClick={() => {}}
        to="/docs/shared"
        exact={true}
      >
        Shared with me
      </NavLink>
    </aside>
  );
};

export default DashSidebar;
