import {
  getRefreshTokenFromStorage,
  setRefreshTokenToStorage,
  removeRefreshTokenFromStorage,
} from "./ApiUtils";
import { refreshToken, getCurrentUser } from "./ApiRequests";
import { constant } from "./ApiConstants";
import integ from "../integ/integ";
import uiConfig from "../common/UIConfig";
import EventListener from "./EventListener";

/*
The user info can be used in react functional component like this

  const onUserLoginStateChange = () => {
    // handler for user login state change
  }

  useEffect(() => {
    userInfo.addListener(onUserLoginStateChange);
    userInfo.get();
    return () => {
      userInfo.removeListener(onUserLoginStateChange);
    }
  }, []);

  // use this check while rendering
  // userInfo.state == 'available' ==> user is logged in
  // userInfo.state == 'loading' ==> the user info is being fetched
  // userInfo.state == 'error' ==> unable to fetch the user info. redirect to login page

*/

const available = constant.userInfoAvailable;
const loading = constant.userInfoLoading;
const error = constant.userInfoError;

var getCalled = false;

// state can be one one of: loading, available, error
var userInfo = {
  state: loading,
  data: {},
  token: "",
  event: new EventListener()
};

userInfo.isLoggedIn = () => {
  return userInfo.state == available && !!userInfo.token;
}

userInfo.logout = () => {
  userInfo.token = "";
  userInfo.data = { };
  userInfo.state = "loading";
  getCalled = false;
  removeRefreshTokenFromStorage();
}

userInfo.get = () => {
  if (getCalled && userInfo.state != error) {
    return
  }
  getCalled = true;

  if (!uiConfig.isUsingLekhBackend()) {
    integ.getUser((state, token, info) => {
      userInfo.token = token;
      userInfo.data = info;
      userInfo.state = state;
      onChange();
    });
    return;
  }

  window.addEventListener('storage', handleStorageChange);
  userInfo.state = loading;
  fetchInfo(false);
};

userInfo.getAccessTokenOnDemand = async (refreshTokenStr) => {
  if (!refreshTokenStr) {
    return false;
  }
  var resp = null;
  try {
    resp = await refreshToken(refreshTokenStr);
  } catch(err) {
    console.log(err);
    return false;
  }
  userInfo.token = resp.data.access_token;
  try {
    resp = await getCurrentUser(resp.data.access_token);
  } catch (err) {
    console.log(err);
    return false;
  }
  userInfo.data = resp.data;
  return true;
}

userInfo.canUseGestures = () => {
  if (!userInfo.isLoggedIn()) {
    return false;
  }
  return userInfo.data.features && userInfo.data.features.gesture;
}

const handleStorageChange = () => {
  var loggedIn = userInfo.isLoggedIn();
  let refreshTokenStr = getRefreshTokenFromStorage();
  if (loggedIn && !!refreshTokenStr) {
    console.log("Not responding to storage change");
    return;
  }
  console.log("Responding to storage change");
  fetchInfo(true)
}

const onChange = () => {
  try {
    userInfo.event.trigger();
  } catch (err) {
    console.log(err);
  }
};

const fetchInfo = async (onStoreChange) => {
  let refreshTokenStr = getRefreshTokenFromStorage();
  if (!refreshTokenStr) {
    if (userInfo.state != error) {
      setErr();
    }
    return;
  }
  var wasLoggedin = userInfo.isLoggedIn()
  var resp = await tryToRefreshToken(refreshTokenStr);
  if (resp.new_refresh_token) {
    if (!onStoreChange) {
      setRefreshTokenToStorage(resp.new_refresh_token);
    }
    if (!wasLoggedin) {
      onChange();
    }
  } else {
    if (resp.do_retry) {
      retry(onStoreChange);
    } else {
      removeRefreshTokenFromStorage();
      setErr();
    }
  }
}

const setErr = () => {
  userInfo.state = error;
  userInfo.data = {};
  userInfo.token = '';
  onChange();
}

const tryToRefreshToken = async (refreshTokenStr) => {
  let resp = null;
  try {
    resp = await refreshToken(refreshTokenStr);
  } catch(err) {
    if (err.response && err.response.data && err.response.data.code) {
      let code = err.response.data.code;
      if (code == "code_expired" || code == "refresh_token_expired" ||
        code == "invalid_input" || code == 'login_failure' || code == 'unknown') {
        return {new_refresh_token: null, do_retry: false};
      }
    }
    console.log(err);
    return {new_refresh_token: null, do_retry: true};
  }

  let new_refresh_token = resp.data.refresh_token
  userInfo.token = resp.data.access_token;

  try {
    resp = await getCurrentUser(resp.data.access_token);
  } catch (err) {
    console.log(err);
    return {new_refresh_token: null, do_retry: true};
  }
  
  renewTokenBeforeExpiry();

  userInfo.data = resp.data;
  userInfo.state = available;

  return {new_refresh_token: new_refresh_token, do_retry: false}
};

const renewTokenBeforeExpiry = () => {
  // TODO: we should look at the expires_at
  // but for now we will refresh every half an hour
  let hafHour = 1000 * 60 * 30;
  setTimeout(() => {
    fetchInfo(false);
  }, hafHour);
};

const retry = (onStoreChange) => {
  setTimeout(() => {
    fetchInfo(onStoreChange);
  }, 1000 * 10);
};

export default userInfo;
