import $ from 'jquery';
import canvas from './Canvas'
import menuState from './MenuState'
import uiConfig from './UIConfig';

var AppInput = {
  showContextMenu:()=>{},
  showShapeProps: null
};

var drawPoints = [];
var drawing = false;
var transparentPanel = null;
var tnode = null;
var ignoreNextClick = false;

const TextAlignHorizontalDefault = 0;
const TextAlignHorizontalCenter = 1;
const TextAlignHorizontalLeft = 2;
const TextAlignHorizontalRight = 3;

function updateCursorLoc(x, y) {
  var docid = window.wsmanager.docid
  if (!docid || menuState.inputMode != menuState.modes.laser) {
    return;
  }

  var uiMappedPt = canvas.mapPtUIScroll({x:x, y:y});
  window.Module.inputFilter().pan(uiMappedPt, window.Module.InputType.OTHER);
}

function finishTextEditing() {
  if (tnode != null) {
    window.Module.finishTextEditing(tnode.val());
    tnode.remove();
    canvas.draw();
    tnode = null;
    ignoreNextClick = true;
    return true;
  }
  return false;
}

function initShapeTextEditor() {
  finishTextEditing()
  ignoreNextClick = false;

  var rect = window.Module.getTextEditRect();

  var vpp = canvas.toViewPort({x: rect.x, y: rect.y});

  var cx = vpp.x + rect.width / 2;
  var cy = vpp.y + rect.height / 2;

  var tid = Math.random() + "t";
  tnode = $("<textarea id='" + tid + "'></textarea>");
  tnode.val(window.Module.getTextForEditing());
  var style = window.Module.getTextEditStyle();
  var fontSize = style.effectiveFontSize() * style.effectiveFontMultiplier();
  var isFLoating = window.Module.isEditingFloatingText();

  var tattrs = {
    "resize": "none",
    "overflow": "hidden",
    "position": "absolute",
    "font": style.effectivePlatformFontFamily(),
    "line-height": fontSize + "px",
    "background-color": "transparent",
    "left": (vpp.x)+ "px", 
    "top": (vpp.y) + "px", 
    "width": rect.width + "px",
    "height": rect.height + "px",
    "z-index": "1000"
  }

  switch (style.effectiveTextAlignHorizontal().value) {
    case TextAlignHorizontalDefault:
    case TextAlignHorizontalCenter:
      tattrs["text-align"] = "center";
      break;
    case TextAlignHorizontalLeft:
      tattrs["text-align"] = "left";
      break;
    case TextAlignHorizontalRight:
      tattrs["text-align"] = "right";
      break;
    default:
      break;
  }
  tnode.css(tattrs);
  $("#drawContent").append(tnode);
  tnode.trigger("focus");
  tnode.on("blur", function(e) {
    finishTextEditing();
  });
  tnode.on("keyup", function(e) {
    if (e.code == "Escape" || e.key == "Escape") {
      finishTextEditing();
      ignoreNextClick = false;
    }
  });

  function updateSize() {
    if (!tnode) {
      return;
    }
    var text = tnode.val();
    var sz = window.Module.getTextEditSize(text, isFLoating);
    if (sz.height == 0) {
      sz.height = fontSize;
    }
    if (sz.width < 50) {
      sz.width = 50;
    }
    sz.height += fontSize / 2;
    tnode.css("top", (cy - sz.height / 2) + "px");
    tnode.css("height", sz.height + "px");
    if (isFLoating) {
      var width = sz.width + 150;
      tnode.css("width", width + "px");
      tnode.css("left", (cx - width / 2) + "px");
    }
    //console.log(sz);
  }
 
  tnode.on('change keyup paste', updateSize);
  updateSize();
}

const drawLaserCircle = function(x, y) {
  clearTransparentPanel();
  var ctx = transparentPanel.getContext("2d");
  ctx.beginPath();
  ctx.arc(x, y, 32, 0, 2 * Math.PI, false);
  ctx.fillStyle = 'red';
  ctx.fill();
  ctx.lineWidth = 1;
  ctx.strokeStyle = 'red';
  ctx.stroke();
}

const clearTransparentPanel = function() {
  if (transparentPanel == null) {
    transparentPanel = document.getElementById('transparentPanel')
  }
  var ctx = transparentPanel.getContext("2d");
  ctx.clearRect(0, 0, transparentPanel.width, transparentPanel.height);
}

const drawTemp = function() {
  clearTransparentPanel();
  var ctx = transparentPanel.getContext("2d");
  ctx.lineJoin = "round";
  ctx.lineCap = "round";

  if (drawPoints.length > 0) {
    window.Module.setCurrentStyleToCtx(ctx);
    ctx.beginPath();
    var dp = drawPoints[0];
    ctx.moveTo(dp.x, dp.y);
    
    for (var i = 0; i < drawPoints.length; i++) {
      ctx.lineTo(drawPoints[i].x, drawPoints[i].y);  
    }
    
    ctx.stroke(); 
  }
}

const dbclick = (x, y) => {
  if (uiConfig.readOnly()) {
    return;
  }
  finishTextEditing()
  var uiMappedPt = canvas.mapPtUIScroll({x:x, y: y});
  window.Module.inputFilter().tap(uiMappedPt, 2);
  canvas.draw();
  if (window.Module.inputReceiver().needTextEditing()) {
    initShapeTextEditor()
  }
}

AppInput.isEditingText = function() {
  return tnode != null;
}

AppInput.onResize = function() {
  transparentPanel = null;
}

AppInput.ignoreNextClick = function(val) {
  ignoreNextClick = val;
}

AppInput.click = function(x, y) {
  if (uiConfig.readOnly()) {
    return;
  }

  finishTextEditing()
  if (ignoreNextClick) {
    ignoreNextClick = false;
    return true;
  }
  if (menuState.inputMode == menuState.modes.move) {
    // Ignore click for move mode
    return;
  }
  if (menuState.inputMode == menuState.modes.text) {
    // Text mode. Edit text and return
    dbclick(x, y);
    return;
  }
  var uiMappedPt = canvas.mapPtUIScroll({x:x, y: y});
  if (menuState.inputMode == menuState.modes.draw) {
    var mpt = {x: canvas.maprx(uiMappedPt.x), y: canvas.mapry(uiMappedPt.y)};
    window.Module.addClickCircle(mpt);
  } else {
    window.Module.inputFilter().tap(uiMappedPt, 1);
  }
  canvas.draw();

  if (AppInput.showShapeProps) {
    AppInput.showShapeProps();
  }
}

AppInput.dbclick = function(x, y) {
    dbclick(x, y);
}

AppInput.drag = function(x, y, kind) {
    //console.log(x + ", " + y + "  ==  " + kind);
    finishTextEditing();
    if (uiConfig.readOnly()) {
        dragMoveMode(x, y, kind);
        return;
    }
    ignoreNextClick = false;
    if (menuState.inputMode == menuState.modes.move) {
        dragMoveMode(x, y, kind);
    } else if (menuState.inputMode == menuState.modes.text) {
      // Text mode
      if (kind == 2) {
        dbclick(x, y);
      }
    } else if (menuState.inputMode == menuState.modes.laser) {
      dragLaser(x, y, kind);
    }  else {
      dragNative(x, y, kind);
    }
}

function dragLaser(x, y, kind) {
  if (kind == 2) {
    clearTransparentPanel();
  } else {
    drawLaserCircle(x, y);
  }
  updateCursorLoc(x, y);
}

function dragNative(x, y, kind) {
    var it = window.Module.InputType.START;
    if (kind == 1) {
        it = window.Module.InputType.OTHER;
    } else if (kind == 2) {
        it = window.Module.InputType.END
    }
    var pt = {x: x, y: y }
    var uiMappedPt = canvas.mapPtUIScroll(pt);
    var mpt = {x: canvas.maprx(uiMappedPt.x), y: canvas.mapry(uiMappedPt.y)};

    if (it == window.Module.InputType.START && menuState.inDrawMode()) {
      if (window.Module.willDrawAt(mpt)) {
        drawing = true;
      }
    }

    if (drawing) {
      drawPoints.push(pt);
      drawTemp();
    } else {
      window.Module.inputFilter().pan(uiMappedPt, it);
      canvas.draw();
    }

    if (it == window.Module.InputType.END && drawing) {
      drawing = false;
      flushTempDrawing();
    }
}

function flushTempDrawing() {
  const mapPt = (pt) => canvas.mapPtUIScroll(pt);
  if (drawPoints.length > 2) {
    window.Module.inputFilter().pan(mapPt(drawPoints[0]), window.Module.InputType.START);
    for (var i = 1; i < drawPoints.length - 1; i++) {
      window.Module.inputFilter().pan(mapPt(drawPoints[i]), window.Module.InputType.OTHER);
    }
    var pt = drawPoints[drawPoints.length - 1];
    window.Module.inputFilter().pan(mapPt(pt), window.Module.InputType.END);
  }
  canvas.draw();
  drawPoints = [];
  drawTemp();
}

function dragMoveMode(x, y, kind) {
    canvas.move(x, y, kind);
    canvas.drawGrid();
    canvas.draw();
}

AppInput.contextmenu = function(x, y) {
  if (uiConfig.readOnly()) {
    return;
  }
  if (menuState.inputMode == menuState.modes.selection ||
    menuState.inputMode == menuState.modes.draw ||
    menuState.inputMode == menuState.modes.smartDraw) {
    AppInput.showContextMenu(true, x, y);
  }
}

AppInput.enter = function(x, y) {
}

AppInput.move = function(x, y) {
  updateCursorLoc(x, y)
}

AppInput.leave = function(x, y) {
}

export default AppInput;
