import util from "../../common/Util";
import $ from 'jquery';

const border = 1;
const padding = 2;

class TextEditor {
    constructor() {
        this.textArea = null;
        this.textInput = null;
        this.lekhCore = null;
    }

    updateMultiline(param, style) {
        if (this.textArea == null) {
            return;
        }

        this.textArea.css("left", (param.inRect.x - border - padding)+ "px");
        this.textArea.css("top", (param.inRect.y - border - padding) + "px");
        this.textArea.css("width", (param.inRect.width) + "px");
        this.textArea.css("height", (param.inRect.height) + "px");
    }

    editMultiline(param, style) {
        if (this.lekhCore == null) {
            this.lekhCore = window.Module.avabodh_lekh_app_LekhCore_instance();
        }

        // In case the single edit is still around
        this.finishSingleLine();

        if (param.isUpdate) {
            if (this.textArea == null) {
                return;
            }
            this.updateMultiline(param, style);
            return;
        }

        // starting new edit. Make sure older is gone
        this.finishMultiLine();

        var tid = Math.random() + "t";
        this.textArea = $("<textarea id='" + tid + "'></textarea>");
        this.textArea.val(param.text)
        var fontSize = style.effectiveFontSize() * style.effectiveFontMultiplier();
        var tattrs = {
            "outline": "none",
            "resize": "none",
            "overflow": "hidden",
            "position": "absolute",
            "font": util.fontStr(style),
            "line-height": fontSize + "px",
            "background-color": "transparent",
            "left": (param.inRect.x - border - padding)+ "px",
            "top": (param.inRect.y - border - padding) + "px",
            "width": param.inRect.width + "px",
            "height": param.inRect.height + "px",
            "border": border + 'px solid gray',
            "margin": 0,
            "padding": padding + "px",
            "z-index": "1000"
        }
        let ha = window.Module.avabodh_lekh_Style_TextAlignHorizontal;
        switch (style.effectiveTextAlignHorizontal()) {
            case ha.TextAlignHorizontalDefault:
            case ha.TextAlignHorizontalCenter:
              tattrs["text-align"] = "center";
              break;
            case ha.TextAlignHorizontalLeft:
              tattrs["text-align"] = "left";
              break;
            case ha.TextAlignHorizontalRight:
              tattrs["text-align"] = "right";
              break;
            default:
              break;
        }

        // For interpreted text
        if (param.defaultAlignAlways) {
            tattrs["text-align"] = "left";
        }

        this.textArea.css(tattrs);
        $("#drawContent").append(this.textArea);
        this.textArea.trigger("focus");

        let self = this;
        this.textArea.on("blur", function(e) {
            self.finishTextEditing();
        });
        this.textArea.on("keyup", function(e) {
            if (e.code == "Escape" || e.key == "Escape") {
                self.finishTextEditing();
            }
        });
        this.textArea.on('input propertychange', function(e) {
            self.lekhCore.platformTextEditorTextChanged(self.textArea.val());
        });
    }

    updateSingleLine(param, style) {

    }

    editSingleLine(param, style) {
        if (this.lekhCore == null) {
            this.lekhCore = window.Module.avabodh_lekh_app_LekhCore_instance();
        }

        // In case the multi line edit is still around
        this.finishMultiLine();

        if (param.isUpdate) {
            if (this.textInput == null) {
                return;
            }
            this.updateSingleLine(param, style);
            return;
        }

        // starting new edit. Make sure older is gone
        this.finishSingleLine();

        var tid = Math.random() + "t";
        this.textInput = $("<input type='text' id='" + tid + "' />");
        this.textInput.val(param.text)
        var fontSize = style.effectiveFontSize() * style.effectiveFontMultiplier();
        var tattrs = {
            "outline": "none",
            "resize": "none",
            "overflow": "hidden",
            "position": "absolute",
            "font": util.fontStr(style),
            "line-height": fontSize + "px",
            "background-color": "transparent",
            "left": (param.inRect.x - padding)+ "px",
            "top": (param.inRect.y - padding) + "px",
            "width": param.inRect.width + "px",
            "height": param.inRect.height + "px",
            "border": '0px',
            "margin": 0,
            "padding": padding + "px",
            "z-index": "1000"
        }
        let ha = window.Module.avabodh_lekh_Style_TextAlignHorizontal;
        switch (style.effectiveTextAlignHorizontal()) {
            case ha.TextAlignHorizontalDefault:
            case ha.TextAlignHorizontalCenter:
              tattrs["text-align"] = "center";
              break;
            case ha.TextAlignHorizontalLeft:
              tattrs["text-align"] = "left";
              break;
            case ha.TextAlignHorizontalRight:
              tattrs["text-align"] = "right";
              break;
            default:
              break;
        }
        this.textInput.css(tattrs);
        $("#drawContent").append(this.textInput);
        this.textInput.trigger("focus");

        let self = this;
        this.textInput.on("blur", function(e) {
            self.finishTextEditing();
        });
        this.textInput.on("keyup", function(e) {
            if (e.code == "Escape" || e.key == "Escape" || e.code == "Enter" || e.key == "Enter") {
                self.finishTextEditing();
            }
        });
        this.textInput.on('input propertychange', function(e) {
            self.lekhCore.platformTextEditorTextChanged(self.textInput.val());
        });
    }

    editText(param, style) {
        if (param.multiLine) {
            this.editMultiline(param, style);
        } else {
            this.editSingleLine(param, style);
        }
    }

    finishTextEditing() {
        this.finishMultiLine();
        this.finishSingleLine();
    }

    finishSingleLine() {
        if (this.textInput != null) {
            this.lekhCore.platformTextEditorFinished();
            this.textInput.remove();
            this.textInput = null;
        }
    }

    finishMultiLine() {
        if (this.textArea != null) {
            this.lekhCore.platformTextEditorFinished();
            this.textArea.remove();
            this.textArea = null;
        }
    }
}

const textEditor = new TextEditor();

export default textEditor;