import { Switch, Route } from "react-router-dom";
import AuthLayout from "../components/AuthLayout/AuthLayout";
import ConfirmUser from "../pages/auth/Confirm";
import ConfirmLogin from "../pages/auth/ConfirmLogin";
import EmailLogin from "../pages/auth/EmailLogin";
import Forgot from "../pages/auth/Forgot";
import Login from "../pages/auth/Login";
import OTPLogin from "../pages/auth/OTPLogin";
import Reset from "../pages/auth/Reset";
import SignUp from "../pages/auth/SignUp";
import PublicRoute from "./PublicRoute";

export const AuthRouteList = [
  { path: "/login", component: Login, exact: true },
  { path: "/email-login", component: EmailLogin, exact: true },
  { path: "/passwordless-login", component: OTPLogin, exact: true },
  { path: "/sign-up", component: SignUp, exact: true },
  { path: "/forgot", component: Forgot, exact: true },
  { path: "/user/reset", component: Reset, exact: true },
  { path: "/user/confirm", component: ConfirmUser, exact: true },
  { path: "/user/passwordless-login", component: ConfirmLogin, exact: true }
];

// To add a private route HOC when approved

const AuthRoutes = () => {
  return (
    <Switch>
      <main className="inner-route-body">
        <AuthLayout>
          {AuthRouteList.map(r => (
            <PublicRoute
              key={r.path}
              component={r.component}
              exact={r.exact}
              path={r.path}
            />
          ))}
        </AuthLayout>
      </main>
    </Switch>
  );
};

export default AuthRoutes;
