import React, { useState } from "react";
import { toast } from "react-toastify";
import InputField from "../../components/InputField/InputField";
import FullLoader from "../../components/Loaders/FullLoader";
import { updateCurrentUser } from "../../utils/ApiRequests";
import userInfo from "../../utils/UserInfo";
import { passwordCheck } from "../auth/AuthUtil";

const Settings = () => {
  const [loading, setLoading] = useState(false);
  const [updateForm, setupdateForm] = useState({
    email: userInfo?.data?.email,
    current_password: "",
    first_name: userInfo?.data?.first_name,
    last_name: userInfo?.data?.last_name
  });
  const [error, setError] = useState(true);

  const handleChange = e => {
    const name = e.target.name;
    const value = e.target.value;
    const newEntry = { [name]: value };
    setupdateForm({ ...updateForm, ...newEntry });
  };

  const handlePasswordChange = e => {
    const name = e.target.name;
    const value = e.target.value;
    const newEntry = { [name]: value };
    setError(passwordCheck.test(value));
    setupdateForm({ ...updateForm, ...newEntry });
  };

  const updateUser = e => {
    e.preventDefault();
    setLoading(true);
    updateCurrentUser(updateForm)
      .then(res => toast.success("Profile updated successfully"))
      .catch(err => toast.error(err.response.data.message))
      .finally(() => setLoading(false));
  };

  return (
    <div>
      {loading && <FullLoader />}
      <h1 className="lg:text-2xl font-medium m-0 mb-10">Update Profile</h1>

      <form
        className="w-full lg:w-5/12 shadow-md p-5 mb-10"
        onSubmit={updateUser}
      >
        <div className="w-full grid grid-cols-2 gap-5">
          <InputField
            name="first_name"
            label="First Name"
            placeholder="First Name"
            handleChange={handleChange}
            value={updateForm.first_name}
          />
          <InputField
            name="last_name"
            label="Last name"
            placeholder="Last Name"
            handleChange={handleChange}
            value={updateForm.last_name}
          />
        </div>

        <InputField
          name="email"
          label="Email"
          placeholder="you@address.com"
          required={true}
          type="email"
          // handleChange={handleChange}
          readOnly={true}
          value={updateForm.email}
        />

        <InputField
          name="current_password"
          type="password"
          placeholder="Current Password"
          //   required={true}
          label="Current Password"
          value={updateForm.current_password}
          handleChange={handleChange}
          //   error={!error}
          errorText="Password must be a minimum of 8 characters including a capital letter and special character"
        />

        <InputField
          name="new_password"
          type="password"
          placeholder="New Password"
          //   required={true}
          label="New Password"
          value={updateForm.new_password}
          handleChange={handlePasswordChange}
          error={!error}
          errorText="Password must be a minimum of 8 characters including a capital letter and special character"
        />

        <button
          type="submit"
          className="flex items-center justify-center text-white font-medium text-lg px-6 py-1 bg-lkhRed outline-none border-none border-0 mx-auto"
        >
          Update
        </button>
      </form>
    </div>
  );
};

export default Settings;
