import { Switch, Route, Redirect } from "react-router-dom";
import DashLayout from "../components/DashLayout/DashLayout";
import Dashboard from "../pages/Dashboard/Dashboard";
import Settings from "../pages/Dashboard/Settings";
import FullLoader from "../components/Loaders/FullLoader";
import { useEffect, useState } from "react";
import userInfo from "../utils/UserInfo";
import "../assests/index.css";
import "../assests/page.css";

export const DashRouteList = [
  { path: "/user/settings", component: Settings, exact: true },
  { path: "/docs/mydocs", component: Dashboard, exact: true },
  { path: "/docs/shared", component: Dashboard, exact: true },
  { path: "/docs/search", component: Dashboard, exact: true }
];

const DashRoutes = () => {
  const [userState, setUserState] = useState(userInfo.state);

  const onUserInfoChange = () => {
    setUserState(userInfo.state);
  }
  useEffect(() => {
    userInfo.event.addListener(onUserInfoChange);
    userInfo.get();

    return () => {
      userInfo.event.removeListener(onUserInfoChange);
    };
  }, []);

  const PrivateRoute = ({ component: Component, ...rest }) => {
    return (
      <Route
        {...rest}
        render={props =>
          userInfo.state === "available" ? (
            <Component {...props} />
          ) : (
            <Redirect to="/login" />
          )
        }
      />
    );
  };

  return (
    <>
      {userInfo.state === "loading" && <FullLoader />}
      <Switch>
        {userInfo.state !== "loading" && (
          <main className="inner-route-body">
            <DashLayout>
              {DashRouteList.map(r => (
                <PrivateRoute
                  key={r.path}
                  component={r.component}
                  exact={r.exact}
                  path={r.path}
                />
              ))}
            </DashLayout>
          </main>
        )}
      </Switch>
    </>
  );
};

export default DashRoutes;
