import React, { useState } from "react";
import FullLoader from "../../components/Loaders/FullLoader";
// import { toast } from "react-toastify";
import DocumentListItem from "../../components/DashboardItems/DocumentListItem";
import docsManager from "../../utils/DocsManager";
import { AiOutlineRight, AiOutlinePlus } from "react-icons/ai";
import { useHistory } from "react-router-dom";
import { NewBoard } from "../../components/DashLayout/DashSidebar";

const Dashboard = props => {
  const [docsState, setDocsState] = React.useState(docsManager.state);
  const [openNew, setOpenNew] = useState(false);

  const history = useHistory();

  React.useEffect(() => {
    var docsManagerCb = () => { setDocsState(docsManager.state) }
    docsManager.event.addListener(docsManagerCb);
    docsManager.setRoute(props.location.pathname, props.location.search);
    docsManager.reload();

    return () => {
      docsManager.event.removeListener(docsManagerCb);
    };
  }, []);

  return (
    <div className="pb-40">
      {docsManager.state == "loading" && <FullLoader />}
      <NewBoard state={openNew} close={() => setOpenNew(false)} />
      {docsManager.state == "available" && (
        <>
          <div className="flex items-center space-x-2">
            <h1
              className="text-xl font-medium m-0 mb-3 lg:mb-10 cursor-pointer"
              onClick={() => {
                docsManager.popParentTo(null);
                history.push(docsManager.getRoute());
                docsManager.reload();
              }}
            >
              {docsManager.getRootName()}
            </h1>

            {docsManager?.path &&
              docsManager.path.map((m, i) => (
                <h1
                  className={`flex items-center space-x-2 text-xl font-medium m-0 mb-2 lg:mb-10 ${
                    i !== docsManager.path.length - 1 ? "cursor-pointer" : ""
                  }`}
                  onClick={() => {
                    if (i !== docsManager.path.length - 1) {
                      docsManager.popParentTo(m.docid);
                      history.push(docsManager.getRoute());
                      docsManager.reload();
                    }
                  }}
                  key={i}
                >
                  <AiOutlineRight className="text-xl font-semibold" />
                  <span> {m?.name} </span>
                </h1>
              ))}
          </div>

          {docsManager.state == "available" &&
            docsManager.canCreateNewDoc() &&
            docsManager?.docs?.length > 0 && (
              <button
                className={`mb-5 lg:hidden flex items-center justify-center text-white font-medium text-lg px-6 py-1 bg-lkhRed outline-none border-none border-0`}
                onClick={() => {
                  setOpenNew(true);
                }}
              >
                <AiOutlinePlus className="mr-2 font-semibold" />{" "}
                <span className="m-0">New</span>
              </button>
            )}
          <div className="w-full grid grid-cols-2 md:grid-cols-4 lg:grid-cols-6 gap-4 pb-20">
            {docsManager.docs.map((m, i) => {
              return <DocumentListItem m={m} key={i} />;
            })}
          </div>
        </>
      )}
      {docsManager.state == "available" &&
        docsManager.mode == "mydocs" &&
        !(docsManager?.docs?.length > 0) && (
          <div className="w-full flex flex-col items-center justify-center space-y-2">
            <div className="w-full flex justify-center flex-col items-center">
              <h3 className="text-lkhDGrey">
                You do not have any document in this folder. Please add a
                document
              </h3>
              <button
                className={`flex items-center justify-center text-white font-medium text-lg px-6 py-1 bg-lkhRed outline-none border-none border-0 mx-auto`}
                onClick={() => {
                  setOpenNew(true);
                }}
              >
                <AiOutlinePlus className="mr-2 font-semibold" />{" "}
                <span className="m-0">New</span>
              </button>
            </div>
          </div>
        )}
    </div>
  );
};

export default Dashboard;
