import { AiOutlineClose } from "react-icons/ai";
import { Dialog } from "@material-ui/core";
// import Select from "react-select";
import { useEffect, useState } from "react";
import MiniLoader from "../Loaders/MiniLoader";
import { toast } from "react-toastify";
import docsManager from "../../utils/DocsManager";
import userInfo from "../../utils/UserInfo";
import { MoveDoc } from "../../utils/ApiRequests";
import { AiFillFolder, AiOutlineRight } from "react-icons/ai";
import { BiArrowBack } from "react-icons/bi";
// import { IoArrowBackCircleSharp } from "react-icons/io5";

const MoveModal = ({ state, close, m }) => {
  const [loading, setLoading] = useState(true);
  const [data, setData] = useState();
  const [parent, setParent] = useState(userInfo?.data?.userid);
  const [error, setError] = useState(null);
  const [parentDoc, setParentDoc] = useState(null);

  const [cache, setCache] = useState(new Map());
  const [childCache, setChildCache] = useState(new Map());

  const getDoc = async (docid) => {
    if (!docid || docid == userInfo?.data?.userid) {
      return {docid: docid, name: 'Root Folder', parent: null}
    }
    if (cache.has(docid)) {
      return cache.get(docid);
    }
    var doc = await docsManager.getADoc(docid);
    cache.set(docid, doc);
    return doc;
  }

  const getChildren = async (docid) => {
    if (childCache.has(docid)) {
      return childCache.get(docid);
    }
    var children = await docsManager.getChildFolders(docid);
    childCache.set(docid, children);
    for (let item of children) {
      cache.set(item.docid, item);
    }
    return children;
  }

  useEffect(() => {
    setLoading(true);
    getDoc(parent)
    .then(resp=>{
      setParentDoc(resp);
    })
    .then(()=> getChildren(parent || userInfo?.data?.userid))
    .then(res => {
      setData(res);
      setLoading(false);
    })
    .catch(err => console.log(err.response));
  }, [parent]);

  // console.log(options);
  const handleSubmit = e => {
    e.preventDefault();
    setLoading(true);
    MoveDoc(m?.docid, { parent: parent || userInfo?.data?.userid, name: m?.name })
      .then(res => {
        docsManager.reload();
        close();
      })
      .catch(err => toast.error(err?.response?.data?.message))
      .finally(() => {
        setLoading(false);
      });
  };

  return (
    <Dialog open={state} className="px-5" aria-labelledby="Share">
      <form
        className={`py-2 h-72 w-64 lg:w-96 bg-white`}
        onSubmit={handleSubmit}
        // onClick
      >
        <div className="flex items-center justify-between my-2 px-3">
          {parent != userInfo?.data?.userid ? (
            <BiArrowBack
              className="text-xl cursor-pointer"
              onClick={() => {
                setParent(parentDoc.parent);
              }}
            />
          ) : (
            <div />
          )}

          <h4 className="text-center m-0 w-44 truncate">
            {" "}
            {parentDoc?.name || "Root Folder"}
          </h4>

          <AiOutlineClose
            className="text-xl cursor-pointer"
            onClick={() => close()}
          />
        </div>
        <hr />

        {loading && (
          <div className="mt-6">
            <MiniLoader />
          </div>
        )}
        {!loading && (
          <>
            <div className="h-1/2 space-y-6 py-2 overflow-y-auto px-3">
              {data?.map((m, i) => (
                <div
                  className={`move-grid text-lg cursor-pointer hover:bg-gray-200`}
                  key={i}
                >
                  <div
                    className="flex items-center space-x-2 cursor-pointer"
                    onClick={() => {
                      setParent(m.docid);
                    }}
                  >
                    <AiFillFolder />
                    <span className="w-40  lg:w-44 truncate">{m.name}</span>
                  </div>
                  <AiOutlineRight
                    className="cursor-pointer text-right ml-auto"
                    onClick={() => setParent(m.docid)}
                  />
                </div>
              ))}
            </div>

            <div className="text-right mt-8 px-3">
              <button
                type="submit"
                className="text-white font-medium lg:text-lg px-3 py-1 bg-lkhRed outline-none border-none border-0 mx-auto"
              >
                Move Here
              </button>
            </div>
          </>
        )}
      </form>
    </Dialog>
  );
};

export default MoveModal;
