import canvas from './Canvas'

import appEvent from './AppEvent'
import shapes from '../shapelib/Shapes'

import luaScripts from '../data/lua_script.json'
import userInfo from '../utils/UserInfo'
import {constant} from '../utils/ApiConstants'
import remoteResouceManager from '../utils/RemoteResourceManager'
import uiConfig from './UIConfig'
import integ from '../integ/integ'
import util from './Util'
import httpUtil from '../utils/HttpUtil';
import messaging from './Messaging'

import EventListener from '../utils/EventListener'

var initializer = { };

var shapesInitDone = false;
var docDetailFetched = false;
var docStarted = false;

var apiBasePath = constant.baseUrl + "/" + constant.backendVersion;

// Adding into window as this is being used by c++ code
window.wsmanager = {
    // state values: init, ready, disconnected, notfound, offline, prompt
    state: "init",
    cb: null,
    docid: null,
    event: new EventListener(),
    setState: function(state) {
        window.wsmanager.state = state;
        if (window.wsmanager.cb != null) { window.wsmanager.cb(state); }
        canvas.onConnectionStateChanged();
    },
    permission: 'write',
    readOnly: () => window.wsmanager.permission != 'write',
}

window.toggleDebug = function() {
    window.Module.toggleDebug();
}

window.getLekhResource = function(name) {
    var sc = luaScripts[name];
    if (sc) {
        return sc;
    }
    return "";
}

window.getBase64FromImage = (img) => {
    const canvas = document.createElement('canvas');
    const ctx = canvas.getContext('2d');
    canvas.width = img.width;
    canvas.height = img.height;
    ctx.drawImage(img, 0, 0);
    var data = canvas.toDataURL();
    if (data.indexOf('base64,') >= 0) {
        data = data.substr(data.indexOf('base64,') + 7)
    }
    return data;
}

window.getRemoteResource = remoteResouceManager.getResource;

var runtimeInitialized = false;
var initialInitDone = false;
var reactAppReady = false;

const appendScript = (scriptToAppend) => {
    const script = document.createElement("script");
    script.src = scriptToAppend;
    script.async = false;
    document.body.appendChild(script);
}

const setStyleAfterDocLoad = () => {
    //window.Module.inputReceiver().currentStyle().setStrokeWidth(4);
    window.Module.inputReceiver().currentStyle().setFontSize(18);
}

/*
window.Module = {
    onRuntimeInitialized: function () {
        runtimeInitialized = true;
        initializer.initInternal();
    }
};
*/

initializer.init = function(cb) {
    reactAppReady = true;
    window.wsmanager.cb = cb;
    //appendScript("a1/bean.min.js");
    httpUtil.initPlatformImpl();
    messaging.init();
    appendScript("a1/lekhapp.js");
    initializer.initInternal();
}

initializer.onDocDetailFetch = () => {
    docDetailFetched = true;
    startDoc();
}

initializer.initInternal = function() {
    if (initialInitDone || !reactAppReady || !runtimeInitialized) {
        return;
    }
    initialInitDone = true;
    var panel = document.getElementById("gridCanvas");
    if (!panel) {
        return;
    }
    var canvasElement = document.getElementById("drawCanvas");
    initialInitDone = true;

    window.Module.init(canvasElement);
    //window.Module.setZoom(70)
    canvas.setSize();
    canvas.drawGrid();
    
    shapes.initialize().then(function() {
        appEvent.attachEventHandlers();
        shapesInitDone = true;
        startDoc();
    });
}

initializer.createNewDoc = () => {
    var url = new URL(window.location);
    var ref = url.searchParams.get('ref');
    var xhttp = new XMLHttpRequest();
    xhttp.onreadystatechange = function() {
        if (this.readyState == 4) {
            if (this.status == 200) {
                var obj = JSON.parse(xhttp.responseText);
                var url = new URL(window.location);
                var newUrl = url.origin + "/?docid=" + obj.docid;
                window.location = newUrl;
            } else {
                window.wsmanager.setState('error');
            }
        }
    };
    var url = apiBasePath + "/docs"
    if (ref) {
        url += "?ref=" + ref;
    }
    xhttp.open("POST", url);
    xhttp.withCredentials = true;
    xhttp.send();
}

function startDoc() {
    if (!shapesInitDone || !docDetailFetched || docStarted) {
        return;
    }
    docStarted = true;

    if (!uiConfig.isUsingLekhBackend()) {
        integ.loadDoc();
        return;
    }

    var url = new URL(window.location);
    var docid = url.searchParams.get('docid');
    var np = url.searchParams.get('_new');
    var ref = url.searchParams.get('ref');
    var offline = url.searchParams.get('offline');
    if (docid) {
        var xhttp = new XMLHttpRequest();
        xhttp.onreadystatechange = function() {
          if (this.readyState == 4) {
            if (this.status == 200) {
              var touch = 'false';
              try {
                if (util.hasTouch()) {
                    touch = 'true';
                }
              } catch(e) {
              }
              var obj = JSON.parse(xhttp.responseText);
              let url = obj.location+'?_touch=' + touch;
              if (userInfo.isLoggedIn()) {
                url += '&access_token=' + userInfo.token;
              }
              window.wsmanager.start(docid, url);
            } else {
                if (this.status == 404) {
                    window.wsmanager.setState('notfound');
                } else if (this.status == 403) {
                    window.wsmanager.setState('forbidden');
                } else {
                  window.wsmanager.setState('error');
                }
            }
          }
        };
        var url = apiBasePath + "/docs/" + docid;
        xhttp.open("POST", url);
        xhttp.withCredentials = true;
        if (userInfo.isLoggedIn()) {
          xhttp.setRequestHeader('x-auth-token', userInfo.token);
        }
        xhttp.send();
    } else if (np == 'true') {
      window.wsmanager.setState('newwait');
    } else if (offline == 'true') {
        window.wsmanager.setState('offline');
    } else {
        window.wsmanager.setState('prompt');
    }
}

window.wsmanager.start = function (docid, location) {
    var timer;
  
    const ws = new window.WebSocket(location);
    window.Module.startDoc(docid);
  
    window.wsmanager.send = function(docid, data) {
      ws.send(data);
      return true;
    }
  
    ws.onopen = function(event) {
        console.log('connection opened: ', event);
        timer = window.setInterval(function() {
            ws.send('{}');
        }, 15000);
    };
  
    ws.onmessage = function(event) {
        window.Module.updateDoc(event.data);
        window.wsmanager.event.trigger(event.data);
        canvas.draw();
        if (window.wsmanager.state == 'init') {
            // The first update is supposed to load update
            // after loading the doc, we can change the default style
            var updateObj = JSON.parse(event.data);
            if (updateObj.type == 'update' && updateObj.updates &&
                updateObj.updates.type == 'load' && updateObj.updates.permission) {
                window.wsmanager.permission = updateObj.updates.permission;
            }
            setStyleAfterDocLoad();
            window.wsmanager.docid = docid;
            window.wsmanager.setState('ready');
        }
    };
  
    ws.onclose = function(event) {
        console.log('connection closed:', event);
        window.wsmanager.setState('disconnected');
        window.clearInterval(timer);
    };
  
    ws.onerror = function(event) {
        console.log('connection error: ', event);
        window.wsmanager.setState('disconnected');
        window.clearInterval(timer);
    };
}

export default initializer;
