import React, { useState } from "react";
import InputField from "../../components/InputField/InputField";
import { Link, useHistory } from "react-router-dom";
import MiniLoader from "../../components/Loaders/MiniLoader";
import { resetPassword } from "../../utils/ApiRequests";
import { toast } from "react-toastify";
import { useQuery } from "../../hooks/useQuery";
import { passwordCheck } from "./AuthUtil";

const Reset = () => {
  const query = useQuery();
  const code = query.get("code");
  const [passwordForm, setpasswordForm] = useState({
    password: "",
    code: code,
    cPassword: ""
  });
  const history = useHistory();
  const [loading, setLoading] = useState(false);
  const [error, setError] = useState(true);
  const [apiError, setApiError] = useState(null);
  
  const handleChange = e => {
    const name = e.target.name;
    const value = e.target.value;
    const newEntry = { [name]: value };
    setError(passwordCheck.test(value));

    setpasswordForm({ ...passwordForm, ...newEntry });
  };

  const proceed = () => {
    if (passwordForm.cPassword !== passwordForm.password) {
      return false;
    }

    if (passwordForm.password === "") {
      return false;
    }
    if (!error) {
      return false;
    }
    return true;
  };

  const handleSubmit = e => {
    e.preventDefault();
    setLoading(true);

    resetPassword(passwordForm)
      .then(res => {
        history.push("/login");
      })
      .catch(err => {
        setApiError("Unable to reset password");
        history.push("/forgot");
      })
      .finally(() => setLoading(false));
  };

  return (
    <div className="w-full min-h-screen flex justify-center items-center ">
      <form
        //   className=""
        onSubmit={handleSubmit}
        className="w-full lg:w-3/5 px-5 lg:px-10 py-3 lg:py-6 shadow-md"
      >
        <h1 className="font-medium text-xl  text-center mb-7">
          Reset password{" "}
        </h1>

        <InputField
          name="password"
          label="Password"
          placeholder="New Password"
          required={true}
          type="password"
          handleChange={handleChange}
          value={passwordForm.password}
          error={!error}
          errorText="Password must be a minimum of 8 characters including a capital letter and special character"
        />

        <InputField
          name="cPassword"
          label="Confirm Password"
          placeholder="Confirm Password"
          required={true}
          type="password"
          handleChange={handleChange}
          value={passwordForm.cPassword}
          error={passwordForm.password !== passwordForm.cPassword}
          errorText="Passwords do not match"
        />

        {apiError && !loading && (
          <span className="-mt-3 block italic text-red-500 text-sm">
            {apiError}
          </span>
        )}

        <div className="flex ">
          {loading ? (
            <MiniLoader />
          ) : (
            <button
              type="submit"
              className={`py-2 lg:py-3.5 px-12 font-Poppins bg-lkhRed text-white rounded-3xl outline-none border-0 ${
                !proceed() ? "bg-lkhGrey cursor-not-allowed" : ""
              }`}
              disabled={!proceed()}
            >
              Reset password
            </button>
          )}
        </div>

        <div className="space-y-1 mt-2">
          {/* <Link to="/forgot" className="text-lg font-medium block">
            Forgot password?
          </Link> */}
          <p className="text-lg font-medium">
            Have an account?{" "}
            <Link to="/login" className="font-normal">
              Log in
            </Link>
          </p>
        </div>
      </form>
    </div>
  );
};

export default Reset;
