import uiConfig from './UIConfig'
import integ from '../integ/integ'
import messaging from './Messaging'
import viewPort from '../components/ViewPort/ViewPort';
import platform from './Platform';
import mouseInput from './MouseInput';
import touchInput from './TouchInput';
import keyboardInput from './KeyboardInput';
import libnames from '../data/libnames.json'
import userInfo from '../utils/UserInfo';
import { getRefreshTokenFromStorage } from '../utils/ApiUtils';

var initializer = { };

var runtimeInitialized = false;
var initialInitDone = false;
var reactAppReady = false;
var shapesInitDone = false;
var uiInitDone = false;
var docStarted = false;
var userInfoFetched = false;
var isLoggedIn = false;

window.Module = {
    onRuntimeInitialized: function () {
        runtimeInitialized = true;
        initializer.initInternal();
    }
};

window.toggleDebug = function() {
    window.Module.toggleDebug();
}

const appendScript = (scriptToAppend) => {
    const script = document.createElement("script");
    script.src = scriptToAppend;
    script.async = false;
    document.body.appendChild(script);
}

const initShapes = async () => {
    // wait for fonts to be ready
    try {
        await document.fonts.ready;
    } catch (e) { }

    let lekhCore = window.Module.avabodh_lekh_app_LekhCore_instance();
    lekhCore.library().initNames(JSON.stringify(libnames));
    lekhCore.shapeManager().initGroupsAndSetDisplaySettings();

    shapesInitDone = true;
}

const initEventHandlers = () => {
    var uiCanvas = document.getElementById("uiCanvas");
    if (uiConfig.shouldAttachMouseEventHandler()) {
        mouseInput.attachEvents(uiCanvas);
        touchInput.attachEvents(uiCanvas);
    }
    if (uiConfig.shouldAttachKeyboardEventHandler()) {
        keyboardInput.attachEvents(uiCanvas);
    }  
}

const initUI = () => {
    if (uiInitDone || !shapesInitDone) {
        return;
    }
    uiInitDone = true;
    viewPort.initUI(true);
    let url = new URL(window.location);
    let offline = url.searchParams.get('offline');
    if (offline != 'true') {
        let hostEnv = window.Module.avabodh_lekh_app_HostEnv_instance();
        hostEnv.blockView('Loading..');
    }
    startDoc();
}

const fetchUserDetail = () => {
    userInfo.event.addListener(() => {
        if (isLoggedIn && !userInfo.isLoggedIn()) {
          window.location.reload();
          return;
        }
        
        if (!isLoggedIn && userInfo.isLoggedIn() && docStarted) {
          window.location.reload();
          return;
        }
  
        if (userInfo.isLoggedIn()) {
            let lekhCore = window.Module.avabodh_lekh_app_LekhCore_instance();
            lekhCore.backendFlow().setAccessToken(userInfo.token);
        }
        if (docStarted) {
          return;
        }
  
        isLoggedIn = userInfo.isLoggedIn();
        let rtoken = getRefreshTokenFromStorage()
        if (!isLoggedIn && rtoken) {
            return;
        }

        userInfoFetched = true;
        startDoc();
    });
  
    userInfo.get();
}

const startDoc = () => {
    if (!uiInitDone || docStarted || !userInfoFetched) {
        return;
    }
    docStarted = true;
    
    if (!uiConfig.isUsingLekhBackend()) {
        integ.loadDoc();
        return;
    }

    let lekhCore = window.Module.avabodh_lekh_app_LekhCore_instance();

    let url = new URL(window.location);
    let docid = url.searchParams.get('docid');
    let offline = url.searchParams.get('offline');
    //var np = url.searchParams.get('_new');
    //var ref = url.searchParams.get('ref');
    if (offline != 'true') {
        lekhCore.coreIO().openRemoteDocById(docid);
    }
}

initializer.init = (dialogHandler, showShapeHelp, showAirGesture) => {
    reactAppReady = true;
    viewPort.showDialog = dialogHandler;
    viewPort.showShapeHelp = showShapeHelp;
    viewPort.showAirGesture = showAirGesture;
    messaging.init();
    appendScript("a1/lekhapp.js");
    initializer.initInternal();
}

initializer.initInternal = () => {
    if (initialInitDone || !reactAppReady || !runtimeInitialized) {
        return;
    }
    initialInitDone = true;
    fetchUserDetail();
    platform.init();
    viewPort.init();
    initShapes().then(function() {
        initEventHandlers();
        initUI();
    });
}

export default initializer;
