import React, { useState, useEffect, useRef } from 'react';
import userInfo from '../../utils/UserInfo';
import Viewer from '../../common/Viewer'
import uiConfig from "../../common/UIConfig";
import canvas from '../../common/Canvas';
import { Button } from '@material-ui/core';
import { GrNext, GrPause, GrPlay, GrRefresh } from "react-icons/gr";

import qs from 'qs';

import { getADocsLogSession, getDocsLogUpdate } from '../../utils/ApiRequests';

const SessionViewer = props => {
    const [session, setSession] = useState(null);
    const [token, setToken] = useState(null);
    const [updates, setUpdates] = useState(null);
    const [loadingMsg, setLoadingMsg] = useState('Loading..');
    const [loading, setLoading] = useState(true);
    
    const playing = useRef(false);

    const onUserInfoChange = () => {
        setToken(userInfo.token);
        getSession();
    }

    const getSession = () => {
        if (session != null) {
            return;
        }

        var search = window.location.hash.substring(window.location.hash.indexOf('?'))
        var params = qs.parse(search, { ignoreQueryPrefix: true })
        var sessionid = params.sessionid;
        setSession(sessionid);
        getADocsLogSession(sessionid).then(resp => {
            console.log('Got ' + resp.data.updates.length + ' versions')
            let updatesArr = resp.data.updates;
            updatesArr.sort((a, b) => {
                return a.version < b.version;
            });
            getUpdates(updatesArr, 0);
        });
    }

    const getUpdates = (updateArr, idx) => {
        const batchSize = 75;
        if (idx < updateArr.length) {
            let from = idx;
            let to = Math.min(idx + batchSize, updateArr.length - 1);

            setLoadingMsg('Loading ' + (from + 1) + '-' + (to + 1) + ' out of ' + updateArr.length + ' updates')

            let verStr = updateArr[from].version;
            if (from != to) {
                verStr = updateArr[from].version + "-" + updateArr[to].version;
            }
            getDocsLogUpdate(updateArr[idx].docid, verStr).then(resp => {
                let respIdx = 0;
                while (from <= to) {
                    updateArr[from].content = resp.data.data[respIdx];
                    from++;
                    respIdx++;
                }
                getUpdates(updateArr, to + 1)
            });
        } else {
            setUpdates(updateArr);
            onUpdatesLoaded(updateArr);
        }
    }

    const onUpdatesLoaded = (updates) => {
        uiConfig.initForOfflineMode();
        if (updates.length > 0 && !updates[0].is_delta) {
            setLoading(false);
        } else {
            setLoadingMsg('Updates are not correct');
        }
    }

    const onWsStateChange = (state) => {
        if (playing.current) {
            return;
        }
        playing.current = true;
        var content = JSON.stringify(JSON.parse(updates[0].content).doc);
        window.Module.startDoc(updates[0].docid)
        window.Module.loadDoc(content);
        canvas.draw();
    }

    useEffect(() => {
        userInfo.event.addListener(onUserInfoChange);
        userInfo.get();
        return () => {
          userInfo.event.removeListener(onUserInfoChange);
        };
    }, []);

    if (!loading) {
        window.wsmanager.permission = 'read';
        console.log(updates)
    }

    return <div>
        {!loading && <Viewer wsStateHandler={onWsStateChange}/>}
        <div style={{width: '100%', position: 'absolute', top: 0, left: 0, marginLeft: 10, marginTop: 10}}>
            {loading &&<div>{loadingMsg}</div>}
            {!loading && <TopBar updates={updates} />}
        </div>
    </div>
}

const TopBar = ({updates}) => {
    const [counter, setCounter] = useState(0);
    
    const counterRef = useRef(0);
    const autoPlay = useRef(false);
    const curVersionIdx = useRef(0);
    const playInterval = useRef(500);

    const redraw = () => {
        counterRef.current += 1;
        setCounter(counterRef.current);
    }

    const restart = () => {
        var content = JSON.stringify(JSON.parse(updates[0].content).doc);
        window.Module.startDoc(updates[0].docid)
        window.Module.loadDoc(content);
        canvas.draw();

        playInterval.current = window.playInterval || playInterval.current;
        curVersionIdx.current = 0;
        autoPlay.current = false;

        redraw();
    }

    const toggleAutoPlay = () => {
        autoPlay.current = !autoPlay.current;
        onTimer();
    }

    const onTimer = () => {
        if (curVersionIdx.current < updates.length - 1 && autoPlay.current) {
            applyNext();
            window.setTimeout(onTimer, playInterval.current);
        } else {
            redraw();
        }
    }

    const applyNext = () => {
        curVersionIdx.current += 1;
        redraw();
        if (curVersionIdx.current < updates.length) {
            var content = updates[curVersionIdx.current].content;
            if (typeof content == 'string') {
                window.Module.updateDoc(content);
                canvas.draw();
            } else {
                window.alert(content + ' is not string');
            }
        }
    }

    var msg = '[ ' + (curVersionIdx.current + 1) + ' of ' + updates.length + ' updates ] ';
    var hasNext = curVersionIdx.current < updates.length - 1;
    return <div style={{display: 'flex'}}>
        <span>{msg}</span>
        <Button onClick={restart}><GrRefresh/></Button>
        {hasNext && autoPlay.current && <Button onClick={toggleAutoPlay}><GrPause/></Button>}
        {hasNext && !autoPlay.current && <Button onClick={toggleAutoPlay}><GrPlay/></Button>}
        {hasNext && !autoPlay.current && <Button onClick={applyNext}><GrNext/></Button>}
    </div>;
}

export default SessionViewer;

