import React from "react";

const InputField = ({
  label,
  type,
  id,
  name,
  value,
  handleChange,
  placeholder,
  pattern,
  required,
  error,
  errorText,
  readOnly
}) => {
  return (
    <div className="flex  flex-col">
      {label ? (
        <label
          htmlFor={id}
          className="text-prmDGrey mb-2 font-Poppins lg:text-lg"
        >
          {label}
        </label>
      ) : (
        <label
          htmlFor="email"
          className="text-prmDGrey mb-2 font-Poppins lg:text-lg invisible"
        >
          *****
        </label>
      )}
      {error && (
        <p className="font-italic text-red-400 my-0 mb-2 text-sm">
          {errorText}
        </p>
      )}
      <input
        type={type}
        id={id}
        placeholder={placeholder}
        name={name}
        value={value}
        onChange={handleChange}
        readOnly={readOnly}
        pattern={pattern}
        required={required}
        className={`${
          readOnly ? "cursor-not-allowed bg-gray-100" : ""
        }   "lg:text-lg outline-none text-prmDGrey border border-1 border-prmLGrey px-3 py-1.5 mb-5 font-Poppins "`}
      />
    </div>
  );
};

export default InputField;
