import './App.css';

import Content from './components/ViewPort/Content';
import React from "react";
import initializer from './common/Initializer'
import GesturePanel from './gestures/GesturePanel';

class AppComponent extends React.Component {
  constructor(props) {
    super(props);
    this.state = {
      dialog: null,
      shapeHelp: null,
      airGestureShowing: false
    };
    this.showDialog = this.showDialog.bind(this);
    this.showShapeHelp = this.showShapeHelp.bind(this);
    this.showAirGesture = this.showAirGesture.bind(this);
  }

  showShapeHelp(help) {
    this.setState({shapeHelp:help});
  }

  showDialog(dialog) {
    this.setState({dialog:dialog});
  }

  showAirGesture(flag) {
    this.setState({airGestureShowing:flag});
  }

  render() {
    return (
      <div className="container">
        <Content cameraActive={this.state.airGestureShowing} />
        {this.state.dialog}
        {this.state.shapeHelp}
        {this.state.airGestureShowing && <GesturePanel showAirGesture={this.showAirGesture}></GesturePanel>}
      </div>
    );
  }

  componentDidMount() {
    initializer.init(this.showDialog, this.showShapeHelp, this.showAirGesture);
  }
}

export default AppComponent;
