import {dist} from './Geometry';
import { mirrorPoint } from './Frame';

export class Dragger {
    constructor() {
        this.reset();
        this.MOVE_DIST_REF = 10.0;
        this.MOVE_DIST_SMALL_REF = 5.0;
        this.MOVE_DIST_SMALL = 8.0;
    }

    reset() {
        this.last = null;
        this.lastRef = null;
        this.moving = false;
        this.points = [];
    }

    start() {
        this.reset();
    }

    addPoint(pt) {
        this.points.push(mirrorPoint(pt));
    }

    checkMove(pt, ptref) {
        if (this.lastRef === null) {
            this.lastRef = ptref;
            this.last = pt;
            return;
        }

        var d = dist(ptref, this.lastRef);
        if (d > this.MOVE_DIST_REF) {
            this.moving = true;
            this.addPoint(this.last);
            this.last = pt;
            this.lastRef = ptref;
        }
    }

    validMove(pt, ptref) {
        var dx1 = (ptref.x - this.lastRef.x);
        var dx2 = (pt.x - this.last.x);
        var dy1 = (ptref.y - this.lastRef.y);
        var dy2 = (pt.y - this.last.y);
        var valid_x = (((dx1 <= 0) && (dx2 <= 0)) || ((dx1 >= 0) && (dx2 >= 0)));
        var valid_y = (((dy1 <= 0) && (dy2 <= 0)) || ((dy1 >= 0) && (dy2 >= 0)));
        return (valid_x && valid_y);
    }

    handleMove(pt, ptref) {
        var d = dist(ptref, this.lastRef);
        if ((d > this.MOVE_DIST_SMALL_REF)) {
            d = dist(pt, this.last);
            if (d > this.MOVE_DIST_SMALL) {
                this.addPoint(pt);
                this.last = pt;
                this.lastRef = ptref;
            }
        }
    }

    track(pt, ptref) {
        if (this.moving) {
            this.handleMove(pt, ptref);
        } else {
            this.checkMove(pt, ptref);
        }
    }
}

