import globalState from './GlobalState'
import shapes from '../shapelib/Shapes'

var canvas = {
    showGrid: true,
    gridType: 2,
 };

const CANVAS_FIXED = 1;
const UNIT_PT = 0;
const UNIT_PX = 1;
const UNIT_IN = 2;
const UNIT_CM = 3;

const minWidth = 200;
const minHeight = 200;

var canvasElement = null;

var isScrolling = false;
var savedScroll = {x:0, y: 0};
var scrollingFrom = {x: 0, y: 0};

var infiniteScroll = true;
var uiScrollX = 0;
var uiScrollY = 0;
var effectiveScrollX = 0;
var effectiveScrollY = 0;

var effectiveWidth = 0;
var effectiveHeight = 0;

var canvasWidth = 0;
var canvasHeight = 0;

var viewPortWidth = 0;
var viewPortHeight = 0;

var pageLayoutDoneOnce = false;

const fixCanvas = function(cnv, width, height) {
    var dpr = window.devicePixelRatio || 1;
    cnv.width = width * dpr;
    cnv.height = height * dpr;
    cnv.style.width = width + "px";
    cnv.style.height = height + "px";
    var ctx = cnv.getContext("2d");
    ctx.scale(dpr, dpr);
}

const computeSize = (vport) => {
    var rect = vport.getBoundingClientRect();
    
    viewPortWidth = rect.width;
    viewPortHeight = rect.height;

    // Initialize assuming it is infinite scroll
    canvasWidth = viewPortWidth;
    canvasHeight = viewPortHeight;

    effectiveHeight = canvasHeight;
    effectiveWidth = canvasWidth;

    effectiveScrollX = 0;
    effectiveScrollY = 0;
    
    infiniteScroll = true;

    // Check if there is fixed canvas size
    if (window.wsmanager && window.wsmanager.state == 'ready') {
        var pageEditor = window.Module.startPageEditing();
        var sz = pageEditor.getSize();
        var tp = pageEditor.getCanvasType();
        
        // In CSS: a pt is 1/72 of an inch, and a px is 1/96 of an inch.
        // In Lekh, a pt in 1/144 of an inch and we map 1 Lekh pt to 1 css px 
        var pointsPerInch = 144;
        var pixelsPerPoint = 1;
        var pixlesPerInch = pointsPerInch * pixelsPerPoint;
        if (tp.value == CANVAS_FIXED) {
            infiniteScroll = false;
            if (sz.unit.value == UNIT_PT) {
                //canvasWidth = (sz.width * 96) / 72;
                //canvasHeight = (sz.height * 96) / 72;
                canvasWidth = sz.width;
                canvasHeight = sz.height;
            } else if (sz.unit.value == UNIT_PX) {
                canvasWidth = sz.width;
                canvasHeight = sz.height;
            } else if (sz.unit.value == UNIT_IN) {
                canvasWidth = sz.width * pixlesPerInch;
                canvasHeight = sz.height * pixlesPerInch;
            } else if (sz.unit.value == UNIT_CM) {
                canvasWidth = (sz.width / 2.54) * pixlesPerInch;
                canvasHeight = (sz.height / 2.54) * pixlesPerInch;
            }
            var zm = window.Module.getZoom();
            canvasWidth *= zm;
            canvasHeight *= zm;

            if (!pageLayoutDoneOnce) {
                pageLayoutDoneOnce = true;
                if (canvasWidth < viewPortWidth) {
                    uiScrollX = (viewPortWidth - canvasWidth) / 2;
                }
                if (canvasHeight < viewPortHeight) {
                    uiScrollY = (viewPortHeight - canvasHeight) / 2;
                }
            }

            var effectiveMinWidth = Math.min(minWidth, viewPortWidth)
            var effectiveMinHeight = Math.min(minHeight, viewPortHeight)
            // check canvas moving out of view port to left and top
            if (canvasWidth <= effectiveMinWidth) {
                uiScrollX = uiScrollX < 0 ? 0 : uiScrollX;
            } else if (canvasWidth + uiScrollX < effectiveMinWidth) {
                uiScrollX =  effectiveMinWidth - canvasWidth;
            }

            if (canvasHeight < effectiveMinHeight) {
                uiScrollY = uiScrollY < 0 ? 0 : uiScrollY;
            } else if (canvasHeight + uiScrollY < effectiveMinHeight) {
                uiScrollY = effectiveMinHeight - canvasHeight;
            }

            // check canvas moving out of view port to right and bottom
            if (uiScrollX > viewPortWidth - effectiveMinWidth) {
                uiScrollX = viewPortWidth - effectiveMinWidth;
            }

            if (uiScrollY > viewPortHeight - effectiveMinHeight) {
                uiScrollY = viewPortHeight - effectiveMinHeight;
            }

            effectiveScrollX = uiScrollX;
            effectiveScrollY = uiScrollY;

            effectiveWidth = canvasWidth;
            effectiveHeight = canvasHeight;

            // check clipping in left and top
            if (effectiveScrollX < 0) {
                effectiveWidth += effectiveScrollX;
                effectiveScrollX = 0;
            }
            if (effectiveScrollY < 0) {
                effectiveHeight += effectiveScrollY;
                effectiveScrollY = 0;
            }

            // check clipping to right and bottom
            if (uiScrollX + canvasWidth > viewPortWidth) {
                effectiveWidth -= canvasWidth + uiScrollX - viewPortWidth;
            }

            if (uiScrollY + canvasHeight > viewPortHeight) {
                effectiveHeight -= canvasHeight + uiScrollY - viewPortHeight;
            }
        }
    }
}

canvas.setSize = () => {
    canvasElement = document.getElementById("drawCanvas");
    var vport = document.getElementById("viewport");
    var gridPanel = document.getElementById("gridCanvas");
    var transparentPanel = document.getElementById("transparentPanel");

    if (!canvasElement || !vport || !gridPanel || !transparentPanel) {
        return;
    }

    computeSize(vport);

    fixCanvas(gridPanel, effectiveWidth, effectiveHeight);
    fixCanvas(canvasElement, effectiveWidth, effectiveHeight);
    fixCanvas(transparentPanel, viewPortWidth, viewPortHeight);
    
    gridPanel.style.position = 'absolute';
    canvasElement.style.position = 'absolute';

    if (infiniteScroll) {
        gridPanel.style.left = '0px';
        gridPanel.style.top = '0px';

        canvasElement.style.left = '0px';
        canvasElement.style.top = '0px';
    } else {    
        gridPanel.style.left = effectiveScrollX + 'px';
        gridPanel.style.top = effectiveScrollY + 'px';

        canvasElement.style.left = effectiveScrollX + 'px';
        canvasElement.style.top = effectiveScrollY + 'px';

        if (uiScrollX < 0 || uiScrollY < 0) {
            var zm = window.Module.getZoom();
            var sx = uiScrollX < 0? uiScrollX : 0;
            var sy = uiScrollY < 0? uiScrollY : 0;
            window.Module.setScroll({x: sx / zm, y: sy / zm});
        } else {
            window.Module.setScroll({x: 0, y: 0});
        }
    }
}

const moveDelta = (dx, dy) => {
    uiScrollX += dx;
    uiScrollY += dy;
    canvas.setSize();
}

const moveDeltaForInfiniteScroll = (dx, dy) => {
    var zm = window.Module.getZoom();
    var scroll = window.Module.getScroll();
    dx = dx / zm;
    dy = dy / zm;
    window.Module.setScroll({x: scroll.x + dx, y: scroll.y + dy});
}

const move = (x, y, type) => {
    if (type == 0) {
        isScrolling = true;
        savedScroll = {x: uiScrollX, y: uiScrollY}
        scrollingFrom = {x: x, y: y}
    }

    if (type == 1 && isScrolling) {
        var dx = x - scrollingFrom.x;
        var dy = y - scrollingFrom.y;

        uiScrollX = savedScroll.x + dx;
        uiScrollY = savedScroll.y + dy;

        canvas.setSize();
    }

    if (type == 2 && isScrolling) {
        isScrolling = false;
    }
}

const moveForInfiniteScroll = (x, y, type) => {
    if (type == 0) {
        isScrolling = true;
        savedScroll = window.Module.getScroll();
        scrollingFrom = {x: x, y: y}
    }

    if (type == 1 && isScrolling) {
        var dx = x - scrollingFrom.x;
        var dy = y - scrollingFrom.y;

        var zm = window.Module.getZoom();
        var sx = savedScroll.x + dx / zm;
        var sy = savedScroll.y + dy / zm;
        window.Module.setScroll({ x: sx, y: sy });
    }

    if (type == 2 && isScrolling) {
        isScrolling = false;
    }
}

canvas.toggleGrid = function() {
    canvas.showGrid = !canvas.showGrid;
    canvas.drawGrid();
}

canvas.onResize = function() {
    canvas.layout();
}

canvas.onConnectionStateChanged = () => {
    if (!window.wsmanager || 
        (window.wsmanager.state != 'init' &&
        window.wsmanager.state != 'ready')) {
        return;
    }
    canvas.layout();
}

canvas.layout = function() {
    canvas.setSize();
    canvas.drawGrid();
    if(window.wsmanager && window.wsmanager.state == 'ready') {
        canvas.draw();
    }
}

canvas.moveDelta = function(dx, dy) {
    if (infiniteScroll) {
        moveDeltaForInfiniteScroll(dx, dy);
    } else {
        moveDelta(dx, dy);
    }
    canvas.drawGrid();
    canvas.draw();
}

canvas.move = function(x, y, type) {
    if (infiniteScroll) {
        moveForInfiniteScroll(x, y, type);
    } else {
        move(x, y, type);
    }
    canvas.drawGrid();
    canvas.draw();
}

canvas.getMidPoint = () => {
    if (infiniteScroll) {
        var tp = document.getElementById('transparentPanel');
        var rect = tp.getBoundingClientRect();
        var x = (rect.left + rect.right) / 2;
        var y = (rect.top + rect.bottom) / 2;
        return {x: x, y: y};
    }
    return {x: effectiveScrollX + effectiveWidth / 2, y: effectiveScrollY + effectiveHeight / 2}
}

canvas.isVisible = (x, y, pad) => {
    var tp = document.getElementById('transparentPanel');
    var rect = tp.getBoundingClientRect();
    if (infiniteScroll) {
        return x-pad >= rect.left && x+pad <= rect.right && y-pad >= rect.top && y+pad <= rect.bottom;
    }
    return x-pad >= effectiveScrollX && x+pad <= effectiveScrollX + effectiveWidth &&
        y-pad >= effectiveScrollY && y+pad <= effectiveScrollY + effectiveHeight;
}

canvas.mapx = function(x) {
    var zm = window.Module.getZoom();
    var scroll = window.Module.getScroll();
    return (x  + scroll.x) * zm;
}

canvas.mapy = function(y) {
    var zm = window.Module.getZoom();
    var scroll = window.Module.getScroll();
    return (y  + scroll.y) * zm;
}

canvas.maprx = function(x) {
    var zm = window.Module.getZoom();
    var scroll = window.Module.getScroll();
    return x / zm - scroll.x;
}

canvas.mapry = function(y) {
    var zm = window.Module.getZoom();
    var scroll = window.Module.getScroll();
    return y / zm - scroll.y;
}

canvas.mapPtUIScroll = (pt) => {
    if (infiniteScroll) {
        return pt;
    }
    return {x: pt.x - effectiveScrollX, y:pt.y - effectiveScrollY};
}

canvas.toViewPort = (pt) => {
    if (infiniteScroll) {
        return pt;
    }
    return {x: pt.x + effectiveScrollX, y:pt.y + effectiveScrollY};
}

canvas.getZoom = function() {
    return window.Module.getZoom();
}

canvas.zoomAt = function(x, y, factor) {
    // (x + d1) * z1 = x'
    // (x + d2) * z2 = x'
    // from these two equations, calculate d2
    var z1 = window.Module.getZoom();
    z1 = Math.round(z1 * 100) / 100;
    var z2 = Math.round(factor * 100) / 100;
    
    // If new zoom and old zoom are same in scale of [1 100]
    // then do not do anything
    if (Math.round(z1 * 100) == Math.round(z2 * 100)) {
        return;
    }
    
    if (infiniteScroll) {
        var scroll = window.Module.getScroll();

        var dx = (x / z2) - (x / z1) + scroll.x;
        var dy = (y / z2) - (y / z1) + scroll.y;

        window.Module.setScroll({x: dx, y: dy});
        window.Module.setZoom(Math.round(z2 * 100));
    } else {
        var uiPt = canvas.mapPtUIScroll({x: x, y: y});
        uiPt.x = uiPt.x < 0 ? 0: uiPt.x;
        uiPt.y = uiPt.y < 0 ? 0: uiPt.y;
        uiPt.x = uiPt.x > effectiveWidth? effectiveWidth: uiPt.x;
        uiPt.y = uiPt.y > effectiveHeight? effectiveHeight: uiPt.y;

        var canvasPtX = canvas.maprx(uiPt.x);
        var canvasPtY = canvas.mapry(uiPt.y);

        window.Module.setZoom(Math.round(z2 * 100));
        canvas.setSize();
        var ptx = canvas.mapx(canvasPtX)
        var pty = canvas.mapy(canvasPtY);
        
        uiScrollX -= ptx - uiPt.x;
        uiScrollY -= pty - uiPt.y;
        canvas.setSize();
    }

    globalState.onZoom();
    canvas.drawGrid();
    canvas.draw();
 }

canvas.zoom = function(factor) {
    var vport = document.getElementById("viewport");
    var rect = vport.getBoundingClientRect();
    canvas.zoomAt(rect.width / 2, rect.height / 2, factor);
}

canvas.exportToImage = function(format) {
    var padding = 10;
    var rect = window.Module.getBoundsOfAll();
    var sx = padding - rect.x;
    var sy = padding - rect.y;
    var width = rect.width + 2 * padding;
    var height = rect.height + 2 * padding;

    var cel = document.createElement("canvas");
    fixCanvas(cel, width, height);
    var ctx = cel.getContext("2d");

    if (format == "jpg" || format == "jpeg") {
        ctx.fillStyle = '#fff';
        ctx.fillRect(0, 0, width, height);
    }
    window.Module.drawCanvasForExport(ctx, sx, sy);
    return {data: cel.toDataURL("image/" + format), width: width, height: height }
}

canvas.drawGrid = function() {
    var panel = document.getElementById("gridCanvas");
    var wd = panel.width;
    var ht = panel.height;
    var ctx = panel.getContext('2d');
    ctx.clearRect(0, 0, panel.width, panel.height);
    if (!canvas.showGrid) {
        return;   
    }
    var zm = window.Module.getZoom();
    var scroll = window.Module.getScroll();
    var rad = 1 * zm;
    var step = 48 * zm;

    var startx = (scroll.x * zm) % step;
    var starty = (scroll.y * zm) % step;

    if (canvas.gridType == 1) {
        if (step < 16) {
            return;
        }
        ctx.beginPath();
        ctx.fillStyle = "#c0c0c0";
        ctx.strokeStyle = "#c0c0c0";
        for (var i = starty; i < ht; i+= step) {
            for (var j = startx; j < wd; j+= step) {
                ctx.moveTo(j + rad, i);
                ctx.arc(j, i, rad, 0, 2 * Math.PI, false);
            }
        }
        ctx.fill();
        ctx.stroke();
    } else {
        if (step < 8) {
            return;
        }
        ctx.lineWidth = 0.1;
        ctx.strokeStyle = "#606060";
        ctx.beginPath();
        for (var i = starty; i < ht; i+= step) {
          ctx.moveTo(0, i);
          ctx.lineTo(wd, i);
        }
        for (i = startx; i < wd; i+= step) {
          ctx.moveTo(i, 0);
          ctx.lineTo(i, ht);
        }
        ctx.stroke();
    }
}
  
canvas.draw = function() {
    if (canvasElement == null) {
        canvasElement = document.getElementById("drawCanvas");
    }
    
    var ctx = canvasElement.getContext("2d");
    ctx.clearRect(0, 0, canvasElement.width, canvasElement.height);
    window.Module.drawCanvas(ctx);
}

canvas.drawCell = function(categ, num, sz, ctx) {
    var row = num / shapes.libraryColCount;
    var col = num % shapes.libraryColCount;
    var padding = 8;
    window.Module.drawCell(categ, row, col, sz - padding, sz / 2, sz / 2, ctx);
}

canvas.drawArrow = function(type, size, ctx) {
}

window.drawCanvas = canvas.draw;
window.layoutCanvas = canvas.layout;

export default canvas;