import { Switch, Redirect, Route } from "react-router-dom";
import AuthRoutes, { AuthRouteList } from "./AuthRoutes";
import { ToastContainer } from "react-toastify";
import "react-toastify/dist/ReactToastify.min.css";
import DashRoutes, { DashRouteList } from "./DashRoutes";
import DocsLogRoutes from "../components/DocsLog/DocsLogRoutes";

const RouteList = [
  { path: DashRouteList.map(r => r.path), component: DashRoutes, exact: true },
  { path: AuthRouteList.map(r => r.path), component: AuthRoutes, exact: true },
  ...DocsLogRoutes
];

const Routes = () => {
  return (
    <>
      <ToastContainer
        position="top-right"
        autoClose={5000}
        hideProgressBar={false}
        newestOnTop={false}
        closeOnClick
        pauseOnHover
      ></ToastContainer>
      <Switch>
        {RouteList.map(r => (
          <Route
            key={r.path}
            exact={r.exact}
            component={r.component}
            path={r.path}
          />
        ))}
        <Route path="/" exact={true}>
          <Redirect to="/docs/mydocs" />
        </Route>
      </Switch>
    </>
  );
};

export default Routes;
