import appEvent from "../common/AppEvent";
import {v4} from 'uuid'
import EventListener from "../utils/EventListener";
import menuState from "../common/MenuState";
import {FileNameInputDialog, ConfirmDialog, MessageDialog} from "./AtlasianIntegUI";
import { FaMousePointer, FaHandRock } from 'react-icons/fa';
import canvas from "../common/Canvas";
import LekhImporter from "../components/LekhImporter/LekhImporter";
import LekhItemPicker from "../components/Picker/LekhItemPicker";
import LekhErrJson from './load_err.json'
import axios from "axios";
import { constant } from "../utils/ApiConstants";
import util from "../common/Util";

const integVersion = '1.0';
const enableEditorTimeout = true;
const editorInitTimeout = 1 * 60 * 1000; // one minute
const enableDebugLog = false;

var macroType = 'confluence.ac.lekh.app:lekh-diagram';
var macroKey = 'lekh';
if (constant.baseUrl.startsWith('http://localhost') || constant.baseUrl.endsWith('ngrok.io')) {
    macroType = 'dev-confluence.ac.lekh.app:lekh-diagram';
    macroKey = 'newdg';
}

const atlassian = {}

var integMode = null;

var customContentInfo = null;
var previousComputedHeight = 0;
var docInfo = {name: '', docid: ''}

// Initial values updated during initialization
var atlassianUserId = '';
var pageLocation = null;
var pageId = null;
var macroData = null;
var showDialog = null;
var setAppState = null;
var contextToken = null;

// Attachment related variables
var lekhAttachment = null;
var draftAttachment;
var lekhAttachmentReadErr = null;
var draftAttachmentReadErr = null;

var editorInitTimer = null;

// variables to track updates in diagrams by editing by user
var numUpdatesInEditor = 0;
var numPageAttrUpdate = 0;
var draftSavedAtCounter = 0;
var saveDraftTimer = null;
var draftSaveFailureWarnDone = false;

const has_touch = util.hasTouch();

const eventListener = new EventListener();
const waitList = [];

const eventTypes = {
    create: 'create',
    promptCreate: 'promptCreate',
    view: 'view',
    fullView: 'fullView',
    edit: 'edit',
    save: 'save',
    saveError: 'saveError',
    readError: 'readErroror',
    timeout: 'timeout',
    deleteDraftError: 'deleteDraftError',
    macroFindError: 'macroFindError',
    diagramImport: 'diagramImport',
    cloudImport: 'cloudImport',
    cloudImportError: 'cloudImportError',
}

const logDebug = (fn) => {
    if (enableDebugLog) {
        fn();
    }
}

const wakeupWaiting = () => {
    let i = 0;
    while (i < waitList.length) {
        if (waitList[i].cond()) {
            waitList[i].resolve();
            waitList.splice(i, 1);
        } else {
            i++;
        }
    }
}
eventListener.addListener(wakeupWaiting);

const waitFor = async (cond) => {
    if (cond()) {
        return;
    }
    return new Promise((resolve, reject) => {
        waitList.push({cond: cond, resolve: resolve});
    });
}

const sendEventToBackend = async (type, info, err) => {
    const makeStr = (obj) => {
        if (obj == null || typeof(obj) == 'undefined') {
            return '';
        } else if (typeof(obj) == 'object') {
            return JSON.stringify(obj);
        }
        return String(obj);
    }
    info = info || { }
    if (macroData && macroData.attachmentName) {
        info.attachmentName = macroData.attachmentName;
        info.diagramName = macroData.diagramName;
    }
    info.pageId = pageId;
    info.location = pageLocation.href;
    info.userId = atlassianUserId;
    info.has_touch = has_touch;
    info.product = 'confluence';
    if (err) {
        info.err = makeStr(err);
    }
    
    let data = {
        type: type,
        info: info
    }
    const config = { baseURL: constant.baseUrl };
    config.headers = { "authorization": 'JWT ' + contextToken };
    return await axios.post('/integ/ac/confluence/api/stats', data, config);
}

const showNotification = (data) => {
    window.AP.flag.create(data);
    //alert(data.title + ': ' + data.body);
}

const setupEditorInitTimeout = () => {
    if (integMode.editor != null && enableEditorTimeout) {
        editorInitTimer = window.setTimeout(()=> {
            sendEventToBackend(eventTypes.timeout);
            showNotification({
                title: "Error",
                body: "Unable to load the Lekh Diagram. Operation timed out.",
                type: "error"
            });
            atlassian.closeEditor();
        }, editorInitTimeout);
    }
}

const getMacroData = (cb) => {
    if (customContentInfo != null) {
        pageId = customContentInfo.pageId;
        cb(customContentInfo);
    } else {
        window.AP.confluence.getMacroData(data => {
            if (!data || Object.keys(data).length == 0) {
                if (pageLocation && pageLocation.target == 'addonmodule' &&
                 pageLocation.context && pageLocation.context.context &&
                 pageLocation.context.context['content.type'] == 'custom' &&
                 pageLocation.context.context['content.id']) {
                    var customContentId = pageLocation.context.context['content.id'];
                    getContentInfo(customContentId, true, (err, resp) => {
                        if (err == null) {
                            customContentInfo = JSON.parse(decodeURIComponent(resp.body.storage.value));
                            pageId = customContentInfo.pageId;
                            cb(customContentInfo);
                        } else {
                            cb(data);
                        }
                    });
                 } else {
                    cb(data);
                 }
            } else {
                cb(data);
            }
        });
    }
}

const getPageContext = () => {
    setupEditorInitTimeout();
    // We are calling atlassian API serially. Ideally we should call them in parallel
    // but it seems the atlassian does not support API call in parallel
    window.AP.navigator.getLocation(loc => {
        logDebug(() => console.log('getLocation responded: ', performance.now()));
        pageLocation = loc;
        pageId = loc.context.contentId;
        window.AP.user.getCurrentUser(user => {
            logDebug(() => console.log('getCurrentUser responded: ', performance.now()));
            atlassianUserId = user.atlassianAccountId;
            window.AP.context.getToken(token => {
                logDebug(() => console.log('getToken responded: ', performance.now()));
                contextToken = token;
                getMacroData(data => {
                    logDebug(() => console.log('getMacroData responded: ', performance.now()));
                    macroData = data;
                    eventListener.trigger();
                    readLekhAttachment(() => {
                        logDebug(() => console.log('LekhAttachment read done: ', performance.now()));
                        readDraftAttachment(() => {
                            logDebug(() => console.log('DraftAttachment read done: ', performance.now()));
                            eventListener.trigger();
                            handleAtlassianInit();
                        });
                    });
                });
            });
        });
    });
}

const handleAtlassianInit = async () => {
    if (editorInitTimer != null) {
        window.clearTimeout(editorInitTimer);
        editorInitTimer = null;
    }

    // if we are unable to read the attachment and we are in editor mode
    // then we should simple close the editor
    if (lekhAttachmentReadErr && lekhAttachmentReadErr.status != 404 && integMode.editor != null) {
        showNotification({
            title: 'Error',
            body: 'Unable to load the editor',
            type: 'error'
        });
        atlassian.closeEditor();
        return;
    }

    eventListener.trigger();

    // when the macro is created then we do not have any attachment name 
    if (!macroData.attachmentName && integMode.editor != null) {
        sendEventToBackend(eventTypes.promptCreate);
        await waitFor(() => showDialog != null);
        
        let onCancel = () => {
            atlassian.closeEditor();
        }

        let onAccept = (value) => {
            var attachmentName = v4();
            macroData = {diagramName: value, attachmentName: attachmentName, counter:1, integVersion: integVersion };
            docInfo.name = value;
            docInfo.docid = attachmentName;
            numUpdatesInEditor++;
            showDialog(null);
            sendEventToBackend(eventTypes.create);
        }

        showDialog(<FileNameInputDialog title={"Enter Diagram Name"} cancelText={"Cancel"} okText={"OK"} onCancel={onCancel} onAccept={onAccept} initialValue={''}/>)
    }
    
    if (integMode.editor != null) {
        sendEventToBackend(eventTypes.edit);
    } else if (integMode.viewer.full) {
        sendEventToBackend(eventTypes.fullView);
    } else {
        sendEventToBackend(eventTypes.view);
    }
    eventListener.trigger();
}

const setViewerSize = () => {
    var vport = document.getElementById("viewport");
    var clientRect = vport.getBoundingClientRect();
    var rect = window.Module.getBoundsOfAll();
    const padding = 12;
    var viewerHeight = rect.height + 2 * padding;
    var viewerWidth = rect.width + 2 * padding;
    var zoom = 1.0;
    var sx = padding - rect.x;
    var sy = padding - rect.y;
    if (viewerWidth > clientRect.width && viewerWidth > 0) {
        // We need to zoom so that the content can fit in the width
        zoom = clientRect.width / viewerWidth;
        viewerHeight *= zoom;
    }
    window.Module.setScroll({x: sx, y: sy});
    if (zoom != 1.0 && !integMode.viewer.full) {
        window.Module.setZoom(Math.floor(zoom * 100));
    }

    if (integMode.viewer != null && !integMode.viewer.full) {
        var newHeight = Math.ceil(viewerHeight);
        if (Math.abs(newHeight - previousComputedHeight) > 2) {
            previousComputedHeight = newHeight;
            window.AP.resize('100%', newHeight);
        }
    }
}

const readLekhAttachment = (cb) => {
    if (lekhAttachment != null) {
        cb();
        return;
    }
    
    if (!macroData.attachmentName) {
        lekhAttachment = "";
        cb();
        return;
    }

    var fileName = macroData.attachmentName + ".lekh";
    readAttachment(fileName, (content, err) => {
        if (err) {
            var errObj = {}
            if (err.status) { errObj.status = err.status}
            if (err.statusText) { errObj.statusText = err.statusText}
            sendEventToBackend(eventTypes.readError, {fileName: fileName}, errObj);
            lekhAttachment = '';
            lekhAttachmentReadErr = err;
        } else {
            lekhAttachment = content == null? '' : content;
        }
        cb();
    });
}

const draftName = () => {
    return macroData.attachmentName + "-" + atlassianUserId + "-draft.lekh";
}

const readDraftAttachment = (cb) => {
    if (integMode.editor == null || !macroData.attachmentName) {
        draftAttachment = "";
        cb();
        return;
    }
    if (draftAttachment != null) {
        cb();
        return;
    }

    var fileName = draftName();
    readAttachment(fileName, (content, err) => {
        if (err) {
            if (err.status && err.status != 404) {
                var errObj = {}
                if (err.status) { errObj.status = err.status}
                if (err.statusText) { errObj.statusText = err.statusText}
                sendEventToBackend(eventTypes.readError, {fileName: fileName}, errObj);
            }
            draftAttachment = '';
            draftAttachmentReadErr = err;
        } else {
            draftAttachment = content == null? '' : content;
        }
        cb();
    });
}

const readAttachment = (fileName, cb) => {
    window.AP.request({
        url: '/download/attachments/' + pageId + '/' + fileName,
        success: (resp) => {
            cb(resp, null);
        },
        error: (err) => {
            cb(null, err);
        }
    });
}

const getContentInfo = (id, fetchBody, cb) => {
    if (!id) {
        cb('err', '');
        return;
    }

    var url = '/rest/api/content/' + id;
    if (fetchBody) {
        url += '/?expand=body.storage'
    }

    window.AP.request({
        url: url,
		type: 'GET',
		contentType: 'application/json;charset=UTF-8',
		success: (resp) => {
            let obj = JSON.parse(resp);
			cb(null, obj);
		},
		error: (resp) => {
            cb('err', resp);
        }
	});
}

const saveCustomContent = (customContentId, cb) => {
    var info = {
	    "pageId": pageId,
        "attachmentName": macroData.attachmentName,
	    "diagramName": macroData.diagramName,
        "counter": macroData.counter
    }

    var customObj = {
        "type": "ac:" + macroType,
        "space": {
           "key": pageLocation.context.spaceKey
         },
         "container": {
               "type": 'page',
               "id": pageId
            },
         "title": macroData.diagramName,
         "body": {
           "storage": {
             "value": encodeURIComponent(JSON.stringify(info)),
             "representation": "storage"
           }
         },
         "status": "current"
    };
    getContentInfo(customContentId, false, (err, resp) => {
        if (err) {
            customContentId = null;
        } else {
            customObj.version = {
                "number": resp.version.number + 1
            }
            info.version = resp.version.number + 1;
            customObj.body.storage.value = encodeURIComponent(JSON.stringify(info));
        }
        window.AP.request({
            type: customContentId? 'PUT' : 'POST',
            data: JSON.stringify(customObj),
            url:  "/rest/api/content/" + (customContentId? customContentId : ""),
            contentType: "application/json",
            success: (resp) => {
                cb(null, resp);
            },
            error: (resp) => {
                cb('err', resp)
            }
        });
    });
}

const saveAttachment = (fileName, content, comment, cb) => {
    logDebug(() => console.log('will save attachment: ', performance.now()));
    var attFile = (content instanceof Blob)? content : new Blob([content], {type: 'application/lekh'});;
    attFile.name = fileName;
    if (!comment) {
        comment = 'Created by Lekh';
    }
    var reqData = {file: attFile, minorEdit: true, comment: comment};
    var draftFlag = pageLocation.target == 'contentcreate' ? '?status=draft' : '';

    window.AP.request({
        type: 'PUT',
        data: reqData,
        url:  "/rest/api/content/"+ pageId +"/child/attachment" + draftFlag,
        contentType: "multipart/form-data",
        success: () => {
            logDebug(() => console.log('attachment saved: ', performance.now()));
            cb(null);
        },
        error: (e) => {
            var errObj = { }
            if (e.status) { errObj.status = e.status}
            if (e.statusText) { errObj.statusText = e.statusText}
            sendEventToBackend(eventTypes.saveError, {fileName: fileName}, errObj);
            cb(e);
        }
    });
}

const getAttachmentInfo = (fileName, cb) => {
	window.AP.request({
        type: 'GET',
        url: '/rest/api/content/' + pageId + '/child/attachment?filename=' + encodeURIComponent(fileName),
        contentType: 'application/json;charset=UTF-8',
        success: function (resp) {
            var tmp = JSON.parse(resp);
            if (tmp.results && tmp.results.length == 1) {
                var info = tmp.results[0], attInfo;
                cb(null, info);
            } else {
                cb({status: 404});
            }
        },
        error: cb
    });
};

const deleteDraft = (cb) => {
    getAttachmentInfo(draftName(), (err, info) => {
        if (err == null && info && info.id) {
            window.AP.request({
                url : '/rest/api/content/' + info.id,
                type : 'DELETE',
                success : () => {cb(null)},
                error : e=> {cb(e)}
            });
        } else {
            cb(err);
        }
    });
}

const saveDraft = async () => {
    if (draftSavedAtCounter == numUpdatesInEditor) {
        return;
    }
    var curUpdatesCounter = numUpdatesInEditor;
    return new Promise((resolve, reject) => {
        var lekhContent = window.Module.getLekh();
        var fileName = draftName();
        saveAttachment(fileName, lekhContent, 'Lekh Draft', (e) => {
            if (!e) {
                draftSavedAtCounter = curUpdatesCounter;
                resolve();
            } else {
                console.log('Error in saving draft', e)
                if (!draftSaveFailureWarnDone) {
                    draftSaveFailureWarnDone = true;
                    let title = "Draft save error";
                    let msg = "Unable to save the draft. Please check you are connected to the internet and logged-in into Atlassian.";
                    showDialog(<MessageDialog title={title} msg={msg} okText={"OK"} onAccept={()=>showDialog(null)}/>)
                }
                reject(e);
            }
        });
    });
}

const onDiagramChange = (docid, data) => {
    var obj = JSON.parse(data);
    if (obj && obj.type == 'update' && obj.updates && obj.updates.type != 'trackpointer') {
        if (obj.updates.type == 'changepageattr' && numPageAttrUpdate == 0) {
            numPageAttrUpdate = 1;
        } else {
            numUpdatesInEditor++;
            if (saveDraftTimer == null) {
                // Accumulate updates of 10 seconds and then save
                saveDraftTimer = window.setTimeout(() => {
                    saveDraft().finally(()=> {
                        saveDraftTimer = null;
                    });
                }, 10 * 1000);
            }
        }
    }
}

const closeEditor = (saveDraftAndClose) => {
    if (saveDraftAndClose) {
        if (setAppState != null) {
            setAppState('closing')
        }
        saveDraft().finally(() => {
            window.AP.dialog.close();
        });
    } else {
        window.AP.dialog.close();
    }
}

const confirmDraftLoad = () => {
    let onAccept = () => {
        lekhAttachment = draftAttachment;
        numUpdatesInEditor++;
        draftAttachment = null;
        showDialog(null);
        eventListener.trigger();
    }
    let onCancel = () => {
        draftAttachment = null;
        showDialog(null);
        eventListener.trigger();
    }

    let title = "Load draft?";
    let msg = "Found unpublished changes from past editing";
    showDialog(<ConfirmDialog title={title} msg={msg} cancelText={"Cancel"} okText={"Load Draft"} onCancel={onCancel} onAccept={onAccept}/>)
}

const getDiagramsInPage = (cb) => {
    var diagrams = [];
    const processMacroNode = (node) => {
        let isLekh = false;
        for (let attrib of node.attributes) {
            if (attrib.name == "ac:name" && attrib.value == macroKey) {
                isLekh = true;
                break;
            }
        }
        if (!isLekh) {
            return;
        }

        let diagram = { }
        for (let param of node.childNodes) {
            if (param.tagName != "ac:parameter") {
                continue;
            }

            for (let attrib of param.attributes) {
                if (attrib.name == "ac:name") {
                    diagram[attrib.value] = param.innerHTML;
                }
            }
        }
        diagrams.push(diagram);
    }

    const processNode = (node) => {
        if (node.tagName == "ac:structured-macro") {
            processMacroNode(node);
        } else {
            for (let item of node.childNodes) {
                processNode(item);
            }
        }
    }
    window.AP.request({
        url: '/rest/api/content/' + pageId + '/?expand=body.storage,version&status=draft',
        contentType: 'application/json;charset=UTF-8',
        success: (resp) => {
            try {
                let page = JSON.parse(resp);
                let content = '<data xmlns:ac="https://www.atlassian.com/confluence/page">' + page.body.storage.value + '</data>';
                let parser = new DOMParser();
                let xmlDoc = parser.parseFromString(content, "text/xml");
                processNode(xmlDoc.childNodes[0]);
            } catch (err) {
                cb(err, [])
            }
            cb(null, diagrams);
        },
        error: (err) => {
            console.log(err)
            cb(err, [])
        }
    });
}

const saveImage = (cb) => {
    try {
        var imgData = canvas.exportToImage('png');
    } catch (e) {
        cb(e);
        return;
    }
    fetch(imgData.data)
    .then(res => res.blob())
    .then(blob => {
        let fileName = macroData.attachmentName + ".png";
        saveAttachment(fileName, blob, null, (err) => {
            if (err == null) {
                macroData.width = imgData.width;
                macroData.height = imgData.height;
            }
            cb(err);
        });
    })
    .catch(cb);
}

const publish = (cb) => {
    var lekhContent = window.Module.getLekh();
    var attName = macroData.attachmentName;
    if (!attName) {
        attName = v4();
    }
    var fileName = attName + ".lekh";
    if (setAppState != null) {
        setAppState('saving')
    }
    saveAttachment(fileName, lekhContent, null, (err) => {
        if (!err) {
            sendEventToBackend(eventTypes.save);
            saveCustomContent(macroData.customContentId, (custErr, custResp) => {
                if (!custErr) {
                    let respObj = null;
                    try {
                        respObj = JSON.parse(custResp);
                    } catch (e) { }
                    if (respObj != null) {
                        macroData.customContentId = respObj.id;
                    }
                }
                saveImage(imgErr => {
                    if (imgErr != null) {
                        console.log("Error in saving image attachment ", imgErr);
                    }
                    macroData.counter = !!macroData.counter? macroData.counter + 1 : 1;
                    macroData.pageId = pageId;
                    window.AP.confluence.saveMacro(macroData);
                    deleteDraft(e=> {
                        setTimeout(() => {
                            window.AP.dialog.close();
                        }, 1);
                        cb(null);
                    });
                });
            });
        } else {
            const onAccept = () => {
                if (setAppState != null) {
                    setAppState('ready')
                }
                showDialog(null);
                cb(err);
            }
            let title = "Error";
            let msg = "Unable to publish changes. Please check that you are connected to the internet and try again.";
            showDialog(<MessageDialog title={title} msg={msg} okText={"OK"} onAccept={onAccept}/>)
            console.log('Error in saving attachment', err)
        }
    });
}

const renameDiagram = () => {
    const rename = (name) => {
        macroData.diagramName = name;
        docInfo.name = name;
        numUpdatesInEditor++;
        showDialog(<MessageDialog title={"Diagram renamed"} msg={"You need to publish to save the new name."} okText={"OK"} onAccept={()=>showDialog(null)} />)
    }

    const cancel = () => {
        showDialog(null);
    }

    let curName = !!macroData.diagramName? macroData.diagramName: '';
    showDialog(<FileNameInputDialog title={"Enter New Name"} cancelText={"Cancel"} okText={"Rename"} onCancel={cancel} onAccept={rename} initialValue={curName}/>)
}

const importLekhFile = () => {
    const doImport = () => {
        const onImport = () => {
            sendEventToBackend(eventTypes.diagramImport);
            numUpdatesInEditor++;
            setPageSize();
        }
        showDialog(<LekhImporter showDialog={showDialog} onImport={onImport} onCancel={()=>{}} />)
    }

    let msg = "Importing a Lekh file will replace the current diagram with a new Lekh file. This operation cannot be undone. Do you want to proceed?";
    let title = "Import";
    showDialog(<ConfirmDialog title={title} msg={msg} cancelText={"No"} okText={"Yes"} onCancel={()=>showDialog(null)} onAccept={doImport}/>)
}

const importFromCloud = () => {
    const doImport = () => {
        const close = () => {
            showDialog(null);
        }

        const pick = (docid) => {
            showDialog(null);
            setAppState('importing');
            window.Module.getRemoteLekhContent(docid, (err, content) => {
                var errMsg = null;
                if (!err) {
                    var out = window.Module.importLekh(content);
                    if (out) {
                        errMsg = "Unable to import due to unexpected error. Please contact our support";
                    } else {
                        canvas.draw();
                        numUpdatesInEditor++;
                    }
                } else {
                    errMsg = "Unable to fetch the diagram from the cloud. Please try again. If the problem persists then contact our support"
                }

                if (errMsg) {
                    sendEventToBackend(eventTypes.cloudImportError);
                    showDialog(<MessageDialog title={"Error"} msg={errMsg} okText={"OK"} onAccept={()=>showDialog(null)}/>)
                } else {
                    sendEventToBackend(eventTypes.cloudImport);
                }
                setAppState('ready');

                setTimeout(() => {
                    canvas.draw();
                }, 10);
            });
        }

        let picker = <LekhItemPicker close={close} pick={pick} defaultTitle={"Import"} folderOnly={false} pickText={"Import"}/>
        showDialog(picker);
    }
    let msg = "Importing from Lekh Cloud will replace the current diagram with the imported file. This operation cannot be undone. Do you want to proceed?";
    let title = "Import";
    showDialog(<ConfirmDialog title={title} msg={msg} cancelText={"No"} okText={"Yes"} onCancel={()=>showDialog(null)} onAccept={doImport}/>)
}

const copyToLekhCloud = () => {
    const close = () => {
        showDialog(null);
    }
    const newName = (children) => {
        const isUnique = (name) => {
            for (let item of children) {
                if (item.name == name) {
                    return false;
                }
            }
            return true;
        }
        var name = macroData.diagramName;
        var count = 0;
        while (!isUnique(name)) {
            count++;
            name = macroData.diagramName + "-" + count;
        }
        return name;
    }

    const pick = (docid, children) => {
        showDialog(null);
        let diagName = newName(children);
        var content = window.Module.getLekh();
        setAppState('exporting');
        window.Module.uploadDoc(diagName, docid, '', content, (err) => {
            setAppState('ready');
            let title = "";
            let msg = "";
            if (err) {
                title = "Error";
                msg = "Unable to copy. Please try again. If problem persists then contact our support";
            } else {
                title = "Copied successfully";
                msg = "";
            }
            showDialog(<MessageDialog title={title} msg={msg} okText={"OK"} onAccept={()=>showDialog(null)}/>)
        })
    }
    let picker = <LekhItemPicker close={close} pick={pick} defaultTitle={"Select Folder"} folderOnly={true} pickText={"Copy Here"}/>
    showDialog(picker);
}

const setPageSize = () => {
    var pageEditor = window.Module.startPageEditing();
    pageEditor.setSize(window.Module.CanvasSizeType.Infinite, {width: 0, height: 0, unit: window.Module.Unit.Inch}, "infinite");
}

atlassian.setShowDialogHandler = (dialogHandler, setStateForApp) => {
    showDialog = dialogHandler;
    setAppState = setStateForApp;
    eventListener.trigger();
}

atlassian.init = (params) => {
    logDebug(() => console.log('Init called: ', performance.now()));

    if (params.editor == 'true') {
        integMode = {
            useLekhBackend: false,
            editor: {
                showOptions: true,
                fileNameEditable: true,
                readOnly: false
            },
            viewer: null
        };
    } else {
        integMode = {
            useLekhBackend: false,
            editor: null,
            viewer: {
                full: params.viewertype == 'full',
                makeLink: params.viewertype != 'full',
                showEditOption: false,
                showOptions: false
            },
        };
    }

    const checkLicenseAndGetPageContext = () => {
        // check license in prod only
        var checkLicense = constant.baseUrl.startsWith('https://api.lekh.app');
        if (checkLicense && integMode.editor != null) {
            if (!params.lic || params.lic == 'none') {
                waitFor(() => showDialog != null).then(() => {
                    let title = "Invalid license";
                    let msg = "You do not have a valid licence to use Lekh Diagram";
                    showDialog(<MessageDialog title={title} msg={msg} okText={"OK"} onAccept={()=> {
                        showDialog(null);
                        closeEditor(false);
                    }}/>)
                });
                return;
            }
        }

        getPageContext();
    }

    const checkCustomContent = () => {
        var customContentId = null;
        if (params.custom == 'true') {
            customContentId = params.customContentId;
        }
        if (customContentId == null) {
            checkLicenseAndGetPageContext();
        } else {
            getContentInfo(customContentId, true, (err, resp) => {
                if (err == null) {
                    customContentInfo = JSON.parse(decodeURIComponent(resp.body.storage.value));
                    checkLicenseAndGetPageContext();
                }
            });
        }
    }
    
    if (!window.AP) {
        var head = document.getElementsByTagName('head')[0];
        var script = document.createElement('script');
        script.type = 'text/javascript';
        script.onload = function() {
            checkCustomContent()
        }
        script.src = 'https://connect-cdn.atl-paas.net/all.js';
        head.appendChild(script);
    } else {
        checkCustomContent();
    }
}

atlassian.getIntegMode = (params) => {
    return integMode;
}

atlassian.getUser = (cb) => {
    //return state, token, info
    cb('available', 'dummy', {name: ''});
}

atlassian.getDocInfo = (cb) => {
    // return state, docid, info
    waitFor(()=> macroData != null).then(() => {
        if (macroData.diagramName) {
            docInfo.name = macroData.diagramName;
        }
        if (macroData.attachmentName) {
            docInfo.docid = macroData.attachmentName;
        }
        cb('available', 'dummy', docInfo);
    })
}

atlassian.handleClickOnViewer = (evt) => {
    var name = "";
    if (macroData && macroData.diagramName) {
        name = macroData.diagramName;
    }
    window.AP.dialog.create({
        key: 'viewer-full-screen',
        width: '100%',
        height: '100%',
        chrome: true,
        header: name
      }).on("close", () => {
      });
}

atlassian.closeEditor = () => {
    closeEditor(true);
}

atlassian.publishEditorChanges = async () => {
    return new Promise((resolve, reject) => {
        if (numUpdatesInEditor > draftSavedAtCounter) {
            getDiagramsInPage((err, diagrams) => {
                if (err) {
                    sendEventToBackend(eventTypes.macroFindError, null, err);
                }
                let count = 0;
                if (!!diagrams) {
                    for (let item of diagrams) {
                        if (item.attachmentName == macroData.attachmentName) {
                            count++
                            if (count > 1) {
                                break;
                            }
                        }
                    }
                }
                if (count > 1) {
                    // there exists a macro with same attachment name
                    // to fix the conflicts, rename the attachment name
                    // for this macro. This happens in copy paste
                    macroData.attachmentName = v4();
                    macroData.customContentId = null;
                }
                publish((err) => {
                    if (err == null) {
                        resolve();
                    } else {
                        reject(err);
                    }
                });
            });
        } else {
            deleteDraft(e=> {
                setTimeout(() => {
                    window.AP.dialog.close();
                }, 1);
                resolve();
            });
        }
    });
}

atlassian.loadDoc = async () => {
    logDebug(() => console.log('loadDoc called: ', performance.now()));
    await waitFor(() => lekhAttachment != null);
    if (integMode.editor != null) {
        await waitFor(() => draftAttachment != null);
        if (draftAttachment != '') {
            confirmDraftLoad();
            await waitFor(() => draftAttachment == null || draftAttachment == '');
        }
    }

    if (lekhAttachmentReadErr != null && integMode.viewer != null && lekhAttachment == '') {
        window.Module.loadDoc(JSON.stringify(LekhErrJson));
    } else {
        window.Module.loadDoc(lekhAttachment);
    }

    logDebug(() => console.log('doc loaded: ', performance.now()));

    // override the send so that we can track user's changes in the doc
    window.wsmanager.send = onDiagramChange;
    window.Module.startDoc(macroData.attachmentName? macroData.attachmentName : 'docid');
    
    setPageSize();

    window.drawCanvas();

    if (integMode.viewer != null) {
        setViewerSize();
        if (!integMode.viewer.full) {
            appEvent.resizeListener.addListener(setViewerSize);
        }
    }
    window.wsmanager.setState('ready');
}

atlassian.customizeSidebar = (sidebarMenu) => {
    let items = [
    {
        id: menuState.menus.selection,
        icon: <FaMousePointer size="1.2em" color="#000000b8"/>,
        name: 'Select',
    },
    {
        id: menuState.menus.move,
        icon: <FaHandRock size="1.2em" color="#000000b8"/>,
        name: 'Move',
    }];

    return items.concat(sidebarMenu.slice(2));
}

atlassian.customizeAppMenu = (appMenu) => {
    var subOption = [
        {id: 'fromLekhFile', name: 'Lekh File', handler: importLekhFile},
        {id: 'fromLekhCloud', name: 'From Lekh Cloud', handler: importFromCloud}
    ]
    var menus = [
        { id: "renameDiagram", name: 'Rename Diagram', checkable: false, handler: renameDiagram },
        { id: "importLekh", name: 'Import', submenu:  subOption},
        { id: "exportToCloud", name: 'Copy to Lekh Cloud', checkable: false, handler: copyToLekhCloud}
    ];

    var newMenu = appMenu.slice(1);
    newMenu.splice(5, 1);
    return menus.concat(newMenu)
}

