import Footer from '../components/Footer/Footer';
import Content from '../components/SideBarMenu/Content';
import React from "react";
import initializer from '../common/AppInit'
import integ from '../integ/integ';
import uiConfig from './UIConfig';
import canvas from './Canvas';

class Viewer extends React.Component {
  constructor(props) {
    super(props);
    this.state = { };
    this.wsCallback = this.handleWSState.bind(this);
  }

  handleWSState(state) {
    if (this.props.wsStateHandler) {
      this.props.wsStateHandler(state);
    }
  }

  setMouseCursor(attempt) {
    const fn = (tpanel) => {
      if (uiConfig.isViewerLink()) {
        tpanel.style.cursor = 'pointer';
        window.bean.on(tpanel, 'click', function (e) {
          if (e.button == 0) {
            e.preventDefault();
            integ.handleClickOnViewer(e);
          }
        });
      } else if (uiConfig.isFullViewer()) {
        tpanel.style.cursor = 'all-scroll';
      }
    };

    var tpanel = document.getElementById("transparentPanel");
    if (tpanel == null) {
      if (attempt > 3) {
        return;
      }
      var self = this;
      setTimeout(()=> {
        self.setMouseCursor(attempt + 1);
      }, 50);
    } else {
      fn(tpanel);
    }
  }

  render() {
    canvas.showGrid = false;
    return (
      <div className="container">
        <Content />
        <Footer />
      </div>
    );
  }

  componentDidMount() {
    initializer.init(this.wsCallback);
    initializer.onDocDetailFetch();
    this.setMouseCursor(0);
  }
}

export default Viewer;
