import $ from 'jquery';
import EventListener from '../../utils/EventListener';
import { JSDrawingPlatform } from '../../common/Platform';
import ImageUploader from '../ImageUploader/ImageUploader';
import { Dialog } from "@material-ui/core";
import DialogTitle from '@material-ui/core/DialogTitle';
import DialogActions from '@material-ui/core/DialogActions';
import DialogContent from '@material-ui/core/DialogContent';
import TextField from '@material-ui/core/TextField';
import Button from '@material-ui/core/Button';
import React, {useState} from "react";
import Help from './ShapeHelp';
import ShareModal from '../DashboardItems/ShareModal';
import ContextHelp from '../ContextHelp/ContextHelp';

const isDarkMode = () => {
    if (window.matchMedia && window.matchMedia('(prefers-color-scheme: dark)').matches) {
        return true;
    }
    return false;
}

const InputDialog = ({input, accept, cancel}) => {
    const [value, setValue] = useState(input.value);
    var okDisabled = input.allowEmpty? false : value.trim() == '';
    return (
    <Dialog onClose={cancel} aria-labelledby="simple-dialog-title" open={true} style={{zIndex:1550}}>
        <DialogTitle id="simple-dialog-title">{input.title}</DialogTitle>
        <DialogContent>
            <div>{input.message}</div><br/>
            <TextField
                autoFocus
                margin="dense"
                id="name"
                fullWidth
                variant="outlined"
                value={value}
                onChange={(e) => setValue(e.target.value)}
            />
        </DialogContent>
        <DialogActions>
            <Button onClick={cancel}>{input.cancelText}</Button>
            <Button onClick={()=> {accept(value)}} disabled={okDisabled}>{input.okText}</Button>
        </DialogActions>
    </Dialog>
    );
};

const showInputDialog = (input, cb) => {
    let types = window.Module.avabodh_lekh_DialogResponse_Type;
    let cancel = () => {
        viewPort.showDialog(null);
        cb.exec({type: types.Cancelled, value: ''});
    }
    let accept = (value) => {
        viewPort.showDialog(null);
        cb.exec({type: types.Accepted, value: value});
    }
    let dialog = <InputDialog input={input} accept={accept} cancel={cancel} />;
    viewPort.showDialog(dialog);
}

const showOkCancel = (title, msg, okText, cancelText, cb) => {
    let types = window.Module.avabodh_lekh_DialogResponse_Type;
    let cancel = () => {
        viewPort.showDialog(null);
        cb.exec({type: types.Cancelled, value: ''});
    }
    let accept = () => {
        viewPort.showDialog(null);
        cb.exec({type: types.Accepted, value: ''});
    }
    let dialog = <Dialog onClose={cancel} aria-labelledby="simple-dialog-title" open={true} style={{zIndex:1550}}>
        <DialogTitle id="simple-dialog-title">{title}</DialogTitle>
        <DialogContent>
            <div>{msg}</div>
        </DialogContent>
        <DialogActions>
            <Button onClick={cancel}>{cancelText}</Button>
            <Button onClick={accept}>{okText}</Button>
        </DialogActions>
    </Dialog>
    viewPort.showDialog(dialog);
}   

const showMsg = (title, msg, okText, cb) => {
    if (!okText) {
        okText = "OK";
    }
    let types = window.Module.avabodh_lekh_DialogResponse_Type;
    let cancel = () => {
        viewPort.showDialog(null);
        cb.exec({type: types.Cancelled, value: ''});
    }
    let accept = () => {
        viewPort.showDialog(null);
        cb.exec({type: types.Accepted, value: ''});
    }
    let dialog = <Dialog onClose={cancel} aria-labelledby="simple-dialog-title" open={true} style={{zIndex:1550}}>
        <DialogTitle id="simple-dialog-title">{title}</DialogTitle>
        <DialogContent>
            <div>{msg}</div>
        </DialogContent>
        <DialogActions>
            <Button onClick={accept}>{okText}</Button>
        </DialogActions>
    </Dialog>
    viewPort.showDialog(dialog);
}

class ViewPort {
    constructor() {
        this.resizeListener = new EventListener();

        this.grid = null;
        this.draw = null;
        this.middle = null;
        this.ui = null;
                
        this.gridDp = null;
        this.drawDp = null;
        this.middleDp = null;
        this.uiDp = null;

        this.gridNeedsUpdate = false;
        this.drawNeedsUpdate = false;
        this.middleNeedsUpdate = false;
        this.uiNeedsUpdate = false;

        this.hostEnv = null;
        this.lekhCore = null;

        this.canvasTypeEnum = null;

        this.showDialog = null;
        this.showShapeHelp = null;
        this.showAirGesture = null;
    }

    attachResizerHandler() {
        let self = this;
        $(window).on("resize", function (evt) {
            self.onResize();
            self.resizeListener.trigger(evt);
        });
    }

    attachScreenModeHandler() {
        let self = this;
        window.matchMedia('(prefers-color-scheme: dark)').addEventListener('change', event => {
            self.reactTo(window.Module.avabodh_lekh_ReactParam.ConfigChange);
        });        
    }

    onResize() {
        let dc = document.getElementById("drawContent");
        let rect = dc.getBoundingClientRect();
        this.fixCanvas(this.middle, rect.width, rect.height);
        this.fixCanvas(this.ui, rect.width, rect.height);
        this.hostEnv.setScreenSize({width: rect.width, height: rect.height});
        this.drawCanvas(this.canvasTypeEnum.Middle);
        this.drawCanvas(this.canvasTypeEnum.UI);
    }

    fixCanvas(cnv, width, height) {
        var dpr = window.devicePixelRatio || 1;
        cnv.width = width * dpr;
        cnv.height = height * dpr;
        cnv.style.width = width + "px";
        cnv.style.height = height + "px";
        var ctx = cnv.getContext("2d");
        ctx.scale(dpr, dpr);
    }

    setCanvasSize(left, top, width, height) {
        this.fixCanvas(this.grid, width, height);
        this.fixCanvas(this.draw, width, height);

        this.grid.style.left = left + 'px';
        this.grid.style.top = top + 'px';

        this.draw.style.left = left + 'px';
        this.draw.style.top = top + 'px';
    }

    measureText(lines, font, fontSize) {
        let sz = { width: 0, height: 0 };
        let ctx = this.drawDp.ctx;
        
        ctx.save();

        ctx.font = font;
        for(let i = 0; i < lines.size(); i++) {
            let str = lines.get(i);
            let metrics = ctx.measureText(str);
            if (sz.width < metrics.width) {
                sz.width = metrics.width;
            }
            sz.height += fontSize;
        }

        ctx.restore();

        return sz;
    }

    init() {
        this.hostEnv = window.Module.avabodh_lekh_app_HostEnv_instance();
        this.lekhCore = window.Module.avabodh_lekh_app_LekhCore_instance();
        this.canvasTypeEnum = window.Module.avabodh_lekh_CanvasType;

        this.grid = document.getElementById("gridCanvas");
        this.draw = document.getElementById("drawCanvas");
        this.middle = document.getElementById("middleCanvas");
        this.ui = document.getElementById("uiCanvas");

        this.gridDp = new JSDrawingPlatform();
        this.gridDp.init();
        this.gridDp.ctx = this.grid.getContext('2d');

        this.drawDp = new JSDrawingPlatform();
        this.drawDp.init();
        this.drawDp.ctx = this.draw.getContext('2d');

        this.middleDp = new JSDrawingPlatform();
        this.middleDp.init();
        this.middleDp.ctx = this.middle.getContext('2d');

        this.uiDp = new JSDrawingPlatform();
        this.uiDp.init();
        this.uiDp.ctx = this.ui.getContext('2d');

        var rect = this.ui.getBoundingClientRect();

        this.fixCanvas(this.grid, rect.width, rect.height);
        this.fixCanvas(this.draw, rect.width, rect.height);
        this.fixCanvas(this.middle, rect.width, rect.height);
        this.fixCanvas(this.ui, rect.width, rect.height);

        this.attachResizerHandler();
        this.attachScreenModeHandler();
        this.onResize();
        
        this.reactTo(window.Module.avabodh_lekh_ReactParam.ConfigChange);
    }

    initUI(initial) {
        this.lekhCore.initUI(initial);
    }

    drawCanvas(type) {
        let self = this;

        if (type == this.canvasTypeEnum.Grid) {
            if (!this.gridNeedsUpdate) {
                this.gridNeedsUpdate = true;
                requestAnimationFrame(()=> {
                    self.gridNeedsUpdate = false;
                    self.gridDp.ctx = self.grid.getContext('2d');
                    self.gridDp.ctx.clearRect(0, 0, self.grid.width, self.grid.height);
                    self.lekhCore.draw(self.gridDp.cppObj, type);
                });
            }
        } else if (type == this.canvasTypeEnum.Drawing) {
            if (!this.drawNeedsUpdate) {
                this.drawNeedsUpdate = true;
                requestAnimationFrame(()=> {
                    self.drawNeedsUpdate = false;
                    self.drawDp.ctx = self.draw.getContext('2d');
                    self.drawDp.ctx.clearRect(0, 0, self.draw.width, self.draw.height);
                    self.lekhCore.draw(self.drawDp.cppObj, type);
                });
            }
        } else if (type == this.canvasTypeEnum.Middle) {
            if (!this.middleNeedsUpdate) {
                this.middleNeedsUpdate = true;
                requestAnimationFrame(()=> {
                    self.middleNeedsUpdate = false;
                    self.middleDp.ctx = self.middle.getContext('2d');
                    self.middleDp.ctx.clearRect(0, 0, self.middle.width, self.middle.height);
                    self.lekhCore.draw(self.middleDp.cppObj, type);
                });
            }
        } else if (type == this.canvasTypeEnum.UI) {
            if (!this.uiNeedsUpdate) {
                this.uiNeedsUpdate = true;
                requestAnimationFrame(()=> {
                    self.uiNeedsUpdate = false;
                    self.uiDp.ctx = self.ui.getContext('2d');
                    self.uiDp.ctx.clearRect(0, 0, self.ui.width, self.ui.height);
                    self.lekhCore.draw(self.uiDp.cppObj, type);
                });
            }
        }
    }

    setPointerIconType(type) {
        if (this.ui == null) {
            return;
        }
        let pointers = window.Module.avabodh_lekh_PointerIconType;
        if (type == pointers.Action) {
            this.ui.style.cursor = 'pointer';
        } else if (type == pointers.Draw) {
            this.ui.style.cursor = 'url("pen-solid.svg") 0 20, auto';
        } else if (type == pointers.Scroll) {
            this.ui.style.cursor = 'grab';
        } else if (type == pointers.Text) {
            this.ui.style.cursor = 'url("title_black.svg") 10 10, auto';
        } else if (type == pointers.Laser) {
            this.ui.style.cursor = 'url("red_circle.svg") 10 10, auto';
        } else if (type == pointers.Erase) {
            this.ui.style.cursor = 'url("eraser-solid.svg") 10 10, auto';
        } else {
            this.ui.style.cursor = 'auto';
        }
    }

    pickImage(cb) {
        this.showDialog(<ImageUploader dialogHandler={this.showDialog} at={{x: 0, y: 0}} cb={cb} />)
    }

    openDialog(input, cb) {
        let types = window.Module.avabodh_lekh_DialogInput_Type;
        if (input.type == types.OkCancel) {
            showOkCancel(input.title, input.message, input.okText, input.cancelText, cb);
        } else if (input.type == types.TextInput) {
            showInputDialog(input, cb);
        } else {
            showMsg(input.title, input.message, input.okText, cb);
        }
    }

    openView(input, cb) {
        let types = window.Module.avabodh_lekh_OpenViewInput_Type;
        let dirs = window.Module.avabodh_lekh_OpenViewInput_PopuDir;

        if (input.type == types.Popup) {
            if (input.arg == "contact") {
                window.open("https://lekh.app/contact.html", '_blank').focus();
            } else if (input.arg == "share") {
                let doc = this.lekhCore.coreIO().currentlyOpenedRemoteDoc();
                let modal = <ShareModal state={true} close={() => this.showDialog(null)} doc={doc} />;
                this.showDialog(modal);
            } else if (input.arg == "main_help") {
                let content = '';
                if (!!input.content) {
                    content = '#' + input.content;
                }
                window.open("https://lekh.app/help/board.html" + content, '_blank').focus();
            } else if (input.arg == "platform_info") {
                window.open("https://lekh.app/help/about.html", '_blank').focus();
            } else if (input.arg.startsWith("context_info")) {
                let modal = <ContextHelp showDialog={this.showDialog} name={input.content} title={input.title}/>;
                this.showDialog(modal);
            }
        } else if (input.type == types.HTMLContent) {
            if (input.arg == "shape_help") {
                let popup = <Help input={input} showShapeHelp={this.showShapeHelp} />
                this.showShapeHelp(popup);
            }
        }
    }

    reactTo(param) {
        if (param == window.Module.avabodh_lekh_ReactParam.AirGesture) {
            this.showAirGesture(true);
            return;
        }

        if (param == window.Module.avabodh_lekh_ReactParam.PageReload) {
            window.location.reload();
            return;
        }

        if (param != window.Module.avabodh_lekh_ReactParam.ConfigChange) {
            return;
        }
        let isDark = false;
        let hostEnv = window.Module.avabodh_lekh_app_HostEnv_instance();

        if (hostEnv.getBrightnessConfig() == 1) {
            isDark = false;
        } else if (hostEnv.getBrightnessConfig() == 2) {
            isDark = true;
        } else {
            isDark = isDarkMode();
        }

        let dc = document.getElementById('drawContent');
        if (!dc) {
            return;
        }

        if (isDark) {
            hostEnv.setBrightnessMode(window.Module.avabodh_lekh_app_HostEnv_BrightnessMode.Dark);
            dc.style.background = '#000000';
        } else {
            hostEnv.setBrightnessMode(window.Module.avabodh_lekh_app_HostEnv_BrightnessMode.Light);
            dc.style.background = '#f2f2f7';
        }
    }
}

const viewPort = new ViewPort()

export default viewPort;
