import { getDoc } from "./ApiRequests";
import uiConfig from "../common/UIConfig";
import integ from "../integ/integ";

var docInfo = {
  state: 'loading',
  docid: '',
  data: {name: ''},
  onChange: () => { },
}

const onChange = () => {
  if (docInfo.onChange) {
    try {
      docInfo.onChange();
    } catch(err) {
      console.log(err)
    }
  }
}

docInfo.reset = () => {
  docInfo.state = 'loading';
  docInfo.data = {name: ''}
}

docInfo.get = async () => {
  if (!uiConfig.isUsingLekhBackend()) {
    integ.getDocInfo((state, docid, info) => {
      docInfo.state = state;
      docInfo.data = info;
      docInfo.docid = docid;
      onChange();
    });
    return;
  }

  docInfo.state = 'loading'
  try {
    let resp = await getDoc(docInfo.docid);
    docInfo.data = resp.data;
    docInfo.state = 'available';
  } catch(err) {
    console.log(err);
    docInfo.state = 'error';
  }
  onChange();
}

export default docInfo;
