import React, { useState } from "react";
import InputField from "../../components/InputField/InputField";
import { Link } from "react-router-dom";
import MiniLoader from "../../components/Loaders/MiniLoader";
import { login, loginWithGoogle, loginWithApple } from "../../utils/ApiRequests";
import util from "../../common/Util";
import { GoogleLogin } from "react-google-login";
import {
  setExpiryTimeToStorage,
  setRefreshTokenToStorage
  // setTokenToStorage
} from "../../utils/ApiUtils";
import { GrMail, GrApple } from "react-icons/gr";
import { useHistory } from "react-router-dom";
import { constant } from "../../utils/ApiConstants";

var attemptedLoadingAppleScript = false;
var appleAuthInitialized = false;

const loadAppleScript = async () => {
  if (attemptedLoadingAppleScript) {
    return;
  }
  attemptedLoadingAppleScript = true;
  await util.loadScript('apple/appleid.auth.js');
  //try {
    //await util.loadScript('https://appleid.cdn-apple.com/appleauth/static/jsapi/appleid/1/en_US/appleid.auth.js');
  //} catch(err) {
  //}
}

const initAppleAuth = () => {
  if (appleAuthInitialized) {
    return;
  }
  appleAuthInitialized = true;
  window.AppleID.auth.init({
    clientId: "com.avabodh.lekh.signin",
    scope: "name email",
    redirectURI: constant.appUrl,
    state: "lekhapp",
    usePopup: true,
  });
}

const Login = () => {
  const [loginForm, setloginForm] = useState({
    email: "",
    password: "",
    code: ""
  });
  const [loading, setLoading] = useState(false);
  const [formType, setFormType] = useState("regular");
  const [error, setError] = useState(null);
  const history = useHistory();
  const [successText, showSuccessText] = useState(false);
  const [appleScriptLoaded, setAppleScriptLoaded] = useState(false);

  if (!appleScriptLoaded) {
    loadAppleScript().then(() => {
      if (window.AppleID) {
        initAppleAuth();
        setAppleScriptLoaded(true);
      }
    })
  }

  const handleChange = e => {
    const name = e.target.name;
    const value = e.target.value;
    const newEntry = { [name]: value };
    setloginForm({ ...loginForm, ...newEntry });
  };

  const proceed = () => {
    if (
      (loginForm.email === "" || loginForm.password === "") &&
      formType === "regular"
    ) {
      return false;
    }
    return true;
  };

  const handleSubmit = e => {
    e.preventDefault();
    setLoading(true);

    if (formType === "email") {
      setloginForm({ ...loginForm, ...{ password: null } });
    }

    login(loginForm)
      .then(res => {
        if (formType === "email") {
          showSuccessText(true);
        } else {
          setRefreshTokenToStorage(res.data.refresh_token);
          setExpiryTimeToStorage(res.data.expires_at);
          history.push("/docs/mydocs");
        }
      })
      .catch(err => {
        setError(err?.response?.data?.message);
      })
      .finally(() => setLoading(false));
  };

  const handleGoogleLoginFailure = async googleData => {
    console.log(googleData);
    setError("Google Login Failed");
    setLoading(false);
  }

  const handleGoogleLogin = async googleData => {
    let res = null;
    try {
      res = await loginWithGoogle({token: googleData.tokenId});
    } catch (err) {
      setError(err?.response?.data?.message);
      setLoading(false);
      return;
    }
    setRefreshTokenToStorage(res.data.refresh_token);
    setExpiryTimeToStorage(res.data.expires_at);
    history.push("/docs/mydocs");
    setLoading(false);
  }

  const handleAppleLogin = async () => {
    var response;
    try {
      response = await window.AppleID.auth.signIn();
    } catch (err) {
      console.log(err);
      setError("Unable to authenticate with Apple");
      setLoading(false);
      return;
    }
      
    let token = '';
    let firstName = '';
    let lastName = '';
    let code = '';
    if (response.authorization) {
      token = response.authorization.id_token;
      code = response.authorization.code;
    }
    if (response.user && response.user.name) {
      firstName = response.user.name.firstName;
      lastName = response.user.name.lastName;
    }
    let data = {
      token: token,
      code: code,
      first_name: firstName,
      last_name: lastName
    }
    try {
      var res = await loginWithApple(data);
    } catch (err) {
      console.log(err)
      setError("Unknown error!");
      setLoading(false);
      return;
    }
    setRefreshTokenToStorage(res.data.refresh_token);
    setExpiryTimeToStorage(res.data.expires_at);
    history.push("/docs/mydocs");
    setLoading(false);
  }

  return (
    <div className="w-full min-h-screen flex justify-center items-center ">
      <form
        //   className=""
        onSubmit={handleSubmit}
        className="w-full lg:w-3/5 px-5 lg:px-10 py-3 lg:py-6 shadow-md"
      >
        <h1 className="font-medium text-xl  text-center mb-7">
          Sign in to Lekh{" "}
        </h1>

        <>
          <InputField
            name="email"
            label="Email"
            placeholder="you@address.com"
            required={true}
            type="email"
            handleChange={handleChange}
            value={loginForm.email}
          />

          <InputField
            name="password"
            type="password"
            placeholder="Password"
            required={true}
            label="Password"
            value={loginForm.password}
            handleChange={handleChange}
          />

          {error && !loading && (
            <span className="-mt-3 block italic text-red-500 text-sm">
              {error}
            </span>
          )}
        </>

        <div className="flex ">
          {loading ? (
            <MiniLoader />
          ) : (
            <button
              type="submit"
              className={`py-2 lg:py-3.5 px-12 font-Poppins bg-lkhRed text-white rounded-3xl outline-none border-0 ${
                !proceed() ? "bg-lkhGrey cursor-not-allowed" : ""
              }`}
              disabled={!proceed()}
            >
              Log In
            </button>
          )}
        </div>

        <div className="flex items-start gap-2 mt-5 flex-col">
          <button
            className="flex items-center space-x-2 py-1 bg-white border-1 outline-none px-4 text-lg"
            onClick={e => {
              history.push("/passwordless-login");
            }}
          >
            <GrMail />
            <span>Login without password</span>
          </button>
        
          {<GoogleLogin
            render={renderProps => (
              <button className="flex items-center space-x-2 py-1 mt-2 bg-white border-1 outline-none px-4 text-lg"
                onClick={renderProps.onClick} disabled={renderProps.disabled}>
                <img src={require("../../assests/icons/google.svg").default} alt="Google" />{" "}
                <span>Sign in with Google &nbsp;&nbsp;&nbsp;&nbsp;</span>
            </button>
            )}
            clientId={"10145530965-qqoheu39gba07e38hi7obqjhtihhrpb9.apps.googleusercontent.com"}
            onSuccess={handleGoogleLogin}
            onFailure={handleGoogleLoginFailure}
            cookiePolicy={'single_host_origin'}
          />}

          <button
            disabled={!appleScriptLoaded}
            className="flex items-center space-x-2 py-1 mt-2 bg-white border-1 outline-none px-4 text-lg"
            onClick={e => {
              e.preventDefault()
              handleAppleLogin()
            }}
          >
            <GrApple />
            <span>Sign in with Apple &nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;</span>
          </button>
        </div>

        <div className="space-y-1 mt-2">
          <Link to="/forgot" className="text-lg font-medium block">
            Forgot Password?
          </Link>

          <p className="text-lg font-medium m-0">
            Don't have an account?{" "}
            <Link to="/sign-up" className="font-normal">
              Sign up here
            </Link>
          </p>
        </div>
      </form>
    </div>
  );
};

export default Login;
