import "./Gestures.css"

import {handleLandmarkResults, initLandmarkHandler, showCameraVideo} from "./LandmarkHandler";
import React, { useRef, useEffect } from 'react'
import {FaAngleDown, FaVideo} from 'react-icons/fa'
import { AiOutlineClose } from "react-icons/ai"
import { Button} from '@material-ui/core';
import {init as initGestures } from './Gestures'

var devices = [];
const getDevices = () => {
    devices = [];
    navigator.mediaDevices.enumerateDevices()
    .then((deviceInfos) => {
        for (const deviceInfo of deviceInfos) {
            if (deviceInfo.kind === 'videoinput') {
              var dev = {name: deviceInfo.label, id: deviceInfo.deviceId}
              devices.push(dev);
            }
          }
    })
}

const GesturePanel = (props) => {
    var [showList, setShowList] = React.useState(false);
    var [showCamera, setShowCamera] = React.useState(false);
    var [initialized, setInitialized] = React.useState(false)
    
    useEffect(() => {
        initGestures();
        getDevices();
        startCamera();
    }, []);

    function startMediapipe(devId) {
        console.log("Initializing mediapipe");
        window.frames[0].startCamera(devId, (results) => {
            handleLandmarkResults(results)
        });
    }
    function startCamera() {
        var outCanvas = document.getElementById('gestureCanvas');
        var cameraCanvas = document.getElementById('cameraCanvas');
        const iframe = document.getElementById('mpframe');
        if (!iframe || !outCanvas || !cameraCanvas) {
            window.setTimeout(() => {
                startCamera();
            }, 10)
        }
        initLandmarkHandler(outCanvas, cameraCanvas);
        iframe.addEventListener('load', () => {
            setInitialized(true);
            startMediapipe(null);
        }, true);
    }

    const onListChange = (event) => {
        console.log(event.target.value)
        setShowList(false);
        startMediapipe(event.target.value)
    }

    const onShowCamera = (event) => {
        if (initialized) {
            showCameraVideo(!showCamera)
            setShowCamera(!showCamera);
        }
    }

    const onShowList = (event) => {
        if (initialized && devices.length > 0) {
            setShowList(!showList);
        }
    }

    return <>
        <div className="gesture_panel">
            <canvas id="gestureCanvas" width="1280px" height="720px"></canvas>
            <iframe src="/mediapipe/mp.html" title="A" id="mpframe" width="0" height="0"></iframe> 
        </div>
        <Button className="listCameraButton" onClick={onShowList}><FaAngleDown /></Button>
        <Button className="showCameraVideoButton" onClick={onShowCamera}><FaVideo /></Button>
        <Button className="closeCameraVideoButton" onClick={()=>props.showAirGesture(false)}><AiOutlineClose /></Button>
        {showList &&
        <select name='option' className="cameraDropDown" size="4" onChange={onListChange}>
            {devices.map((dev) => {
                return <option key={dev.id} value={dev.id}>{dev.name}</option>
            })}
        </select>
        }
    </>
}

export default GesturePanel;


