import './CHelp.css'

import React from 'react'
import { Dialog } from "@material-ui/core";
import { AiOutlineClose } from "react-icons/ai"
import titles from './Captions';

const startCaption = (name, vid, spanElement) => {
    let curTitles = titles[name];
    let idx = 0;
    let showingIndex = -1;

    function updateCaption() {
        while (idx < curTitles.length) {
            if (vid.currentTime < curTitles[idx].start) {
                return;
            } else if (vid.currentTime >= curTitles[idx].end) {
                removeCaption();
                idx++;
            } else {
                updateForIndex(idx);
                break;
            }
        }
    }

    function removeCaption() {
        showingIndex = -1;
        spanElement.innerHTML = "&nbsp;";
    }

    function updateForIndex(index) {
        if (index == showingIndex) {
            return;
        }
        showingIndex = index;
        var msg = curTitles[index].msg;
        const isMac = navigator.userAgent.indexOf("OS X") > 0;
        if (isMac) {
            msg = msg.replace('Ctrl +', 'Cmd +')
        }
        spanElement.innerHTML = msg;
    }

    vid.addEventListener('loadeddata', function() {
        //vid.textTracks[0].mode = "showing";
        window.setTimeout(function() {
            if (vid.paused) {
                //vid.setAttribute("controls", "controls");
                vid.play();
            }
        }, 2000);
    });

    vid.addEventListener('timeupdate', function() {
        updateCaption();
    });

    vid.addEventListener('ended', function () {
        idx = 0;
        this.play();
    })
}

const checkAndStartCaption = (name) => {
    var vid = document.getElementById("tip_video");
    var span = document.getElementById("caption_text");
    if (vid == null || span == null) {
        setTimeout(()=> {
            checkAndStartCaption(name);
        }, 10);
        return;
    }
    window.setTimeout(()=> {
        if (vid != null && vid.paused) {
            vid.setAttribute("controls", "controls")
        }
    }, 200);
    startCaption(name, vid, span);
}

const ContextHelp = (props) => {
    let close = () => {
        props.showDialog(null);
    }

    React.useEffect(() => {
        checkAndStartCaption(props.name);
    }, [])

    let src = "a1/videos/cs/" + props.name + ".mp4";
    return <Dialog onClose={close} open={true}>
        <div className="tip_container">
            <div className="tip_header">
                <span className="tip_title">{props.title}</span>
                <AiOutlineClose className="tip_close" onClick={close}></AiOutlineClose>
            </div>
            <div className="vid_container">
                <span className="caption_text" id="caption_text">&nbsp;</span>
                <video id="tip_video" className="tip_video" autoPlay muted playsInline>
                    <source src={src} type="video/mp4" />
                </video>
            </div>
        </div>
    </Dialog>
}

export default ContextHelp;