import EventListener from "../utils/EventListener";
import { constant } from "../utils/ApiConstants";

const messaging  = {
    event: new EventListener(),
    data: null,
    init: () => { window.addEventListener("message", onMessage, false); }
}

const onMessage = (event) => {
    if (event.origin !== constant.appUrl) {
        return;
    }
    messaging.data = event.data;
    messaging.event.trigger(event);
}

export default messaging;