import React, { useState } from "react";
import InputField from "../../components/InputField/InputField";
import { Link, useHistory } from "react-router-dom";
import MiniLoader from "../../components/Loaders/MiniLoader";
import { signUp } from "../../utils/ApiRequests";
import { toast } from "react-toastify";
import { passwordCheck } from "./AuthUtil";

const SignUp = () => {
  const [signUpForm, setSignUpForm] = useState({
    email: "",
    password: "",
    first_name: "",
    last_name: "",
    cPassword: ""
  });
  const [loading, setLoading] = useState(false);
  const history = useHistory();
  const [error, setError] = useState(true);
  const [apiError, setApiError] = useState(null);
  const [submitSuccess, setSubmitSuccess] = useState(false);

  // console.log(error)
  const handleChange = e => {
    const name = e.target.name;
    const value = e.target.value;
    const newEntry = { [name]: value };
    setSignUpForm({ ...signUpForm, ...newEntry });
  };

  const handlePasswordChange = e => {
    const name = e.target.name;
    const value = e.target.value;
    const newEntry = { [name]: value };
    setError(passwordCheck.test(value));
    setSignUpForm({ ...signUpForm, ...newEntry });
  };

  const proceed = () => {
    if (signUpForm.email === "" || signUpForm.password === "") {
      return false;
    }
    if (signUpForm.password !== signUpForm.cPassword) {
      return false;
    }
    if (!error) {
      return false;
    }
    return true;
  };

  const handleSubmit = e => {
    e.preventDefault();
    setLoading(true);
    signUp(signUpForm)
      .then(res => {
        // console.log(res.data)
        //toast.success("Please follow the email to active your account");
        //history.push("/login");
        setSubmitSuccess(true);
      })
      .catch(err => {
        // console.log(err.response);
        setApiError(err.response.data.message);
      })
      .finally(() => setLoading(false));
  };

  return (
    <div className="w-full min-h-screen flex justify-center items-center ">
      <form
        //   className=""
        onSubmit={handleSubmit}
        className="w-full lg:w-3/5 px-5 lg:px-10 py-3 lg:py-6 shadow-md"
      >
        <h1 className="font-medium text-xl  text-center mb-7">
          Sign up to Lekh{" "}
        </h1>

        {!submitSuccess &&<><div className="w-full grid grid-cols-2 gap-5">
          <InputField
            name="first_name"
            label="First Name"
            placeholder="First Name"
            handleChange={handleChange}
            value={signUpForm.first_name}
          />
          <InputField
            name="last_name"
            label="Last name"
            placeholder="Last Name"
            handleChange={handleChange}
            value={signUpForm.last_name}
          />
        </div>

        <InputField
          name="email"
          label="Email"
          placeholder="you@address.com"
          required={true}
          type="email"
          handleChange={handleChange}
          value={signUpForm.email}
        />
        <InputField
          name="password"
          type="password"
          placeholder="Password"
          required={true}
          label="Password"
          value={signUpForm.password}
          handleChange={handlePasswordChange}
          error={!error}
          errorText="Password must be a minimum of 8 characters including a capital letter and special character"
        />

        <InputField
          name="cPassword"
          type="password"
          placeholder="Confirm Password"
          required={true}
          label="Confirm Password"
          value={signUpForm.cPassword}
          handleChange={handleChange}
          error={signUpForm.password !== signUpForm.cPassword}
          errorText="Passwords do not match"
        />

        {apiError && !loading && (
          <span className="-mt-3 block italic text-red-500 text-sm">
            {apiError}
          </span>
        )}
        <div className="flex ">
          {loading ? (
            <MiniLoader />
          ) : (
            <button
              type="submit"
              className={`py-2 lg:py-3.5 px-12 font-Poppins bg-lkhRed text-white rounded-3xl outline-none border-0 ${
                !proceed() ? "bg-lkhGrey cursor-not-allowed" : ""
              }`}
              disabled={!proceed()}
            >
              Create an account
            </button>
          )}
        </div></>}
        {submitSuccess && <>
          <p className="-mt-3 font-medium">
          Please follow the instructions in the email to activate your account.
          </p>
          <p className="-mt-3 italic text-sm">
          (Please check the <strong>"All Mail"</strong> folder if you don’t find the email in Inbox)
          </p>

          <p className="text-lg font-medium">
            Back to{" "}
            <Link to="/login" className="font-normal">
              Log in
            </Link>
          </p>
        </>}

        <p className="text-lg font-medium">
          Have an account?{" "}
          <Link to="/login" className="font-normal">
            Log in
          </Link>
        </p>
        <p className="text-lg font-medium">
          <Link to="/passwordless-login" className="font-normal">
          Log in
          </Link>
          {" "}without signing up
        </p>
      </form>
    </div>
  );
};

export default SignUp;
