import $ from 'jquery';
import canvas from './Canvas'
import AppInput from './AppInput'
import touchInput from './TouchInput'
import mouseInput from './MouseInput'
import keyboardInput from './KeyboardInput'
import uiConfig from './UIConfig';
import EventListener from '../utils/EventListener';

var appEvent = {
  resizeListener: new EventListener()
};

function attachResizeHandler() {
  $(window).on("resize", function(evt) {
    canvas.onResize();
    AppInput.onResize();
    appEvent.resizeListener.trigger(evt);
  });
}

appEvent.attachEventHandlers = function() {
  attachResizeHandler();
  var canvasElement = document.getElementById("transparentPanel");
  if (uiConfig.shouldAttachMouseEventHandler()) {
    mouseInput.attachEvents(canvasElement);
    touchInput.attachEvents(canvasElement);
  }
  if (uiConfig.shouldAttachKeyboardEventHandler()) {
    keyboardInput.attachEvents(canvasElement);
  }
}

export default appEvent;
