import {GestureRecognizer} from './GestureRecognizer';

import { 
    mkpt, 
    setCanvas,
    drawLandmarks,
    showInit,
    showCameraImage,
    clearCameraCanvas,
    clearLandmarkCanvas,
    showFlashMessage,
    drawActivePoint
    } from './Frame';

var PENDING_THRESHOLD = 30;

var gestureRecognizer = null;
var prevGesture = null;
var timer = null;
var pendingGesture = null;

var initStartTime = null;
var flashDuration = -1;

var showingVideo = false;
var flashing = true;

function handleLast(curGesture) {
    if (pendingGesture != null && curGesture.id != 'noop') {
        if (timer != null) {
            window.clearTimeout(timer);
            timer = null;
        }
        if (pendingGesture.id != curGesture.id) {
            pendingGesture.done();
        }
        pendingGesture = null;
    }

    if (prevGesture != null && prevGesture.id != curGesture.id) {
        if ((prevGesture.id == 'drag1' || prevGesture.id == 'drag2') &&
            prevGesture.dragger.moving &&
            curGesture.id == 'noop') {
            pendingGesture = prevGesture;
            timer = window.setTimeout(()=> {
                if (pendingGesture != null) {
                    pendingGesture.done()
                    pendingGesture = null;
                    timer = null;
                }
            }, PENDING_THRESHOLD);
        } else {
            prevGesture.done();
        }
    }

    prevGesture = curGesture;
}

function handle(landmarks, hand) {
    //if ((hand.toLowerCase() === "right")) {
    //    return;
    //}
    drawLandmarks(landmarks);

    var pts = []
    for (let item of landmarks) {
        pts.push(mkpt(item))
    }

    var curGesture = gestureRecognizer.get(landmarks, hand);
    handleLast(curGesture);
    curGesture.move(pts);
    var activeptIdx = curGesture.activePoint();
    if (activeptIdx >= 0) {
        drawActivePoint(landmarks[activeptIdx]);
    }
}

function showVideo(results) {
    if (!showingVideo && !flashing) {
        return;
    }
    showCameraImage(results.image)

    if (flashing) {
        var now = Date.now()
        if (now - initStartTime > flashDuration) {
            flashing = false;
            clearCameraCanvas();
        } else {
            showFlashMessage();
        }
    }
}

export var handleLandmarkResults = (results) => {
    clearLandmarkCanvas();
    showVideo(results);
    
    if (results.multiHandLandmarks && results.multiHandLandmarks.length > 0) {
        var lms = {left: [], right: []}
        //var used = new Set();
        for (let item of results.multiHandedness) {
            /*
            let idx = item.index;
            if (results.multiHandLandmarks.length > idx && !used.has(idx)) {
                used.add(idx);
                let hand = item.label.toLowerCase();
                if (hand == "left") {
                    lms.left.push(results.multiHandLandmarks[idx]);
                } else if (hand == "right"){
                    lms.right.push(results.multiHandLandmarks[idx]);
                }
            }*/
            // Right now we have enabled only one hand
            if (results.multiHandLandmarks.length > 0) {
                let hand = item.label.toLowerCase();
                // Left and right are revered becuase we are seeing front camera
                if (hand == "left") {
                    lms.right.push(results.multiHandLandmarks[0]);
                } else if (hand == "right"){
                    lms.left.push(results.multiHandLandmarks[0]);
                }
                break;
            }

        }
        for (let item of lms.left) {
            handle(item, 'left')
        }
        for (let item of lms.right) {
            handle(item, 'right')
        }
    }
}

export function initLandmarkHandler(canvasElement, cameraCanvas) {
    initStartTime = Date.now();
    if (gestureRecognizer == null) {
        gestureRecognizer = new GestureRecognizer(null);
    }
    if (flashing) {
        flashDuration = 7 * 1000;
    } else {
        flashDuration = 0;
    }
    showingVideo = false;

    setCanvas(canvasElement, cameraCanvas);
    showInit();
}

export function showCameraVideo(val) {
    clearCameraCanvas();
    showingVideo = val;
}

export default handleLandmarkResults;


