import util from './Util';

var keyboardInput = { 
    showingDialog: false
};

var listeners = [];
var postListeners = [];

const isMac = navigator.userAgent.indexOf("OS X") > 0;

keyboardInput.addListener = function(cb) {
    listeners.push(cb);
}

keyboardInput.removeListener = function(cb) {
    for (var i = 0; i < listeners.length; i++) {
        if (listeners[i] == cb) {
            listeners.splice(i, 1);
            return;
        }
    }
}

keyboardInput.addPostListener = function(cb) {
    postListeners.push(cb);
}

keyboardInput.removePostListener = function(cb) {
    for (var i = 0; i < postListeners.length; i++) {
        if (postListeners[i] == cb) {
            postListeners.splice(i, 1);
            return;
        }
    }
}

keyboardInput.handleKeyDown = function(event) {
    for (let cb of listeners) {
        if (cb(event)) {
            return;
        }
    }

    let cmd = window.Module.avabodh_lekh_app_Command_instance();
    let CmdKey = window.Module.avabodh_lekh_event_CmdKey;
    let Modifiers = window.Module.avabodh_lekh_event_Modifiers;
    let key = event.key.toLowerCase();

    if (key == 'backspace') {
        cmd.executeKeys2(CmdKey.Delete, Modifiers.None);
    } else if (key == 'delete') {
        cmd.executeKeys2(CmdKey.Delete, Modifiers.None);
    } else if (key == 'escape') {
        cmd.executeKeys2(CmdKey.Escape, Modifiers.None);
    } else if (key == 'enter') {
        cmd.executeKeys2(CmdKey.Enter, Modifiers.None);
    } else if (key == 'arrowright') {
        cmd.executeKeys2(CmdKey.RightArrow, Modifiers.None);
    } else if (key == 'arrowleft') {
        cmd.executeKeys2(CmdKey.LeftArrow, Modifiers.None);
    } else if (key == 'arrowup') {
        cmd.executeKeys2(CmdKey.UpArrow, Modifiers.None);
    } else if (key == 'arrowdown') {
        cmd.executeKeys2(CmdKey.DownArrow, Modifiers.None);
    } else {
        let modVal = util.getModifiersFromEvent(event);
        // convert Ctrl -> Meta
        if ((modVal & 2) != 0 && !isMac) {
            modVal &= ~2;
            modVal |= 16;
        }
        let modifiers = new window.Module.avabodh_lekh_event_Modifiers()
        modifiers.value = modVal;
        cmd.executeKeys(key, modifiers);
    }
    event.preventDefault();

    for (let cb of postListeners) {
        cb(event);
    }
}

keyboardInput.attachEvents = function(canvasElement) {
    canvasElement.addEventListener("keydown", e => keyboardInput.handleKeyDown(e), false);
}

export default keyboardInput;
