export function init() {
    toolMenu = window.Module.avabodh_lekh_app_ToolMenu_instance();
    hostEnv = window.Module.avabodh_lekh_app_HostEnv_instance();

    InputModeEnum = window.Module.avabodh_lekh_app_ToolMenu_InputMode;
    EventPointState = window.Module.avabodh_lekh_event_EventPointState;
}

const eventPointFromTouch = (x, y, state, ts) => {
    let mod = window.Module;
    let pt = {
      x: x,
      y: y,
      pointerId: -2,
      state: state,
      pressure: 0,
      altitude:0,
      azimuth: 0,
      timestamp: ts,
      pointerType: mod.avabodh_lekh_event_PointerType.Finger,
      button: mod.avabodh_lekh_event_MouseButton.None,
      flag: mod.avabodh_lekh_event_EventPoint_Flag.None
    };
    return pt;
  }

const sendPoint = (x, y, state) => {
    let mod = window.Module;
    let pe = new mod.avabodh_lekh_event_PointerEvent();
    let list = new mod.List_avabodh_lekh_event_EventPoint();
    let ts = Date.now();
    list.push_back(eventPointFromTouch(x, y, state, ts));
    pe.setPoints(list);
    hostEnv.onPointerEvent(pe);
}


function captureDrawMode() {
    if (toolMenu.inputMode() == InputModeEnum.Draw || toolMenu.inputMode() == InputModeEnum.SmartDraw) {
        lastDrawMode = toolMenu.inputMode();
    }

    /*
    if (menuState.selected == menuState.menus.smartDraw ||
        menuState.selected == menuState.menus.draw) {
            lastDrawMode = menuState.selected;
    }*/
}

function setDrawMode() {
    if (toolMenu.inputMode() == InputModeEnum.Draw || toolMenu.inputMode() == InputModeEnum.SmartDraw) {
        lastDrawMode = toolMenu.inputMode();
        return;
    }

    if (lastDrawMode != InputModeEnum.Draw && lastDrawMode != InputModeEnum.SmartDraw) {
        lastDrawMode = InputModeEnum.SmartDraw;
    }
    toolMenu.setInputMode(lastDrawMode);

    /*
    if (menuState.selected == menuState.menus.smartDraw ||
        menuState.selected == menuState.menus.draw) {
            return;
    }
    if (!(lastDrawMode == menuState.menus.draw ||
        lastDrawMode == menuState.menus.smartDraw)) {
            lastDrawMode = menuState.menus.smartDraw;
    }
    menuState.handleMenuClick(lastDrawMode);
    menuState.popup = null;
    updateSidebarMenus();*/
}

function setSelectionMode() {
    if (toolMenu.inputMode() == InputModeEnum.Select) {
        return;
    }
    captureDrawMode();
    toolMenu.setInputMode(InputModeEnum.Select);

    /*
    if (menuState.selected == menuState.menus.selectionGroup &&
        menuState.inputMode == menuState.modes.selection) {
        return;
    }

    captureDrawMode();
    menuState.handleMenuClick(menuState.menus.selection);
    menuState.popup = null;
    updateSidebarMenus();*/
}

function setEraseMode() {
    if (toolMenu.inputMode() == InputModeEnum.Erase) {
        return;
    }
    captureDrawMode();
    toolMenu.setInputMode(InputModeEnum.Erase);

    /*
    if (menuState.selected == menuState.menus.erase) {
        return;
    }
    
    captureDrawMode();
    menuState.handleMenuClick(menuState.menus.erase);
    menuState.popup = null;
    updateSidebarMenus();*/
}

function checkModeBeforeClick() {
    /*
    if (menuState.selected == menuState.menus.smartDraw ||
        menuState.selected == menuState.menus.draw) {
            return;
    }*/
    if (toolMenu.inputMode() == InputModeEnum.Draw || toolMenu.inputMode() == InputModeEnum.SmartDraw) {
        return;
    }
    setSelectionMode();
}

export class Gesture {
    constructor(id) {
        this.dragger = new Dragger();
        this.id = id;
        this.ptidx = 0;
    }
    
    done() {
    }

    move(landmarks) {
    }

    setMode() {
    }

    sendPoints() {
        var pts = this.dragger.points;
        if (pts.length > this.ptidx) {
            if (this.ptidx == 0) {
                this.setMode();
                sendPoint(pts[0].x, pts[0].y, EventPointState.Down);
                //AppInput.drag(pts[0].x, pts[0].y, 0);
                this.ptidx = 1;
            }
            while (this.ptidx < pts.length) {
                sendPoint(pts[this.ptidx].x, pts[this.ptidx].y, EventPointState.Move);
                //AppInput.drag(pts[this.ptidx].x, pts[this.ptidx].y, 1);
                this.ptidx++;
            }
        }
    }

    checkClick() {
        if (!this.dragger.moving && this.dragger.last != null)  {
            checkModeBeforeClick();
            //handleClick(this.dragger.last);
            var mpt = mirrorPoint(this.dragger.last);
            sendPoint(mpt.x, mpt.y, EventPointState.Down);
            sendPoint(mpt.x, mpt.y, EventPointState.Up);
            this.dragger.points = []
        }
    }

    sendLastPoint() {
        var pts = this.dragger.points;
        if (pts.length > 0 && this.ptidx > 0) {
            var lastpt = pts[pts.length - 1];
            sendPoint(lastpt.x, lastpt.y, EventPointState.Up);
            //AppInput.drag(lastpt.x, lastpt.y, 2);
        }
        this.ptidx = 0;
        this.dragger.reset();
    }

    activePoint() {
        return -1;
    }
}

export class Drag1Gesture extends Gesture {
    constructor() {
        super("drag1");
    }

    setMode() {
        setSelectionMode();
    }

    move(landmarks) {
        this.dragger.track(landmarks[4], landmarks[0]);
        this.sendPoints();
    }

    done() {
        this.checkClick();
        this.sendLastPoint();
    }

    activePoint() {
        return 4;
    }
}

export class Drag2Gesture extends Gesture {
    constructor() {
        super("drag2");
        
    }

    setMode() {
        setDrawMode();
    }

    move(landmarks) {
        this.dragger.track(landmarks[8], landmarks[0]);
        this.sendPoints();
    }

    done() {
        this.checkClick();
        this.sendLastPoint()
    }

    activePoint() {
        return 8;
    }
}

export class ThumbGesture extends Gesture {
    constructor() {
        super("thumb");
        this.dragger.MOVE_DIST_REF = 1.0;
    }

    setMode() {
        setEraseMode();
    }

    move(landmarks) {
        this.dragger.track(landmarks[4], landmarks[0]);
        this.sendPoints();
    }

    done() {
        this.sendLastPoint();
    }

    activePoint() {
        return 4;
    }
}

export class PinchGesture extends Gesture {
    constructor() {
        super("pinch");
        this.dragger.MOVE_DIST_REF = 1.0;
        this.dragger.MOVE_DIST_SMALL = 1.0;
        this.dragger.MOVE_DIST_SMALL_REF = 2.0;
    }

    move(landmarks) {
        let mod = window.Module;

        this.dragger.track(landmarks[0], landmarks[0]);
        var pts = this.dragger.points;
        while (this.ptidx < pts.length) {
            if (this.ptidx > 0) {
                let p2 = pts[this.ptidx];
                let p1 = pts[this.ptidx - 1];
                let dx = p2.x - p1.x;
                let dy = p2.y - p1.y;
                let se = new mod.avabodh_lekh_event_ScrollEvent();
                se.setDelta(dx, dy);
                se.setType(mod.avabodh_lekh_event_ScrollEvent_Type.Generic);
                hostEnv.onScrollEvent(se);
                //canvas.moveDelta(p2.x - p1.x, p2.y - p1.y)
            }
            this.ptidx++;
        }
    
    }

    done() {
        this.ptidx = 0;
        this.dragger.reset();
    }
}

