import { Dialog } from "@material-ui/core";
import React, { useState, useEffect } from "react";
import { Button } from '@material-ui/core';
import { AiOutlineClose } from "react-icons/ai";
import { constant } from "../../utils/ApiConstants";
import { ApiRequestWithToken } from "../../utils/ApiRequests";
import docInfo from "../../utils/DocInfo";
import canvas from "../../common/Canvas";
import remoteResouceManager from "../../utils/RemoteResourceManager";
import uiConfig from "../../common/UIConfig";

const MaxSize = 5 * 1024 * 1024;
var selectedImage = null;

const ImageUploader = (props) => {
  const [file, setFile] = useState(null);
  const [width, setWidth] = useState(0);
  const [height, setHeight] = useState(0);
  const [errMsg, setErrMsg] = useState('');
  const [uploading, setUploading] = useState(false);

  var btnText = 'Upload';
  if (uploading) {
    btnText = "Uploading..";
  }
  if (!uiConfig.isUsingLekhBackend()) {
    btnText = "Insert";
  }
  var btnEnabled = true;
  if (uploading || width == 0 || height == 0 || file == null || file.size > MaxSize) {
    btnEnabled = false;
  }
  const onChange = (e) => {
    var changedFile = e.target.files[0];
    setFile(changedFile);
    setWidth(0);
    setHeight(0);
    if (changedFile.size > MaxSize) {
      setErrMsg('Must be less than 5M');
    } else {
      setErrMsg('');
    }

    var objectUrl = window.URL.createObjectURL(changedFile);
    var img = new Image();
    img.src = objectUrl;
    img.onload = function() {
      setWidth(img.width)
      setHeight(img.height);
      //window.URL.revokeObjectURL(objectUrl)
      if (selectedImage != null) {
        window.URL.revokeObjectURL(selectedImage.src);
      }
      selectedImage = img;
    }
  }

  /*
  const insertImage = () => {
    var objectUrl = window.URL.createObjectURL(file);
    var img = new Image();
    img.src = objectUrl;
    img.onload = function() {
      window.Module.insertImage(img, props.at.x, props.at.y, img.width, img.height);
      window.URL.revokeObjectURL(objectUrl);
      props.dialogHandler(null);
      setTimeout(() => {
        canvas.draw();
      }, 1);
    }
  }
  */

  const handleSubmit = e => {
    e.preventDefault();
    props.cb.exec(false, selectedImage);
    props.dialogHandler(null);

    /*
    
    if (!uiConfig.isUsingLekhBackend()) {
      insertImage();
      return;
    }

    const dataArray = new FormData();
    dataArray.append("data", file);
    dataArray.append("type", 'image');
    dataArray.append("scope", docInfo.docid);

    setUploading(true);
    ApiRequestWithToken()
      .post(constant.baseUrl + "/v1.1/resources/upload", dataArray, {
        headers: {
          "Content-Type": "multipart/form-data"
        }
      })
      .then((resp) => {
        props.dialogHandler(null);
        var url = 'res://' + resp.data.resid;
        window.Module.inputReceiver().insertImageWithResource(url, {width:width, height: height}, props.at);
        remoteResouceManager.addToResources(url, selectedImage);
        canvas.draw();
      })
      .catch((err) => {
          setErrMsg('Error in upload');
          console.log(err);
      });*/
  };

  const close = ()=> {
      props.dialogHandler(null);
  }

  return (
    <Dialog open={true} className="px-5">
        <div className="flex justify-between items-center my-2 px-2">
            <div />
            <h4 className="text-center m-0">Insert Image</h4>
            <AiOutlineClose className="text-xl cursor-pointer" onClick={close} />
        </div>
        <form
            action=""
            className=""
            onSubmit={handleSubmit}
        >
            <input type='file' accept="image/*" disabled={uploading} className="mx-3 mt-4" onChange={onChange}/>
            {errMsg && 
              <div className="mt-2 mx-3 text-red-600 text-sm">
                <label>{errMsg}</label>
              </div>
            }
            <div className="flex mb-4 mt-5 mx-3">
                <div className="mr-4"><Button variant="outlined" onClick={close}>Cancel</Button></div>
                <Button variant="outlined" disabled={!btnEnabled} onClick={handleSubmit}>{btnText}</Button>
            </div>
        </form>
    </Dialog>
  );
};

export default ImageUploader;
