import viewPort from "../components/ViewPort/ViewPort";
import { JSDrawingPlatform } from "./Platform";
import Dialog from '@material-ui/core/Dialog';
import Button from '@material-ui/core/Button';
import DialogTitle from '@material-ui/core/DialogTitle';
import { jsPDF } from "jspdf";
import util from "./Util";

var FileSaver = require('file-saver');

const errMsg = 'Unable to export. Canvas is probably too large. Please try exporting from Android / iOS app or contact support at info@lekhapp.com';

class Exporter
{
    constructor() {
        this.input = null;
        this.showDialog = null;
    }

    export(input) {
        this.input = input;

        if (input.format == window.Module.avabodh_lekh_ExportInput_ExportFormat.PDF) {
            this.exportPdf();
        } else if (input.format == window.Module.avabodh_lekh_ExportInput_ExportFormat.PNG) {
            this.exportImage('png');
        } else if (input.format == window.Module.avabodh_lekh_ExportInput_ExportFormat.JPG) {
            this.exportImage('jpg');
        } else if (input.format == window.Module.avabodh_lekh_ExportInput_ExportFormat.SVG) {
            this.exportSvg();
        } else if (input.format == window.Module.avabodh_lekh_ExportInput_ExportFormat.LEKH) {
            this.exportLekh();
        }
    }

    checkError(img) {
        if (img == null || img.data == "data:,") {
            const handleClose = () => viewPort.showDialog(null);
            var dialog = (
                <Dialog onClose={handleClose} aria-labelledby="simple-dialog-title" open={true}>
                    <DialogTitle id="simple-dialog-title">{errMsg}</DialogTitle>
                    <Button onClick={handleClose}>OK</Button>
                </Dialog>
            )
            viewPort.showDialog(dialog)
            return true;
        }
        return false;
    }

    exportPdf() {
        let pdfDoc = null;
        for (let i = 0; i < this.input.pages.size(); i++) {
            let img = this.getImage(i, "jpg", 0.5);
            if (this.checkError(img)) {
                return;
            }
            var orient = "landscape";
            if (img.height > img.width) {
                orient = "portrait";
            }
            if (pdfDoc == null) {
                pdfDoc = new jsPDF({
                    unit: 'pt',
                    orientation: orient,
                    format: [img.width, img.height]
                });
            } else {
                pdfDoc = pdfDoc.addPage([img.width, img.height], orient);
            }

            pdfDoc.addImage(img.data, 'JPEG', 0, 0, img.width, img.height)
        }
        pdfDoc.save(this.input.fileName);
    }

    exportImage(format) {
        for (let i = 0; i < this.input.pages.size(); i++) {
            let pageInfo = this.input.pages.get(i);
            let img = this.getImage(i, format, pageInfo.imageScale);
            if (this.checkError(img)) {
                return;
            }
            FileSaver.saveAs(img.data, pageInfo.fileName);
        }
    }

    exportLekh() {
        var blob = new Blob([this.input.content], { type: "text/plain;charset=utf-8" });
        FileSaver.saveAs(blob, this.input.fileName);
    }

    exportSvg() {
        for (let i = 0; i < this.input.pages.size(); i++) {
            let pageInfo = this.input.pages.get(i);
            var blob = new Blob([pageInfo.content], { type: "text/plain;charset=utf-8" });
            FileSaver.saveAs(blob, pageInfo.fileName);
        }
    }

    getImage(pageIdx, format, imageScale) {
        let pageInfo = this.input.pages.get(pageIdx);
        let scale = 1;
        if (imageScale > 0 && imageScale < 8) {
            scale = imageScale;
        }

        var cel = document.createElement("canvas");
        viewPort.fixCanvas(cel, pageInfo.width * scale, pageInfo.height * scale);
        var ctx = cel.getContext("2d");

        let lekhCore = window.Module.avabodh_lekh_app_LekhCore_instance();

        if (format == "jpg" || format == "jpeg") {
            ctx.fillStyle = '#fff';
            if (this.input.includeBackground) {
                let back = lekhCore.getPageBackground(pageInfo.pageIndex);
                if (back.type() == window.Module.avabodh_lekh_LekhBackground_Type.TypeColor) {
                    ctx.fillStyle = util.colorStr(back.getColor(), false);
                }
            }
            ctx.fillRect(0, 0, pageInfo.width * scale, pageInfo.height * scale);
        }

        let jsp = new JSDrawingPlatform();
        jsp.init();
        jsp.ctx = ctx;

        if (scale != 1) {
            ctx.scale(scale, scale);
        }

        lekhCore.drawForExporting(jsp.cppObj, pageInfo.pageIndex, this.input.includeBackground, pageInfo.translateX, pageInfo.translateY);

        jsp.cppObj.delete();

        return { data: cel.toDataURL("image/" + format), width: pageInfo.width * scale, height: pageInfo.height * scale }
    }
}

export default Exporter;