import { pt2d } from "./Frame";

const RANGE_DELTA = 0.5;

export function dist(p1, p2) {
    var dx, dy;
    dx = (p2.x - p1.x);
    dy = (p2.y - p1.y);
    return Math.sqrt(((dx * dx) + (dy * dy)));
}

export function dist2(p1, p2) {
    var dx, dy, dz;
    dx = (p2.x - p1.x);
    dy = (p2.y - p1.y);
    dz = (p2.z - p1.z);
    return Math.sqrt((((dx * dx) + (dy * dy)) + (dz * dz)));
}

export function inOrder(pts) {
    if (pts.length < 3) {
        return true;
    }

    function inord(p1, p2, p3) {
        var eq = new LineEq(p1, p2);
        eq = eq.perp(p2);
        return eq.crosses(p1, p3);
    }
    for (let i = 0; i < pts.length - 2; i++) {
        if (!inord(pts[i], pts[i + 1], pts[i + 2])) {
            return false;
        }
    }
    return true;
}

export function turnAngle(pts) {
    if (pts.length < 3) {
        return 0;
    }

    function ta(p1, p2, p3) {
        var dp = (p2.x - p1.x) * (p3.x - p2.x) + (p2.y - p1.y) * (p3.y - p2.y);
        var d1 = dist(p1, p2);
        var d2 = dist(p2, p3);
        var D = d1 * d2;
        // if D is very small
        if (Math.abs(D) < 0.0001) {
            return 0;
        }
        var theta = Math.acos(dp / D);
        var cp = crossProduct(p1, p2, p3);
        if (cp < 0) {
            return -theta;
        }
        return theta;
    }

    var turn = 0;
    for (let i = 0; i < pts.length - 2; i++) {
        turn += ta(pts[i], pts[i+1], pts[i+2]);
    }
    return turn;
}

export function crossProduct(p1, p2, p3) {
    var v1x = p2.x - p1.x;
    var v1y = p2.y - p1.y;

    var v2x = p3.x - p2.x;
    var v2y = p3.y - p2.y;

    return v1x * v2y - v2x * v1y;
}

export class LineEq {
    constructor(p1, p2) {
        this.A = (p2.y - p1.y);
        this.B = (p1.x - p2.x);
        this.C = ((p1.y * p2.x) - (p1.x * p2.y));
    }

    distFrom(pt) {
        var dist = (Math.abs((((this.A * pt.x) + (this.B * pt.y)) + this.C)) / Math.sqrt(((this.A * this.A) + (this.B * this.B))));
        return dist;
    }

    solve(eq2) {
        var D = ((this.A * eq2.B) - (eq2.A * this.B));
        var Dx = ((eq2.B * (- this.C)) - (this.B * (- eq2.C)));
        var Dy = ((this.A * (- eq2.C)) - (eq2.A * (- this.C)));
        if ((D === 0)) {
            return [false, null];
        }
        return [true, pt2d(Dx / D, Dy / D)];
    }

    angleWithHz() {
        return Math.atan2(this.A, (- this.B));
    }

    crosses(p1, p2) {
        var d1 = (((this.A * p1.x) + (this.B * p1.y)) + this.C);
        var d2 = (((this.A * p2.x) + (this.B * p2.y)) + this.C);
        return ((d1 * d2) <= 0);
    }

    perp(p) {
        var eq = new LineEq(pt2d(0, 0), pt2d(1, 1));
        eq.A = (- this.B);
        eq.B = this.A;
        eq.C = (((- eq.A) * p.x) - (eq.B * p.y));
        return eq;
    }

    pval(pt) {
        return pt.x * this.A + pt.y*this.B + this.C;
    }
}

export class LineSegment {
    constructor(p1, p2) {
        this.p1 = p1;
        this.p2 = p2;
    }

    length() {
        return dist(this.p1, this.p2);
    }

    eq() {
        return new LineEq(this.p1, this.p2);
    }

    contains(pt) {
        var d1 = dist(pt, this.p1);
        var d2 = dist(pt, this.p2);
        var len = this.length();
        var diff = Math.abs((len - (d1 + d2)));
        return (diff < RANGE_DELTA);
    }

    distFrom(point) {
        var _, d1, d2, eq, eq2, near;
        eq = new LineEq(this.p1, this.p2);
        eq2 = eq.perp(point);
        [_, near] = eq.solve(eq2);
        if (this.contains(near)) {
            return eq.distFrom(point);
        }
        d1 = dist(this.p1, point);
        d2 = dist(this.p2, point);
        return Math.min(d1, d2);
    }

    intersect(seg) {
        var at, eq1, eq2, got;
        eq1 = new LineEq(this.p1, this.p2);
        eq2 = new LineEq(seg.p1, seg.p2);
        [got, at] = eq1.solve(eq2);
        if ((((! got) || (! this.contains(at))) || (! seg.contains(at)))) {
            return [false, pt2d(0, 0)];
        }
        return [true, at];
    }

    intersectEq(eq) {
        var at, eq1, got;
        eq1 = new LineEq(this.p1, this.p2);
        [got, at] = eq1.solve(eq);
        if (((! got) || (! this.contains(at)))) {
            return [false, pt2d(0, 0)];
        }
        return [true, at];
    }
}

