class EventListener {
    constructor() {
        this.listeners = [];

        this.addListener = (cb) => {
            var idx = this.listeners.indexOf(cb);
            if (idx < 0) {
                this.listeners.push(cb);
            }
        };

        this.removeListener = (cb) => {
            var idx = this.listeners.indexOf(cb);
            if (idx >= 0) {
                this.listeners.splice(idx, 1);
            }
        };

        this.trigger = (data) => {
            for (let cb of this.listeners) {
                try {
                    cb(data);
                } catch (e) {
                    console.log(e);
                }
            }
        };
    }
}

export default EventListener;
