import atlassian from './atlassian';

const integ = { }
var integImpl = null;

integ.isIntegration = (params) => {
    if (params.atlassian == 'true') {
        integImpl = atlassian;
        integImpl.init(params);
        return true;
    }
    return false;
}

integ.getIntegMode = (params) => {
    if (params.atlassian == 'true') {
        return atlassian.getIntegMode(params);
    }
    return null;
}

integ.getUser = (cb) => {
    integImpl.getUser(cb);
}

integ.getDocInfo = (cb) => {
    integImpl.getDocInfo(cb);
}

integ.loadDoc = async () => {
    integImpl.loadDoc();
}

integ.handleClickOnViewer = (evt) => {
    integImpl.handleClickOnViewer();
}

integ.closeEditor = () => {
    integImpl.closeEditor();
}

integ.publishEditorChanges = async () => {
    await integImpl.publishEditorChanges();
}

integ.customizeSidebar = (sidebarMenu) => {
    return integImpl.customizeSidebar(sidebarMenu);
}

integ.customizeAppMenu = (appMenu) => {
    return integImpl.customizeAppMenu(appMenu);
}

integ.setShowDialogHandler = (showDialog, setAppState) => {
    integImpl.setShowDialogHandler(showDialog, setAppState);
}

integ.handleLogoClick = () => {
    integImpl.handleLogoClick();
}

integ.shouldShowQuickTip = () => {
    return integImpl.shouldShowQuickTip();
}

export default integ;