
import util from "./Util";

var touchInput = {}

var mod = null;
var hostEnv = null;

const eventPointFromTouch = (touch, state, ts) => {
    let pt = {
      x: touch.pageX,
      y: touch.pageY,
      pointerId: touch.identifier,
      state: state,
      pressure: 0,
      altitude:0,
      azimuth: 0,
      timestamp: ts,
      pointerType: mod.avabodh_lekh_event_PointerType.Finger,
      button: mod.avabodh_lekh_event_MouseButton.None,
      flag: mod.avabodh_lekh_event_EventPoint_Flag.None
    };
    return pt;
  }

const handleEvent = (ev, state) => {
    let pe = new mod.avabodh_lekh_event_PointerEvent();
    let list = new mod.List_avabodh_lekh_event_EventPoint();
    for (let touch of ev.changedTouches) {
        list.push_back(eventPointFromTouch(touch, state, ev.timeStamp));
    }
    pe.setPoints(list);
    util.setModifiersFromEvent(ev, pe);
    hostEnv.onPointerEvent(pe);
}

touchInput.attachEvents = function(canvasElement) {
    mod = window.Module;
    hostEnv = mod.avabodh_lekh_app_HostEnv_instance();

    canvasElement.addEventListener('touchstart', function(ev) {
      ev.preventDefault();
      handleEvent(ev, mod.avabodh_lekh_event_EventPointState.Down);
    });
    canvasElement.addEventListener('touchmove', function(ev) {
      ev.preventDefault();
      handleEvent(ev, mod.avabodh_lekh_event_EventPointState.Move);
    });
    canvasElement.addEventListener('touchend', function(ev) {
      ev.preventDefault();
      handleEvent(ev, mod.avabodh_lekh_event_EventPointState.Up);
    });
    canvasElement.addEventListener('touchleave', function(ev) {
      ev.preventDefault();
      hostEnv.resetPointerInputState();
    });
    canvasElement.addEventListener('touchcancel', function(ev) {
      ev.preventDefault();
      hostEnv.resetPointerInputState();
    });
}

export default touchInput;