import qs from 'qs';
import { getDocs, getDoc, deleteDoc, createNew, duplicateDoc } from "./ApiRequests";
import EventListener from './EventListener'

var nextToken = null;

var docsManager = {
  state: "loading",
  parent: null,
  search: null,
  order: null,
  orderBy: null,
  docs: [],
  path: [],
  mode: 'mydocs',
  event: new EventListener(),
};

docsManager.setPath = (path) => {
  var arr = path.split('/');
  if (arr.length % 2 != 0) {
    return;
  }

  docsManager.path = [];
  docsManager.parent = null;

  for (let i = 0; i < arr.length; i+= 2) {
    var obj = {docid: arr[i], name: arr[i + 1]}
    docsManager.path.push(obj);
  }
  if (docsManager.path.length > 0) {
    docsManager.parent = docsManager.path[docsManager.path.length - 1].docid;
  } else {
    docsManager.parent = null;
  }
}

docsManager.serializePath = () => {
  var path = '';
  for(let item of docsManager.path) {
    if (path != '') {
      path += '/';
    }
    path += item.docid + '/' + item.name;
  }
  return encodeURIComponent(path);
}

docsManager.setRoute = (route, qparam) => {
  if (route == '/docs/mydocs') {
    docsManager.mode = 'mydocs';
  } else if (route == '/docs/shared') {
    docsManager.mode = 'shared';
  } else if (route == '/docs/search') {
    docsManager.mode = 'search';
  } else {
    return;
  }
  docsManager.reset();
  var qpath = qs.parse(qparam, { ignoreQueryPrefix: true }).path;
  if (qpath) {
    docsManager.setPath(qpath);
  }
  var qsearch = qs.parse(qparam, { ignoreQueryPrefix: true }).search;
  if (qsearch) {
    docsManager.search = qsearch;
  }
}

docsManager.getRoute = () => {
  var qs = docsManager.serializePath();
  var path = '/docs/' + docsManager.mode;
  var arr = []
  if (qs != '') {
    arr.push('path=' + qs);
  } 
  if (docsManager.mode == 'search' && docsManager.search) {
    arr.push('search=' + encodeURIComponent(docsManager.search));
  }

  if (arr.length > 0) {
    path += '?' + arr.join('&');
  }
  return path;
}

docsManager.setSearchMode = (searchTerm) => {
    if (docsManager.mode == 'search') {
      docsManager.search = searchTerm;
      return;
    }
    docsManager.reset();
    docsManager.search = searchTerm;
    docsManager.mode = 'search';
}

docsManager.getRootName = () => {
    if (docsManager.mode == 'shared') {
        return 'Shared with me';
    } else if (docsManager.mode == 'search') {
        return 'Search';
    } else {
        return 'My Documents'
    }
}

docsManager.reset = () => {
  docsManager.state = "loading";
  docsManager.docs = [];
  docsManager.parent = null;
  docsManager.search = null;
  docsManager.order = null;
  docsManager.orderBy = null;
  docsManager.path = [];
  nextToken = null;
};

docsManager.pushParent = function (parent) {
  for (let item of docsManager.docs) {
    if (item.docid == parent) {
      docsManager.parent = parent;
      docsManager.path.push(item);
      docsManager.docs = [];
      docsManager.state = 'loading';
      break;
    }
  }
};

docsManager.popParentTo = function (parent) {
  docsManager.docs = [];
  docsManager.state = 'loading';

  if (!parent) {
    docsManager.path = [];
    docsManager.parent = null;
    return;
  }

  while (docsManager.path.length > 0) {
    let item = docsManager.path[docsManager.path.length - 1];
    if (item.docid == parent) {
        docsManager.parent = parent;
        break;
    }
    docsManager.path.pop();
  }
};

docsManager.deleteDoc = async docid => {
  await deleteDoc(docid);
  await docsManager.reload();
};

docsManager.canCreateNewDoc = () => {
  return docsManager.mode == "mydocs" || docsManager.path.length > 0;
}

docsManager.createDoc = async (parent, type, name) => {
  await createNew({ parent: parent, type: type, name: name });
  await docsManager.reload();
};

docsManager.reload = async () => {
  var qs = [];
  if (docsManager.parent) {
    qs.push("parent=" + docsManager.parent);
  }
  if (docsManager.mode == 'search' && docsManager.search && !docsManager.parent) {
    qs.push("search=" + docsManager.search);
  }
  if (docsManager.order) {
    qs.push("order=" + docsManager.order);
  }
  if (docsManager.orderBy) {
    qs.push("order_by=" + docsManager.orderBy);
  }
  if (docsManager.mode == 'shared' && !docsManager.parent) {
    qs.push("shared=true");
  }
  var qstr = "";
  if (qs.length > 0) {
    qstr = "?" + qs.join("&");
  }

  docsManager.state = "loading";
  docsManager.event.trigger();
  try {
    var resp = await getDocs(qstr);
    docsManager.docs = resp.data.docs;
    docsManager.state = "available";
  } catch (err) {
    docsManager.state = "error";
    console.log(err);
  }
  docsManager.event.trigger();
};

docsManager.getADoc = async (docid) => {
  var resp = await getDoc(docid);
  return resp.data;
}

docsManager.getFullPath = async (docid, root) => {
  var arr = [];
  var doc = null;
  try {
    doc = await docsManager.getADoc(docid);
    arr.push({name: doc.name, docid: doc.docid});
  } catch (err) {
    console.log(err);
  }

  while (doc.parent != root) {
    try {
      doc = await docsManager.getADoc(doc.parent);
      arr.unshift({name: doc.name, docid: doc.docid});
    } catch(err) {
      console.log(err);
      break;
    }
  }
  return arr;
}

docsManager.get = async () => {
  if (docsManager.state == "available") {
    return;
  }
  docsManager.reload();
};

docsManager.duplicate = async (docid) => {
  await duplicateDoc(docid);
}

docsManager.getChildFolders = async folderId => {
  var docs = await docsManager.getChildItems(folderId);
  var folders = [];
  for (let item of docs) {
    if (item.type == "folder") {
      folders.push(item);
    }
  }
  return folders;
};

docsManager.getChildItems = async folderId => {
  var qstr = "?parent=" + folderId;
  var docs = [];
  try {
    var resp = await getDocs(qstr);
    docs = resp.data.docs;
  } catch (err) {
    console.log(err);
  }
  return docs;
};

export default docsManager;
