import React, { useRef, useState } from "react";
// import { truncateString } from "../../utils/Helpers";
import { BsThreeDotsVertical } from "react-icons/bs";
import useClickOutside from "../../hooks/useOnClickOutside";
import { toast } from "react-toastify";
import FullLoader from "../Loaders/FullLoader";
import ShareModal from "./ShareModal";
import DetailsModal from "./DetailsModal";
import docsManager from "../../utils/DocsManager";
import RenameModal from "./RenameModal";
import MoveModal from "./MoveModal";
import { useHistory } from "react-router-dom";
import { IoFolderOutline, IoPeople } from "react-icons/io5";
import { Dialog } from "@material-ui/core";

const DocumentListItem = ({ m }) => {
  const [open, setOpen] = useState(false);
  const ref = useRef();
  useClickOutside(ref, () => setOpen(false));
  const [loading, setLoading] = useState(false);
  const [share, setShare] = useState(false);
  const [showDetails, setShowDetails] = useState(false);
  const [rename, setRename] = useState(false);
  const [move, setMove] = useState(false);
  const [deleting, setDeleting] = useState(false);
  const history = useHistory();

  // console.log(m);

  const handleOpen = () => {
    if (share || showDetails || rename || move) {
      return;
    }
    if (m.type === "board") {
      const win = window.open(`/?docid=${m.docid}`, "_blank");
      //win.focus();
    } else if (m.type === "folder") {
      docsManager.pushParent(m.docid);
      history.push(docsManager.getRoute());
      docsManager.reload();
    }
  };

  const duplicate = () => {
    docsManager
      .duplicate(m.docid)
      .then(() => {
        docsManager.reload();
      })
      .catch(err => {
        console.log(err);
      });
  };

  return (
    <div
      className="w-full h-40 lg:h-52 rounded-2xl shadow-md  flex flex-col justify-between"
      ref={ref}
    >
      {loading && <FullLoader />}
      {share && (
        <ShareModal state={share} close={() => setShare(false)} doc={m} />
      )}
      {showDetails && (
        <DetailsModal
          m={m}
          close={() => setShowDetails(false)}
          open={showDetails}
        />
      )}
      <RenameModal state={rename} close={() => setRename(false)} m={m} />
      {move && <MoveModal state={move} close={() => setMove(false)} m={m} />}

      <Dialog open={deleting}>
        <div className="w-64 lg:w-72 py-8 px-5 space-y-10">
          <p className="font-sans text-center text-lg">
            Are you sure you want to delete?
          </p>

          <div className="grid grid-cols-2 gap-5">
            <button
              className="button border border-lkhRed text-center py-3 bg-white "
              onClick={() => setDeleting(false)}
            >
              Cancel
            </button>

            <button
              onClick={() => {
                setDeleting(false);
                setLoading(true);
                docsManager
                  .deleteDoc(m.docid)
                  .then(res => {})
                  .catch(err => {
                    toast(err?.response?.data?.code);
                  })
                  .finally(() => {
                    setLoading(false);
                  });
              }}
              className="button bg-lkhRed text-white border-0"
            >
              Delete
            </button>
          </div>
        </div>
      </Dialog>

      <div
        className="h-3/4 relative transition-all hover:bg-gray-100 px-5 py-5  cursor-pointer"
        onClick={handleOpen}
      >
        {m.type === "board" && (
          <img
            src={require("../../assests/icons/Logo-grey.png").default}
            alt="Board Icon"
            className="w-5/6 h-5/6 object-scale-down"
          />
        )}

        {m.type === "folder" && (
          <IoFolderOutline className="w-5/6 h-5/6 scale-y-90 scale-x-90 object-scale-down"></IoFolderOutline>
        )}

        {m?.shared && m.in_my_scope && (
          <IoPeople className="absolute right-3 text-lg top-5" />
        )}
      </div>

      <div className="flex items-center justify-between h-1/4 px-4 py-3 bg-gray-50">
        <h1 className="m-0 text-sm break-all font-medium line-clamp-2">
          {m.name}
        </h1>

        <div className="relative">
          <BsThreeDotsVertical
            onClick={() => setOpen(!open)}
            className="cursor-pointer"
          />

          {open && (
            <div className="absolute top-0 right-3 py-3 px-7 shadow-md bg-white space-y-4 text-left z-20">
              <p
                className="text-sm cursor-pointer m-0 mb-2"
                onClick={() => {
                  handleOpen();
                }}
              >
                Open
              </p>

              <p
                className="text-sm cursor-pointer m-0 mb-2"
                onClick={() => setShowDetails(true)}
              >
                Details
              </p>

              {m.in_my_scope && (
                <>
                  <p
                    className="text-sm cursor-pointer"
                    onClick={() => setShare(true)}
                  >
                    Share
                  </p>

                  <p
                    className="text-sm cursor-pointer"
                    onClick={() => setRename(true)}
                  >
                    Rename
                  </p>

                  {m.type != "folder" && (
                    <p
                      className="text-sm cursor-pointer"
                      onClick={() => {
                        setOpen(!open);
                        duplicate();
                      }}
                    >
                      Duplicate
                    </p>
                  )}

                  {m.type == "board" && (
                    <p
                      className="text-sm cursor-pointer"
                      onClick={() => setMove(true)}
                    >
                      Move
                    </p>
                  )}

                  <p
                    className="text-sm cursor-pointer"
                    onClick={() => setDeleting(true)}
                  >
                    Delete
                  </p>
                </>
              )}
            </div>
          )}
        </div>
      </div>
    </div>
  );
};

export default DocumentListItem;
