import canvas from './Canvas'
import EventListener from '../utils/EventListener'

var modes = {
    selection: 1,
    move: 2,
    draw: 3,
    smartDraw: 4,
    text: 5,
    erase: 6,
    laser: 7
}

var menus = {
    selectionGroup: 0,
    selection: 1,
    move: 2,
    laser: 3,
    draw: 4,
    smartDraw: 5,
    text: 6,
    shapes: 7,
    erase: 8,
    eraseAll: 9,
    cameraInput: 10
}

var menuState = {
    menus: menus,
    modes: modes,
    popup: null,
    selected: menus.smartDraw,
    inputMode: modes.smartDraw,
    selectionGroupVal: modes.selection,
    cameraActive: false,
    event: new EventListener()
} 

menuState.handleMenuClick = function(menu) {
    if (menu == menus.eraseAll) {
        window.Module.eraseAll();
        canvas.draw();
        return;
    }
    
    if (menu == menus.selectionGroup) {
        if (menuState.popup == menus.selectionGroup) {
            menuState.popup = null;
        } else {
            menuState.popup = menus.selectionGroup;
        }
        menuState.selected = menus.selectionGroup;
        menuState.inputMode = menuState.selectionGroupVal;
    } else if (menu == menus.selection) {
        menuState.popup = null;
        menuState.selectionGroupVal = modes.selection;
        menuState.selected = menus.selectionGroup;
        menuState.inputMode = modes.selection;
    } else if (menu == menus.move) {
        menuState.popup = null;
        menuState.selectionGroupVal = modes.move;
        menuState.selected = menus.selectionGroup;
        menuState.inputMode = modes.move;
    } else if (menu == menus.laser) {
        menuState.popup = null;
        menuState.selected = menus.laser;
        menuState.inputMode = modes.laser;
    } else if (menu == menus.draw) {
        if (menuState.popup == menus.draw) {
            menuState.popup = null;
        } else {
            menuState.popup = menus.draw;
        }
        menuState.selected = menus.draw;
        menuState.inputMode = modes.draw;
    } else if (menu == menus.smartDraw) {
        if (menuState.popup == menus.smartDraw) {
            menuState.popup = null;
        } else {
            menuState.popup = menus.smartDraw;
        }
        menuState.selected = menus.smartDraw;
        menuState.inputMode = modes.smartDraw;
    } else if (menu == menus.text) {
        if (menuState.popup == menus.text) {
            menuState.popup = null;
        } else {
            menuState.popup = menus.text;
        }
        menuState.selected = menus.text;
        menuState.inputMode = modes.text;
    } else if (menu == menus.shapes) {
        if (menuState.popup == menus.shapes) {
            menuState.popup = null;
        } else {
            menuState.popup = menus.shapes;
        }
    } else if (menu == menus.erase) {
        menuState.popup = null;
        menuState.selected = menus.erase;
        menuState.inputMode = modes.erase;
    } else if (menu == menus.cameraInput) {
        menuState.cameraActive = !menuState.cameraActive
        menuState.popup = null;
    }

    handleMode();
    window.setTimeout(()=> {
        menuState.event.trigger();
    }, 1);
}

menuState.hasPopup = function() {
    return menuState.popup != null;
}

menuState.removePopup = function() {
    menuState.popup = null;
}

menuState.inDrawMode = function() {
    return menuState.inputMode == modes.draw || menuState.inputMode == modes.smartDraw;
}

function handleMode() {
    var mode = menuState.inputMode;
    var tpanel = document.getElementById("transparentPanel");

    if (mode == modes.selection) {
        window.Module.inputReceiver().setMode(window.Module.InputMode.SELECTION);
        tpanel.style.cursor = 'auto';
    } else if (mode == modes.move) {
        window.Module.inputReceiver().setMode(window.Module.InputMode.SELECTION);
        tpanel.style.cursor = 'all-scroll';
    } else if (mode == modes.laser) {
        window.Module.inputReceiver().setMode(window.Module.InputMode.POINTING);
        tpanel.style.cursor = 'url("red_circle.svg") 10 10, auto';
    } else if (mode == modes.draw) {
        window.Module.inputReceiver().setMode(window.Module.InputMode.DRAWING);
        window.Module.enableRecognition(false);
        tpanel.style.cursor = 'url("pen-solid.svg") 0 20, auto';
        //tpanel.style.cursor = 'auto';
    } else if (mode == modes.smartDraw) {
        window.Module.inputReceiver().setMode(window.Module.InputMode.DRAWING);
        window.Module.enableRecognition(true);
        //tpanel.style.cursor = 'url("pen-solid.svg") 0 20, auto';
        tpanel.style.cursor = 'auto';
    } else if (mode == modes.text) {
        window.Module.inputReceiver().setMode(window.Module.InputMode.SELECTION);
        tpanel.style.cursor = 'url("title_black.svg") 0 20, auto';
    } else if (mode == modes.erase) {
        window.Module.inputReceiver().setMode(window.Module.InputMode.ERASING);
        tpanel.style.cursor = 'url("eraser-solid.svg"), auto';
    }
}

export default menuState;
