import { AiOutlineClose } from "react-icons/ai";
import userInfo from "../../utils/UserInfo";
import ModalContainer from "../ModalContainer/ModalContainer";
import { IoFolderOutline } from "react-icons/io5";
import { useEffect, useRef, useState } from "react";
import useClickOutside from "../../hooks/useOnClickOutside";
import docsManager from "../../utils/DocsManager";
import MiniLoader from "../Loaders/MiniLoader";

const DetailsModal = ({ open, close, m }) => {
  const [data, setData] = useState([]);
  const [loading, setLoading] = useState(true);
  const inScope = m?.in_my_scope === true;
  const ref = useRef(null);

  // useClickOutside(open ? ref : null, close);

  // if (open)
  useEffect(() => {
    if (!inScope) {
      return;
    }
    docsManager
      .getFullPath(m?.docid, userInfo.data.userid)
      .then(res => setData(res))
      .catch(err => console.log(err.response))
      .finally(() => {
        setLoading(false);
      });
    // eslint-disable-next-line react-hooks/exhaustive-deps
  }, []);

  useEffect(() => {
    if (inScope) {
      return;
    }

    docsManager
      .getADoc(m?.docid)
      .then(res => setData(res))
      .catch(err => console.log(err.response))
      .finally(() => {
        setLoading(false);
      });
    // eslint-disable-next-line react-hooks/exhaustive-deps
  }, []);

  // const handleKeyDown = event => {
  //   if (event.key.toLowerCase()) {
  //     close();
  //   }
  // };

  // useEffect(() => {
  //   document.addEventListener("keydown", handleKeyDown);

  //   return () => {
  //     document.removeEventListener("keydown", handleKeyDown);
  //   };
  //   // eslint-disable-next-line react-hooks/exhaustive-deps
  // }, []);

  return (
    <ModalContainer>
      <div className="h-full lg:w-80 w-64 py-7 px-2 bg-white fixed right-0 top-0 z-20 ">
        <div ref={open ? ref : null}>
          <div className="">
            <AiOutlineClose
              className="text-3xl cursor-pointer"
              onClick={() => close()}
            />
          </div>

          <h1 className="text-center text-xl">{m.name}</h1>
          {m.type === "board" && (
            <img
              src={require("../../assests/icons/Logo-grey.png").default}
              alt="Board Icon"
              className="w-full h-20 lg:h-40 object-scale-down my-7"
            />
          )}

          {m.type === "folder" && (
            <IoFolderOutline className="w-full h-20 lg:h-40  object-scale-down my-7"></IoFolderOutline>
          )}

          <div className="w-full space-y-3">
            <div className="details-grid">
              <h2 className="m-0 font-medium text-base">Type:</h2>

              <p className="capitalize m-0">{m.type}</p>
            </div>

            <div className="details-grid">
              <h2 className="m-0 font-medium text-base">Created at:</h2>

              <p className="capitalize m-0">
                {new Date(m.created_at).toDateString()}
              </p>
            </div>

            <div className="details-grid">
              <h2 className="m-0 font-medium text-base">Updated at:</h2>

              <p className="capitalize m-0">
                {new Date(m.updated_at).toDateString()}
              </p>
            </div>
            {m.in_my_scope === true && !loading && (
              <div className="details-grid">
                <h2 className="m-0 font-medium text-base">Location:</h2>

                <p className="m-0 break-all">
                  {userInfo?.data?.userid === m.parent ? (
                    "My Documents"
                  ) : (
                    <>
                      <span>My Documents</span>{" "}
                      {data?.map(m => (
                        <span key={m?.name}>/{m?.name}</span>
                      ))}
                    </>
                  )}
                </p>
              </div>
            )}

            {m.in_my_scope === false && !loading && (
              <div className="details-grid">
                <h2 className="m-0 font-medium text-base">Shared by:</h2>
                <p className="m-0 break-all">{data?.owner}</p>
              </div>
            )}

            {loading && <MiniLoader />}
          </div>
        </div>
      </div>
    </ModalContainer>
  );
};

export default DetailsModal;
