import axios from "axios";
import userInfo from "./UserInfo";

var httpUtil = { }

const b64toBlob = (base64, type = 'application/octet-stream') => 
  fetch(`data:${type};base64,${base64}`).then(res => res.blob());

const blobToBase64 = async (blob) => {
    return new Promise(resole => {
        var reader = new FileReader();
        reader.onloadend = function() {
            resole(reader.result);
        }
        reader.readAsDataURL(blob);
    });
}

const blobToStr = async (blob) => {
    return new Promise(resole => {
        var reader = new FileReader();
        reader.onloadend = function() {
            resole(reader.result);
        }
        reader.readAsText(blob); 
    });
}

const toBlob = (data) => {
    if (data instanceof Blob) {
        return data;
    }

    if (typeof(data) == 'string') {
        return new Blob([data]);
    }
    
    if (typeof(data) == 'object') {
        return new Blob([JSON.stringify(data)]);
    }
    return new Blob(['']);
}

const toStr = async (data) => {
    if (typeof(data) == 'string') {
        return data;
    }
    if (data instanceof Blob) {
        return await blobToStr(data);
    }
    if (typeof(data) == 'object') {
        return JSON.stringify(data);
    }
    return '';
}

const shouldIncludeHeader = (url) => {
    if (url.startsWith('https://rajeevku02.github.io')) {
        return false;
    }
    return true;
}

httpUtil.requestHttp = async (request) => { 
    
    let headers = { }
    let keys = request.headers.keys();
    for (let i = 0; i < keys.size(); i++) {
        let hk = keys.get(i);
        let hv = request.headers.get(hk);
        headers[hk] = hv;
    }
    headers['x-auth-token'] = userInfo.token;
    const config = {
        url: request.url,
        method: request.method,
        timeout: request.timeoutSec* 1000,
        responseType: 'blob'
    };

    if (shouldIncludeHeader(request.url)) {
        config['headers'] = headers;
    } else {
        config.withCredentials = false;
    }

    if (!request.encodeResponseBase64) {
        config.responseType = 'text';
    }

    if (request.multiPart.size() > 0) {
        const dataArray = new FormData();
        for (let idx = 0; idx < request.multiPart.size(); idx++) {
            let mp = request.multiPart.get(idx);
            if (mp.decodeValueBase64) {
                var mime = mp.mime? mp.mime: 'application/octet-stream';
                var blob = await b64toBlob(mp.value, mime);
                dataArray.append(mp.name, blob);
            } else {
                if (mp.mime) {
                    dataArray.append(mp.name, new Blob([mp.value], {type: mp.mime}));
                } else {
                    dataArray.append(mp.name, mp.value);
                }
            }
        }
        config.data = dataArray;
    } else if (request.method.toLowerCase() != 'get' && !!request.body) {
        if (request.decodeRequestBase64) {
            config.data = await b64toBlob(request.body);
        } else {
            config.data = request.body;
        }
    }

    var httpResp = {
        error: true,
        status: 0,
        body: '',
        headers: new window.Module.Map_std_string_std_string(),
    }
    var resp = null;
    try {
        resp = await axios(config);
    } catch (e) {
        if (!!e && !!e.response && !!e.response.status) {
            resp = e.response;
        }
    }

    if (resp != null) {
        httpResp.error = false;
        httpResp.status = resp.status;

        for (let item in resp.headers) {
            if (typeof(resp.headers[item]) == 'string') {
                httpResp.headers.set(item, resp.headers[item]);
            }
        }

        if (request.encodeResponseBase64) {
            let body = await blobToBase64(toBlob(resp.data));
            if (body.startsWith('data:') && body.indexOf('base64,') >= 0) {
                body = body.substring(body.indexOf('base64,') + 7);
            }
            httpResp.body = body;
        } else {
            let body = await toStr(resp.data)
            httpResp.body = body;
        }
    }
    
    return httpResp;
}

export default httpUtil;