import { Dialog } from "@material-ui/core";
import React, { useState, useEffect } from "react";
import { Button } from '@material-ui/core';
import { AiOutlineClose } from "react-icons/ai";
import canvas from "../../common/Canvas";

const LekhImporter = ({showDialog, onImport, onCancel}) => {
  const [file, setFile] = useState(null);
  const [errMsg, setErrMsg] = useState('');

  var btnText = 'Import';
  var btnEnabled = file != null;
  
  const onChange = (e) => {
    var changedFile = e.target.files[0];
    setFile(changedFile);
    setErrMsg('')
  }

  const handleSubmit = e => {
    e.preventDefault();

    const reader = new FileReader();
    reader.addEventListener("load", () => {
        var out = window.Module.importLekh(reader.result);
        if (out) {
            setErrMsg('Unable to import. Please check that you are importing a valid Lekh file.');
        } else {
            showDialog(null);
            canvas.draw();
            onImport();
        }
    }, false);

    if (file) {
        reader.readAsText(file);
    }
  };

  const close = ()=> {
    showDialog(null);
    onCancel();
  }

  return (
    <Dialog open={true} className="px-5">
        <div className="flex justify-between items-center my-2 px-2">
            <div />
            <h4 className="text-center m-0">Import Lekh File</h4>
            <AiOutlineClose className="text-xl cursor-pointer" onClick={close} />
        </div>
        <form
            action=""
            className=""
            onSubmit={handleSubmit}
        >
            <input type='file' accept=".lekh" className="mx-3 mt-4" onChange={onChange}/>
            {errMsg && 
              <div className="mt-2 mx-3 text-red-600 text-sm">
                <label>{errMsg}</label>
              </div>
            }
            <div className="flex mb-4 mt-5 mx-3">
                <div className="mr-4"><Button variant="outlined" onClick={close}>Cancel</Button></div>
                <Button variant="outlined" disabled={!btnEnabled} onClick={handleSubmit}>{btnText}</Button>
            </div>
        </form>
    </Dialog>
  );
};

export default LekhImporter;
