import integ from "../integ/integ";

const uiConfig = {
}

var integMode = {
    useLekhBackend: false,
    editor: {
        showOptions: false,
        fileNameEditable: false,
        readOnly: false
    },
    viewer: {
        full: false,
        makeLink: false,
        showEditOption: false,
        showOptions: false
    },
};

var offline = false;

uiConfig.initForIntegMode = (params) => {
    integMode = integ.getIntegMode(params);
}

uiConfig.initForLekhBackendMode = () => {
    integMode = null;
    offline = false;
}

uiConfig.initForOfflineMode = () => {
    integMode = null;
    offline = true;
}

uiConfig.isIntegMode = () => {
    return integMode != null;
}

uiConfig.isIntegViewer = () => {
    return integMode != null && integMode.editor == null;
}

uiConfig.isViewerLink = () => {
    if (integ != null && integMode != null && integMode.viewer != null) {
        return integMode.viewer.makeLink;
    }
    return false;
}

uiConfig.isFullViewer = () => {
    if (integ != null && integMode != null && integMode.viewer != null) {
        return integMode.viewer.full;
    }
    return false;
}

uiConfig.readOnly = () => { 
    if (integMode != null) {
        if (integMode.editor != null) {
            return integMode.editor.readOnly;
        }
        return true;
    }
    return window.wsmanager && window.wsmanager.readOnly();
}

uiConfig.offline = () => {
    return offline;
}

uiConfig.shouldLoadShapes = () => {
    if (integMode != null) {
        return integMode.editor != null;
    }
    return true;
}

uiConfig.shouldAttachMouseEventHandler = () => {
    if (integMode != null) {
        if (integMode.viewer != null) {
            return integMode.viewer.full;
        } else {
            return true;
        }
    }
    return true;
}

uiConfig.shouldAttachKeyboardEventHandler = () => {
    if (integMode != null) {
        return integMode.editor != null;
    }
    return true;
}


uiConfig.shouldShowFooter = () => {
    if (integMode != null) {
        if (integMode.viewer != null) {
            return integMode.viewer.full;
        }
        return true;
    }
    return true;
}

uiConfig.isZoomOnlyFooter = () => {
    if (integMode != null) {
        if (integMode.viewer != null) {
            return integMode.viewer.full;
        }
        return false;
    }
    return false;
}

uiConfig.shouldShowPagesInFooter = () => {
    return integMode == null;
}

uiConfig.shouldShowUndoRedoInFooter = () => {
    if (integMode != null) {
        if (integMode.viewer != null) {
            return integMode.viewer.full;
        }
        return false;
    }
    return true;
}

uiConfig.isUsingLekhBackend = () => {
    if (integMode != null) {
        return integMode.useLekhBackend;
    }
    return true;
}

uiConfig.shouldShowPublishCancelButton = () => {
    return integMode != null && integMode.editor != null
}

uiConfig.shouldShowQuickTip = () => {
    if (integMode != null && integMode.editor != null) {
        return integ.shouldShowQuickTip();
    }
    return true;
}

export default uiConfig;