
import React, { useState, useEffect } from 'react';
import { Button } from '@material-ui/core';
import { GrNext } from "react-icons/gr";
import userInfo from '../../utils/UserInfo';
import { dateStr } from './DocsLogUtil';

import { getDocsLogSessions } from '../../utils/ApiRequests';

const SessionList = props => {
    let date = new Date();
    let defKey = date.getFullYear() + "-" + date.getMonth();
    const [key, setKey] = useState(defKey);
    const [sessions, setSessions] = useState(null);
    const [token, setToken] = useState(null);
    const [nextToken, setNextToken] = useState(null);
    const [loading, setLoading] = useState(false);

    const handleKeyChange = (e) => {
        setNextToken(null);
        setKey(e.target.value);
    }

    const fetchData = () => {
        setLoading(true);
        setSessions(null);
        var elm = document.getElementById('key_text');
        var parts = elm.value.split("-");
        var qstr = '';
        if (parts.length == 2) {
            qstr = 'year=' + parts[0] + '&month=' + parts[1];
        } else {
            qstr = 'docid=' + elm.value;
        }
        if (nextToken) {
            qstr += '&next_token=' + encodeURIComponent(nextToken);
        }
        getDocsLogSessions(qstr).then(resp => {
            setLoading(false);
            if (resp.data) {
                setSessions(resp.data.sessions);
                console.log(resp.data)
                setNextToken(resp.data.next_token);
            }
        });
    }

    const onUserInfoChange = () => {
        setToken(userInfo.token);
    }

    useEffect(() => {
        userInfo.event.addListener(onUserInfoChange);
        userInfo.get();
        return () => {
          userInfo.event.removeListener(onUserInfoChange);
        };
    }, []);

    var items = [];
    if (sessions) {
        let count = 0;
        for (let sess of sessions) {
            let ds = dateStr(sess.timestamp);
            items.push(<div className='mt-4 ml-4' key={ds}>
                <span>[{count}]</span><span> </span>
                <span>{sess.email}</span><span> </span>
                <span> [Updates: {sess.num_updates}] </span><span> </span>
                <span><a target="_blank" className="text-blue-700" href={"/#/docslog/sessionviewer?sessionid=" + sess.id}>{'id:' + sess.id}</a></span><span> </span>
                <span>{'docid:' + sess.docid}</span><span> </span>
                <span className="text-purple-500">{ds}</span><span> </span>
                </div>)
            count++;
        }
    }
    return (<>
    <div className="flex justify-around mt-4">
        <input type="text" id="key_text" value={key} onChange={handleKeyChange} className="bg-gray-50 border border-gray-300 text-gray-900 text-sm rounded-lg focus:ring-blue-500 focus:border-blue-500 block p-2.5"></input>
        <div className="mt-2">1 - 100</div>
        <Button onClick={fetchData} disabled={token == null}><GrNext/></Button>
    </div>
    <hr/>
    {loading &&<div>Loading..</div>}
    {!loading && <div style={{overflow: 'auto', height: 'calc(100% - 110px)'}}>{items}</div>}
    </>);
}

export default SessionList;