var speechCommand = { }

const cmdMap = {
    "start a flowchart": [2, 20],
    "start flowchart": [2, 20],
    "start": [2, 20],
    "end": [2, 20],
    "done": [2, 20],
    "end the flowchart": [2, 20],
    "add input": [2, 4],
    "input": [2, 4],
    "add decision": [2, 1],
    "decision": [2, 1],
    "add process": [2, 0],
    "process": [2, 0],
}

const handleText = (text, addShape) => {
    console.log(text);
    var shape = cmdMap[text];
    if (shape) {
        addShape(shape[0], shape[1]);
    }
}

speechCommand.start = (addShape) => {
    const SpeechRecognition = window.SpeechRecognition || window.webkitSpeechRecognition    
    var recognition = new SpeechRecognition();
    //var grammar = '#JSGF V1.0; grammar colors; public <color> = aqua | azure | beige | bisque | black | blue | brown | chocolate | coral | crimson | cyan | fuchsia | ghostwhite | gold | goldenrod | gray | green | indigo | ivory | khaki | lavender | lime | linen | magenta | maroon | moccasin | navy | olive | orange | orchid | peru | pink | plum | purple | red | salmon | sienna | silver | snow | tan | teal | thistle | tomato | turquoise | violet | white | yellow ;'
    //var speechRecognitionList = new SpeechGrammarList();
    //speechRecognitionList.addFromString(grammar, 1);
    //recognition.grammars = speechRecognitionList;
    recognition.continuous = false;
    recognition.lang = 'en-US';
    recognition.interimResults = false;
    recognition.maxAlternatives = 1;

    recognition.onresult = function(event) {
        var text = event.results[0][0].transcript;
        handleText(text, addShape);
    }

    recognition.start();
    console.log('Ready to receive speech command.');
}

export default speechCommand;