import {Drag1Gesture, Drag2Gesture, ThumbGesture, PinchGesture, Gesture} from './Gestures'
import { mkpt } from './Frame';
import { getFeatures } from './LandmarksFeatures';
import { dist, LineSegment, inOrder } from './Geometry';
//import command from '../common/Command'

var drag1Active = false;
var drag2Active = false;

const undo = () => {
    window.Module.avabodh_lekh_app_Command_instance().undo();
}

class RepeatedGesture {
    constructor(type, noop, gap, times, handler) {
        this.type = type;
        this.noop = noop;
        this.gap = gap;
        this.lastGesture = null;
        this.lastAt = 0;
        this.count = 0;
        this.handler = handler;
        this.times = times;
    }

    check(gesture) {
        if (this.lastGesture == null) {
            this.lastGesture = gesture;
            return;
        }

        if (gesture == this.noop) {
            if (this.lastGesture == this.type && !this.lastGesture.dragger.moving) {
                var now = Date.now();
                if (now - this.lastAt > this.gap) {
                    this.count = 1;
                } else {
                    this.count++;
                }
                this.lastAt = now;
                if (this.count == this.times) {
                    this.count = 0;
                    this.lastAt = 0;
                    this.handler();
                }
            }

        } else {
            if (gesture != this.type) {
                this.count = 0;
                this.lastAt = 0;
            }
        }

        this.lastGesture = gesture;
    }
}

function isDrag2(pts, features) {
    if (features.backSide) {
        return false;
    }

    if (features.indexBent || !features.middleBent ||
        !features.ringBent || !features.indexInOrder) {
        return false;
    }
    //const THREASHOLD_1 = 0.32;
    //const THREASHOLD_2 = 0.42;
    //var d1 = dist(pts[0], pts[5]);

    const THREASHOLD_1 = 1.1;
    const THREASHOLD_2 = 1.2;
    var d1 = dist(pts[5], pts[9]);

    if (dist(pts[10], pts[11]) < 0.0001) {
        return false;
    }
    var seg = new LineSegment(pts[10], pts[11]);
    var d2 = seg.distFrom(pts[4]);

    var target1 = d1 * THREASHOLD_1;
    var target2 = d1 * THREASHOLD_2;

    if (!drag2Active) {
        if (d2 < target1) {
            drag2Active = true;
        }
    } else {
        if (d2 > target2) {
            drag2Active = false;
        }
    }
    return drag2Active;
}

function isDrag1(pts, features) {
    if (features.middleBent || features.ringBent || features.backSide) {
        return false;
    }

    const THRESHOLD_1 = 0.3;
    const THRESHOLD_2 = 0.4;
    const THREASHOLD_3 = 0.5;

    var d1 = dist(pts[0], pts[5]);

    var dPinch = d1 * THREASHOLD_3;
    var pinchLike = true;
    for (let id of [8, 12, 16, 20]) {
        let tmp = dist(pts[id], pts[4]);
        if (tmp > dPinch) {
            pinchLike = false;
            break;
        }
    }
    if (pinchLike) {
        return false;
    }

    var d2 = dist(pts[4], pts[8]);
    var factor = 100.0;
    if (d1 !== 0) {
        factor = d2 / d1;
    }
    if (drag1Active) {
        if (factor > THRESHOLD_2) {
            drag1Active = false;
        }
    } else {
        if (factor < THRESHOLD_1) {
            drag1Active = true;
        }
    }
    return drag1Active;
}

function isThumb(pts, features) {
    const FACTOR = 0.3;
    var d1 = dist(pts[0], pts[5]) * FACTOR;
    var d2 = dist(pts[4], pts[8]);
    if (d2 < d1) {
        return false;
    }

    if (features.indexBent && features.middleBent && features.ringBent) {
        if (features.indexCurved || features.middleCurved || features.ringCurved) {
            return true;
        }
    }
    if (pts[4].y < pts[5].y && pts[4].y < pts[9].y && pts[4].y < pts[13].y) {
        if (inOrder([pts[5], pts[8], pts[6]]) &&
            inOrder([pts[9], pts[12], pts[10]]) &&
            inOrder([pts[13], pts[16], pts[14]])) {
            return true;
        }
    }
    return false;
}

function isPinch(pts, features) {
    const FACTOR = 0.4;
    var d1 = dist(pts[0], pts[5]) * FACTOR;
    for (let id of [8, 12, 16, 20]) {
        let d2 = dist(pts[id], pts[4]);
        if (d2 > d1) {
            return false
        } 

    }
    return true;
}

export class GestureRecognizer {
    constructor(model) {
        this.model = model;
        this.drag1Gesture = new Drag1Gesture();
        this.drag2Gesture = new Drag2Gesture();
        this.thumbGesture = new ThumbGesture();
        this.pinchGesture = new PinchGesture();
        this.noopGesture = new Gesture("noop");
        this.undoGesture = new RepeatedGesture(this.drag2Gesture, this.noopGesture, 500, 3, undo);
    }

    handleRepeated(gesture) {
        this.undoGesture.check(gesture);
    }

    get(landmarks, hand) {
        var gesture = this.getGesture(landmarks, hand);
        this.handleRepeated(gesture);
        return gesture;
    }

    getGesture(landmarks, hand) {
        var pts = [];
        for (let item of landmarks) {
            pts.push(mkpt(item))
        }
    
        var features = getFeatures(pts, hand);
        //features.print()

        if (isPinch(pts, features)) {
            return this.pinchGesture;
        }

        if (isThumb(pts, features)) {
            return this.thumbGesture;
        }

        if (features.backSide) {
            if (drag1Active) {
                return this.drag1Gesture;
            }
            if (drag2Active) {
                return this.drag2Gesture;
            }

            return this.noopGesture;
        }

        if (isDrag2(pts, features)) {
            drag1Active = false;
            return this.drag2Gesture;
        }

        if (isDrag1(pts, features)) {
            drag2Active = false;
            return this.drag1Gesture;
        }

        return this.noopGesture;
    }
}

