export class JSDrawingPlatform {
    constructor() {
        this.ctx = null;
        this.style = null;
        this.path = null;
        this.coreUtil = null;
    }

    init() {
        this.cppObj = new window.Module.DrawingPlatformImpl(this);
        this.coreUtil = window.Module.avabodh_lekh_app_CoreUtil_instance();
    }

    setStrokeStyle() {
        let cl = this.style.effectiveStrokePaint().solidColor;
        this.ctx.strokeStyle = util.colorStr(cl, this.style.effectiveDisabled());
        let mod = window.Module;
        if (this.style.isJoinStyleDefined()) {
            let js = this.style.effectiveJoinStyle();
            if (js == mod.avabodh_lekh_Style_JoinStyle.JoinStyleBevelJoin) {
                this.ctx.lineJoin = 'bevel';
            } else if (js == mod.avabodh_lekh_Style_JoinStyle.JoinStyleMiterJoin) {
                this.ctx.lineJoin = 'miter';
            } else if (js == mod.avabodh_lekh_Style_JoinStyle.JoinStyleRoundJoin) {
                this.ctx.lineJoin = 'round';
            }
        } else {
            this.ctx.lineJoin = 'round';
        }

        let dashArray = this.style.effectiveDashArray();
        
        if (this.style.isCapStyleDefined()) {
            let lc = this.style.effectiveCapStyle();
            if (lc == mod.avabodh_lekh_Style_CapStyle.CapStyleButtCap) {
                this.ctx.lineCap = 'butt';
            } else if (lc == mod.avabodh_lekh_Style_CapStyle.CapStyleSquareCap) {
                this.ctx.lineCap = 'square';
            } else if (lc == mod.avabodh_lekh_Style_CapStyle.CapStyleRoundCap) {
                this.ctx.lineCap = 'round';
            }
        } else {
            if (dashArray.size() > 0) {
                this.ctx.lineCap = 'butt';
            } else {
                this.ctx.lineCap = 'round';
            }
        }

        let dc = dashArray.size();
        if (dc > 0) {
            let arr = [];
            for (let i = 0; i < dc; i++) {
                arr.push(dashArray.get(i));
            }
            this.ctx.setLineDash(arr);
            this.ctx.lineDashOffset = 0;
        }
    }

    setBackground(background) {

    }

    startDrawing(point, style) {
        this.style = style.get();
        this.path = new Path2D();
        this.path.moveTo(point.x, point.y);
    }

    endDrawing() {
        this.ctx.save();
        
        if (this.style.fill()) {
            this.ctx.fillStyle = util.colorStr(this.style.effectiveFillPaint().solidColor, this.style.effectiveDisabled());
            this.ctx.fill(this.path);
        }
        
        let width = this.style.effectiveStrokeWidth();
        if (width < 0) { width = 0; }
        let shadow = this.style.effectiveShadow();
        let hasShadow = shadow.radius > 0;
        if ((width > 0 || hasShadow) && this.style.effectiveStrokePaint().type != window.Module.avabodh_lekh_Style_Paint_Type.PaintTypeNoPaint) {
            this.setStrokeStyle();
            if (width == 0) {
                // This can happen if there is shadow but no border.
                // sice the zero value is ignored (old value is kept) so
                // set it to small value so that small values is set.
                width = 0.001;
                // make the border color same as shadow color
                this.ctx.strokeStyle = util.colorStr(shadow.color, false);

            }
            this.ctx.lineWidth = width;

            if (hasShadow) {
                this.ctx.shadowOffsetX = shadow.dx;
                this.ctx.shadowOffsetY = shadow.dy;
                this.ctx.shadowBlur = shadow.radius;
                this.ctx.shadowColor = util.colorStr(shadow.color, false);
            }

            this.ctx.stroke(this.path);    
        }

        this.ctx.restore();
    }

    closePath() {
        this.path.closePath();
    }

    addLineTo(point) {
        this.path.lineTo(point.x, point.y);
    }

    moveTo(point) {
        this.path.moveTo(point.x, point.y);
    }

    addCircle(center, radius) {
        this.path = new Path2D();
        if (radius < 0) { radius = 0; }
        this.path.ellipse(center.x, center.y, radius, radius, 0, 0, 6.3);
    }

    addArc(center, radius, startAngle, endAngle) {
        this.path = new Path2D();
        this.path.arc(center.x, center.y, radius, startAngle, endAngle, false);
    }

    addEllipse(center, width, height, angle) {
        if (width < 0) {
            width = 0;
          }
          if (height < 0) {
            height = 0;
          }
          this.path = new Path2D();
          this.path.ellipse(center.x, center.y, width / 2, height / 2, angle, 0, 6.3);
    }

    addCurve(curve) {
        if (curve.numPoints == 3) {
            this.path.quadraticCurveTo(curve.point2.x, curve.point2.y, curve.point3.x, curve.point3.y);
          } else {
            this.path.bezierCurveTo(curve.point2.x, curve.point2.y, curve.point3.x, curve.point3.y, curve.point4.x, curve.point4.y);
          }
    }

    addRoundedRect(rect, angle) {

    }

    drawText(str, floating, at, rect, angle, style) {

    }

    drawTextWithParam(param, splits, style) {
        if (splits.size() == 0) {
            return;
        }

        style = style.get();
        this.ctx.save();

        let cx = param.inRect.x + param.inRect.width / 2;
        let cy = param.inRect.y + param.inRect.height / 2;

        let ahz = window.Module.avabodh_lekh_Style_TextAlignHorizontal;

        let atx = cx;
        let align = 'center'
        if (style.effectiveTextAlignHorizontal() == ahz.TextAlignHorizontalLeft) {
            atx = param.inRect.x;
            align = 'left';
        } else if (style.effectiveTextAlignHorizontal() == ahz.TextAlignHorizontalRight) {
            atx = param.inRect.x + param.inRect.width;
            align = 'right';
        }

        let lineHeight = style.effectiveFontSize();
        let aty = cy - lineHeight * splits.size() / 2;
        this.ctx.textBaseline = 'top';
        this.ctx.textAlign = align;
        this.ctx.font = util.fontStr(style);
        let colorStr = util.colorStr(style.effectiveTextFill().solidColor, style.effectiveDisabled());
        this.ctx.fillStyle = colorStr;
        this.ctx.strokeStyle = colorStr;
        
        if (param.angle != 0) {
            this.ctx.translate(cx, cy);
            this.ctx.rotate(param.angle);
            atx -= cx;
            aty -= cy;
        }

        let underline = this.coreUtil.isUnderlineText(style);
        let strikeThrough = this.coreUtil.isStrikeThroughText(style);

        this.ctx.lineWidth = lineHeight / 15;
        let delta = lineHeight * 0.92;

        for (let i = 0; i < splits.size(); i++) {
            this.ctx.fillText(splits.get(i).text, atx, aty);

            if (underline || strikeThrough) {
                let tw = splits.get(i).width;
                let x1 = atx;
                let x2;
                if (align == 'left') {
                    x2 = x1 + tw;
                } else if (align == 'right') {
                    x2 = x1 - tw;
                } else {
                    x2 = x1 + tw / 2;
                    x1 = x1 - tw / 2;
                }

                let path = new Path2D();
                if (strikeThrough) {
                    path.moveTo(x1, aty + lineHeight / 2);
                    path.lineTo(x2, aty + lineHeight / 2);
                }

                if (underline) {
                    path.moveTo(x1, aty + delta);
                    path.lineTo(x2, aty + delta);
                }
                this.ctx.stroke(path);
            }

            aty += lineHeight;
        }

        this.ctx.restore();
    }

    drawImage(center, size, angle, img, style) {
        if (img == null) {
          return;
        }
        this.ctx.save();
        this.ctx.translate(center.x, center.y);
        if (angle != 0) {
            this.ctx.rotate(angle);
        }
        this.ctx.drawImage(img, -size.width / 2, -size.height / 2, size.width, size.height);
        this.ctx.restore();
    }

    saveContext() {
        this.ctx.save();
    }

    restoreContext() {
        this.ctx.restore();
    }

    translate(x, y) {
        this.ctx.translate(x, y);
    }

    clip(pathSteps) {
        if (pathSteps.size() == 0) {
            return;
        }
        let cp = new Path2D();
        let tp = window.Module.avabodh_geometry_PathStep_Type;

        for (let i = 0; i < pathSteps.size(); i++) {
            let p = pathSteps.get(i);
            if (p.type == tp.Move) {
                cp.moveTo(p.p1.x, p.p1.y);
            } else if (p.type == tp.Line) {
                cp.lineTo(p.p1.x, p.p1.y);
            } else if (p.type == tp.Quad) {
                cp.quadraticCurveTo(p.p1.x, p.p1.y, p.p2.x, p.p2.y);
            } else if (p.type == tp.Cubic) {
                cp.quadraticCurveTo(p.p1.x, p.p1.y, p.p2.x, p.p2.y, p.p3.x, p.p3.y);
            } else if (p.type == tp.Close) {
                cp.closePath();
            } else if (p.type == tp.Arc) {
                // TODO:
            }
        }
        this.ctx.clip(cp);
    }
}

const platform = new JSPlatform();
export default platform;