import React, { useRef, useState } from "react";
import { AiOutlineSearch } from "react-icons/ai";
import { Link, useHistory } from "react-router-dom";
import useClickOutside from "../../hooks/useOnClickOutside";
import { GiHamburgerMenu } from "react-icons/gi";
import ModalContainer from "../ModalContainer/ModalContainer";
import DashSidebar from "./DashSidebar";
import userInfo from "../../utils/UserInfo";
import docsManager from "../../utils/DocsManager";

const MobileMenu = ({ close }) => {
  return (
    <ModalContainer>
      <DashSidebar close={close} />
    </ModalContainer>
  );
};

const DashboardTop = () => {
  const settingsRef = useRef();
  const [openSettings, setOpenSettings] = useState(false);
  const [openMenu, setOpenMenu] = useState(false);
  const [text, setText] = useState("");
  useClickOutside(settingsRef, () => setOpenSettings(false));
  const history = useHistory();

  const handleSubmit = e => {
    e.preventDefault();
    docsManager.setSearchMode(text);
    history.push(docsManager.getRoute());
    docsManager.reload();
  };

  return (
    <div className="sticky top-0 left-0 pb-5  shadow-md w-full flex justify-between items-center px-5 z-10 mb-5 lg:flex-row flex-col bg-white">
      {openMenu && <MobileMenu close={() => setOpenMenu(false)} />}
      <div className="lg:hidden w-full flex justify-between items-center">
        <Link
          to="/docs/mydocs"
          className="flex items-center justify-center mb-3"
        >
          <img
            src={require("../../assests/icons/Logo.svg").default}
            className="w-24"
            alt="Logo"
          />
        </Link>

        <GiHamburgerMenu
          className="text-xl"
          onClick={() => setOpenMenu(true)}
        />
      </div>

      <div>
        <h1 className="text-xl font-medium m-0">{`Welcome, ${
          userInfo?.data?.first_name || userInfo?.data.email
        }!`}</h1>
      </div>

      <div className="flex items-center lg:space-x-2 justify-between w-full lg:w-auto lg:justify-start">
        <form
          className="flex items-center px-1 py-0 mx-auto"
          style={{ border: "1px solid black" }}
          onSubmit={handleSubmit}
        >
          <input
            type="text"
            placeholder="Search"
            className="border-none outline-none"
            value={text}
            onChange={e => setText(e.target.value)}
          />

          <button
            className="bg-transparent outline-none border-none m-0 p-0  "
            type="submit"
          >
            <AiOutlineSearch className="text-lg" />
          </button>
        </form>

        {/* <img
          src={require("../../assests/icons/help.svg").default}
          alt="avatar"
          className="w-7 h-6"
          // onClick={() => setOpenSettings(!openSettings)}
        /> */}

        <div className="relative" ref={settingsRef}>
          <img
            src={require("../../assests/icons/avatar.svg").default}
            alt="avatar"
            className="w-7 h-7"
            onClick={() => setOpenSettings(!openSettings)}
          />

          {openSettings && (
            <div className="absolute bg-white shadow-md transition-all  w-28 py-2 px-2 right-0 space-y-2 flex flex-col text-center">
              {/* <h1>Settings</h1> */}
              <Link to="/user/settings" className="text-gray-500">
                Settings
              </Link>

              <button
                to="/login"
                onClick={() => {
                  userInfo.logout();
                  history.push("/login");
                }}
                className="text-gray-500 cursor-pointer p-0 bg-transparent border-0 outline-none  text-base "
              >
                Logout
              </button>
            </div>
          )}
        </div>
      </div>
    </div>
  );
};

export default DashboardTop;
