import canvas from './Canvas'
import mouseInput from './MouseInput'
import menuState from './MenuState'
import speechCommand from '../experimental/SpeechCommand';

var command = {inputModeChanged: false };

function getShapeAddLocation() {
    var loc = mouseInput.pointerLocation();
    var at = null;
    if (loc.x == null || loc.y == null || !canvas.isVisible(loc.x, loc.y, 40)) {
        at = canvas.getMidPoint();
    } else {
        at = loc;
    }
    at = canvas.mapPtUIScroll(at);
    at = {x: canvas.maprx(at.x), y: canvas.mapry(at.y)};
    return at;
}

command.del = () => {
    window.Module.del();
}

command.copy = () => {
    window.Module.copy();
}

command.cut = () => {
    window.Module.cut();
}

command.paste = () => {
    window.Module.paste();
}

command.pasteAt = (at) => {
    window.Module.pasteAt(at);
}

command.selectAll = () => {
    command.inputModeChanged = true;
    menuState.handleMenuClick(menuState.menus.selection);
    window.Module.selectAll();
}

command.addBox = (at) => {
    if (typeof at == 'undefined') {
        at = getShapeAddLocation();
    }
    window.Module.libraryStore().addObjectToCanvas(0, 2, at.x, at.y);
}

command.addCircle = (at) => {
    if (typeof at == 'undefined') {
        at = getShapeAddLocation();
    }
    window.Module.libraryStore().addObjectToCanvas(0, 0, at.x, at.y);
}

command.addUser = (at) => {
    if (typeof at == 'undefined') {
        at = getShapeAddLocation();
    }
    window.Module.libraryStore().addObjectToCanvas(5, 8, at.x, at.y);
}

command.addDb = (at) => {
    if (typeof at == 'undefined') {
        at = getShapeAddLocation();
    }
    window.Module.libraryStore().addObjectToCanvas(2, 6, at.x, at.y);
}

command.addCloud = (at) => {
    if (typeof at == 'undefined') {
        at = getShapeAddLocation();
    }
    window.Module.libraryStore().addObjectToCanvas(5, 9, at.x, at.y);
}

command.addNote = (at) => {
    if (typeof at == 'undefined') {
        at = getShapeAddLocation();
    }
    window.Module.libraryStore().addObjectToCanvas(5, 7, at.x, at.y);
}

command.escape = () => {
    window.Module.handleEscapeKey();
}

command.undo = () => {
    window.Module.inputReceiver().undo();
}

command.redo = () => {
    window.Module.inputReceiver().redo();
}

command.moveToFront = () => {
    window.Module.moveObjectToFront();
}

command.moveToBack = () => {
    window.Module.moveObjectToBack();
}

command.startSpeech = () => {
    speechCommand.start((r, c) => {
        var at = getShapeAddLocation();
        window.Module.libraryStore().addObjectToCanvas(r, c, at.x, at.y);
        canvas.draw();
    });
}

export default command;
