import axios from "axios";
// import { getTokenFromStorage } from "./ApiUtils";
import { constant } from "./ApiConstants";
import userInfo from "./UserInfo";

export const ApiRequestWithToken = () => {
  const config = { baseURL: constant.baseUrl };
  config.headers = { "X-Auth-Token": userInfo.token };
  const instance = axios.create(config);
  return instance;
};

export const ApiRequestWithoutToken = () => {
  const config = { baseURL: constant.baseUrl, withCredentials: true };
  const instance = axios.create(config);
  return instance;
};

export const fetcher = url =>
  ApiRequestWithToken()
    .get(url)
    .then(res => res.data);

export const signUp = data =>
  ApiRequestWithoutToken().post("/v1.1/users", data);

export const login = data => ApiRequestWithoutToken().post("/v1.1/auth", data);

export const confirmAccount = data =>
  ApiRequestWithoutToken().post("/v1.1/auth/confirm", data);

export const forgotPassword = data =>
  ApiRequestWithoutToken().post("/v1.1/auth/forgot", data);

export const resetPassword = data =>
  ApiRequestWithoutToken().post("/v1.1/auth/reset", data);

export const refreshToken = data =>
  ApiRequestWithToken().post("/v1.1/auth/refresh", { refresh_token: data });

export const loginWithGoogle = data =>
ApiRequestWithoutToken().post("/v1.1/auth/googlelogin", data);

export const loginWithApple = data =>
ApiRequestWithoutToken().post("/v1.1/auth/applelogin", data);

export const getCurrentUser = () =>
  ApiRequestWithToken().get("/v1.1/users/current");

export const getDocs = qstr => ApiRequestWithToken().get("/v1.1/docs" + qstr);

export const getDoc = docid => ApiRequestWithToken().get("/v1.1/docs/" + docid);

export const updateCurrentUser = data =>
  ApiRequestWithToken().post("/v1.1/users/current", data);

export const createNew = data => ApiRequestWithToken().post("/v1.1/docs", data);

export const openDoc = id => ApiRequestWithToken().post(`/v1.1/docs/${id}`, {});

export const deleteDoc = id => ApiRequestWithToken().delete(`/v1.1/docs/${id}`);

export const claimDoc = id => ApiRequestWithToken().post(`/v1.1/docs/${id}`, {'operation': {'type': 'claim'}});

export const duplicateDoc = id => ApiRequestWithToken().post(`/v1.1/docs/${id}`, {'operation': {'type': 'duplicate'}});

export const shareDoc = (id, data) =>
  ApiRequestWithToken().post(`/v1.1/docs/${id}/share`, data);

export const RenameDoc = (id, data) =>
  ApiRequestWithToken().post(`/v1.1/docs/${id}`, data);

export const MoveDoc = (id, data) =>
  ApiRequestWithToken().post(`/v1.1/docs/${id}`, data);

export const deleteShare = (id, email) =>
  ApiRequestWithToken().delete(`/v1.1/docs/${id}/share?email=${email}
`);

export const getResource = (id) => ApiRequestWithToken().get(`/v1.1/resources/${id}`, {responseType: 'blob'});

export const hello = (ref) => ApiRequestWithoutToken().get('/hello?ref=' + ref, {withCredentials: true});

export const getDocsLogSessions = (qstr) => ApiRequestWithToken().get('/v1.1/docslog/logs/sessions?' + qstr);
export const getADocsLogSession = (sessionid) => ApiRequestWithToken().get('/v1.1/docslog/logs/sessions/' + sessionid);
export const getDocsLogUpdate = (docid, version) => ApiRequestWithToken().get('/v1.1/docslog/logs/updates/' + docid + '/' + version);