
import { getResource } from "./ApiRequests";
import canvas from "../common/Canvas";

var resources = new Map();
var inProgress = new Set();
var failedUrl = new Map();

var remoteResouceManager = { }

const onDataFromBackend = (data, url, type) => {
    if (type == "image") {
        var img = new Image();
        img.src = window.URL.createObjectURL(data);
        img.onload = function() {
            remoteResouceManager.addToResources(url, img);
        }
    } else {
        remoteResouceManager.addToResources(url, data);
    }
}

remoteResouceManager.addToResources = (url, data) => {
    resources.set(url, data);
    window.setTimeout(()=> {
        resources.delete(url);
    }, 30 * 1000);
    canvas.draw();
}

remoteResouceManager.getResource = (url, type) => {
    if (resources.has(url)) {
        return resources.get(url);
    }
    if (failedUrl.has(url)) {
        var count = failedUrl.get(url);
        if (count >= 3) {
            return null;
        }
    }

    if (inProgress.has(url)) {
        return null;
    }

    if (url.startsWith('res://')) {
        inProgress.add(url);
        var resid = url.substr(6);    
        getResource(resid).then(data => {
            onDataFromBackend(data.data, url, type);
        }).catch(err=> {
            console.log(err);
        }).finally(()=> {
            inProgress.delete(url);
            if (failedUrl.has(url)) {
                failedUrl.set(url, failedUrl.get(url) + 1);
            } else {
                failedUrl.set(url, 1);
            }
        });
        return null;
    }
    return null;
}

export default remoteResouceManager;