import util from "./Util";
var mouseInput = { };

var downCount = 0;
var mod = null;
var hostEnv = null;

const eventPointsFromEvent = (e, state) => {
  let list = new mod.List_avabodh_lekh_event_EventPoint();
  let btn = mod.avabodh_lekh_event_MouseButton.Left;
  if (e.button == 0) {
    btn = mod.avabodh_lekh_event_MouseButton.Left;
  } else if (e.button == 1) {
    btn = mod.avabodh_lekh_event_MouseButton.Middle;
  } else if (e.button == 2) {
    btn = mod.avabodh_lekh_event_MouseButton.Right;
  } else if (e.button == 3) {
    btn = mod.avabodh_lekh_event_MouseButton.Back;
  }
  if (downCount == 0 && state == mod.avabodh_lekh_event_EventPointState.Move) {
    btn = mod.avabodh_lekh_event_MouseButton.None;
  }
  let pt = {
    x: e.offsetX,
    y: e.offsetY,
    pointerId: -1,
    state: state,
    pressure: 0,
    altitude:0,
    azimuth: 0,
    timestamp: 0,
    pointerType: mod.avabodh_lekh_event_PointerType.Mouse,
    button: btn,
    flag: mod.avabodh_lekh_event_EventPoint_Flag.None
  };
  list.push_back(pt);
  return list;
}

mouseInput.attachEvents = function(canvasElement) {
    mod = window.Module;
    hostEnv = mod.avabodh_lekh_app_HostEnv_instance();

    canvasElement.addEventListener('wheel', (e) => {
        e.preventDefault();
        let se = new mod.avabodh_lekh_event_ScrollEvent();
        se.setDelta(-e.deltaX, -e.deltaY);
        se.setType(mod.avabodh_lekh_event_ScrollEvent_Type.Generic);
        se.setLocation({x: e.offsetX, y: e.offsetY});
        se.setHasValidLocation(true);
        util.setModifiersFromEvent(e, se);
        hostEnv.onScrollEvent(se);
    });
  
    window.bean.on(canvasElement, 'click', function (e) {
        e.preventDefault();
    });
  
    window.bean.on(canvasElement, 'mousedown', function (e) {
      downCount++;
      let pe = new mod.avabodh_lekh_event_PointerEvent();
      let pts = eventPointsFromEvent(e, mod.avabodh_lekh_event_EventPointState.Down);
      pe.setPoints(pts);
      util.setModifiersFromEvent(e, pe);
      hostEnv.onPointerEvent(pe);
    });
  
    window.bean.on(canvasElement, 'mouseup', function (e) {
      if (downCount > 0) {
        downCount--;
      }
      let pe = new mod.avabodh_lekh_event_PointerEvent();
      let pts = eventPointsFromEvent(e, mod.avabodh_lekh_event_EventPointState.Up);
      pe.setPoints(pts);
      util.setModifiersFromEvent(e, pe);
      hostEnv.onPointerEvent(pe);
    });
  
    window.bean.on(canvasElement, 'mousemove', function (e) {
        e.preventDefault();
        let pe = new mod.avabodh_lekh_event_PointerEvent();
        let pts = eventPointsFromEvent(e, mod.avabodh_lekh_event_EventPointState.Move);
        pe.setPoints(pts);
        util.setModifiersFromEvent(e, pe);
        hostEnv.onPointerEvent(pe);
    });
  
    window.bean.on(canvasElement, 'mouseenter', function (e) {
      downCount = 0;
    });
  
    window.bean.on(canvasElement, 'mouseleave', function (e) {
      downCount = 0;
      hostEnv.resetPointerInputState();
    });
  
    window.bean.on(canvasElement, 'contextmenu', function (e) {
        e.preventDefault();
    });
}

export default mouseInput;
