import './Footer.css'

import React, { useState } from 'react';
import { Button } from '@material-ui/core';
import Style from './FooterStyle';
import { makeStyles } from "@material-ui/core/styles";
import RefreshIcon from '@material-ui/icons/Refresh';
import ReplayIcon from '@material-ui/icons/Replay';
import RemoveIcon from '@material-ui/icons/Remove';
import AddIcon from '@material-ui/icons/Add';
import canvas from '../../common/Canvas'
import globalState from '../../common/GlobalState'
import command from '../../common/Command'
import { GrMultiple } from "react-icons/gr";
import PageList from '../PageList/PageList'
import uiConfig from '../../common/UIConfig';

const useStyles = makeStyles(Style);
const Footer = () => {
    const classes = useStyles();
    const [count, setCount] = useState(100);
    const [showPages, setShowPages] = useState(false);
    const [pagesAnchorEl, setPagesAnchorEl] = useState(null);

    if (!uiConfig.shouldShowFooter()) {
        return null;
    }

    const zoomOnlyFooter = uiConfig.isZoomOnlyFooter();

    const handleCount = () => {
        var curCount = count - 10;
        curCount = Math.round(curCount / 10) * 10;
        if (curCount < 10) {
            curCount = 5;
        }
        setCount(curCount);
        canvas.zoom(curCount / 100);
    }

    const handleAddCount = () => {
        var curCount = 0;
        if (count < 10) {
            curCount = 10;
        } else {
            curCount = count + 10;
            curCount = Math.round(curCount / 10) * 10;
        }
        setCount(curCount);
        canvas.zoom(curCount / 100);
    }

    const handleUndo = () => {
        command.undo();
        canvas.draw();
    }

    const handleRedo = () => {
        command.redo();
        canvas.draw();
    }

    const handlePages = (e) => {
        setPagesAnchorEl(e.currentTarget)
        setShowPages(!showPages);
    }

    globalState.setZoomHandler(function(zoom) {
        var val = Math.round(zoom * 100);
        setCount(val);
    });

    return (
        <>
        {showPages && <PageList pos={{placement: 'top', anchorEl: pagesAnchorEl}} close={()=>{setShowPages(false)}}></PageList>}
        <div className="footer-area">
            {uiConfig.shouldShowPagesInFooter() && <div className="pages-button">
                <Button className="resButton" onClick={handlePages}><GrMultiple className={classes.iconClass}/></Button>
            </div>}
            {!zoomOnlyFooter && <div className="undo-redo-buttons">
                <Button className="resButton"><ReplayIcon className={classes.iconClass} onClick={() => handleUndo()}/></Button>
                <Button className="resButton"><RefreshIcon className={classes.iconClass} onClick={() => handleRedo()}/> </Button>
            </div>}
            <div className='zoom-info'>
                <Button className={classes.countBtn}><RemoveIcon className={classes.iconClass} onClick={() => handleCount()} /></Button>
                <div className={classes.valueClass}> {count}% </div> 
                <Button className={classes.countBtn}><AddIcon className={classes.iconClass} onClick={() => handleAddCount()} /></Button>
            </div>
        </div>
        </>
    );
}

export default Footer;