import canvas from "../../common/Canvas";

var pageManager = {
    curIdx: 0,
    pages: [
        {name: "page 1", id: "page1"},
        {name: "page 2 sdf  fd fds fsd fds fdsfsf sdf sdfd fsdfsdfsdf sdf", id: "page2"},
        {name: "page 3", id: "page3"},
        {name: "page 4", id: "page4"},
        {name: "page 5", id: "page5"},
        {name: "page 6", id: "page6"},
        {name: "page 7", id: "page7"},
        {name: "page 8", id: "page8"},
        {name: "page 9", id: "page9"},
        {name: "page 10", id: "page10"},
        {name: "page 11", id: "page11"},
        {name: "page 12", id: "page12"},
        {name: "page 13", id: "page13"},
        {name: "page 14", id: "page14"},
        {name: "page 15", id: "page15"},
        {name: "page 16", id: "page16"},
        {name: "page 17", id: "page17"},
        {name: "page 18", id: "page18"},
        {name: "page 19", id: "page19"},
        {name: "page 20", id: "page20"},
        {name: "page 21", id: "page21"},
        {name: "page 22", id: "page22"},
        {name: "page 23", id: "page23"},
    ]
}

var editor = null;
pageManager.load = () => {
    if (editor == null) {
        editor = window.Module.startPageEditing();
    }
}

pageManager.getPages = () => {
    var numPages = editor.numPages();
    var pages = [];
    for (let i = 0; i < numPages; i++) {
        var page = {name: editor.getPageName(i), id: editor.getPageId(i)};
        pages.push(page);
    }
    return pages;
}

pageManager.changeOrder = (oldIdx, newIdx) => {
    if (oldIdx == newIdx) {
        return;
    }
    editor.reorder(oldIdx, newIdx);
    canvas.draw();
}

pageManager.add = (name) => {
    if (!name) {
        name = "Page " + (editor.numPages() + 1)
    }
    editor.addPage(name);
    canvas.draw();
}

pageManager.remove = (idx) => {
    editor.deletePage(idx);
    canvas.draw();
}

pageManager.rename = (idx, newName) => {
    editor.setPageName(idx, newName);
    canvas.draw();
}

pageManager.duplicate = (idx) => {
    editor.duplicatePage(idx);
    canvas.draw();
}

pageManager.current = () => {
    return editor.activePageIndex();
}

pageManager.setCurrent = (idx) => {
    editor.select(idx);
    canvas.draw();
}

export default pageManager;