import {LineEq, inOrder, turnAngle} from './Geometry'

class Features {
    constructor() {
        this.backSide = false;

        this.indexBent = false;
        this.middleBent = false;
        this.ringBent = false;

        this.indexInOrder = false;
        this.middleInOrder = false;
        this.ringInOrder = false;

        this.indexCurved = false;
        this.middleCurved = false;
        this.ringCurved = false;
    }
    print() {
        console.log("idb:", this.indexBent, ", midb", 
        this.middleBent, ", rb", this.ringBent, ', back:', this.backSide,
        ', idxs', this.indexInOrder)
    }
}

function getSide(p1, p2, p3) {
    var eq = new LineEq(p1, p2);
    return eq.pval(p3) >= 0;
}

function isCurved(p1, p2, p3, p4) {
    var ta = turnAngle([p1, p2, p3, p4]);
    var deg = (ta * 180) / Math.PI;
    return Math.abs(deg) >= 90;
}

export function getFeatures(pts, hand) {
    var features = new Features();
    
    features.indexBent = !inOrder([pts[0], pts[5], pts[8]]);
    features.middleBent = !inOrder([pts[0], pts[9], pts[12]]);
    features.ringBent = !inOrder([pts[0], pts[13], pts[16]]);

    features.indexInOrder = inOrder([pts[5], pts[6], pts[7], pts[8]]);
    //features.middleInOrder = inOrder([pts[9], pts[10], pts[11], pts[12]]);
    //features.ringInOrder = inOrder([pts[13], pts[14], pts[15], pts[16]]);

    features.indexCurved = isCurved(pts[5], pts[6], pts[7], pts[8]);
    features.middleCurved = isCurved(pts[9], pts[10], pts[11], pts[12]);
    features.ringCurved = isCurved(pts[13], pts[14], pts[15], pts[16]);
                             
    features.backSide = !getSide(pts[0], pts[5], pts[9]) || 
                        !getSide(pts[0], pts[5], pts[13]) ||
                        !getSide(pts[0], pts[5], pts[17])
    if (hand == 'left') {
        features.backSide = !features.backSide;
    }
    return features;
}