class WSManager
{
    constructor() {
        this.ws = null;
        this.lekhCore = null;
        this.setTimer();
    }

    setTimer = () => {
        let self = this;
        window.setInterval(() => {
            if (self.ws == null) {
                return;
            }
            try {
                self.ws.send('{}');
            } catch (e) { }
        }, 15000);
    }

    send(docid, data) {
        if (this.ws != null) {
            try {
                this.ws.send(data);
            } catch(e) {
                this.lekhCore.coreIO().onWebSocketDisconnect();
                return false;
            }
            return true;
        }
    }

    connect(docid, location) {
        this.lekhCore = window.Module.avabodh_lekh_app_LekhCore_instance();
        this.ws = new window.WebSocket(location);
        let self = this;

        this.ws.onopen = function(event) {
            console.log('connection opened: ', event);
            self.lekhCore.coreIO().onWebSocketConnect();    
        };
    
        this.ws.onmessage = function(event) {
            self.lekhCore.coreIO().onWebSocketMessage(event.data);
        };
    
        this.ws.onclose = function(event) {
            console.log('connection closed:', event);
            self.lekhCore.coreIO().onWebSocketDisconnect();
        };
    
        this.ws.onerror = function(event) {
            console.log('connection error: ', event);
            self.lekhCore.coreIO().onWebSocketDisconnect();
        };
    }

    disconnect() {
        if (this.ws != null) {
            try {
                this.ws.close();
            } catch (e) { }
        }
    }
}

let wsmanager = new WSManager();
export default wsmanager;