import './ShapeProps.css'

import React from "react"
import Popper from '@material-ui/core/Popper';
import ClickAwayListener from '@material-ui/core/ClickAwayListener'
import keyboardInput from '../../common/KeyboardInput';

const getTopPlacement = (rect) => {
    const getBoundingClientRect = () =>  {
        return {
            x: rect.x,
            y: rect.y,
            width:rect.width,
            height: rect.height,
            top: rect.y,
            left: rect.x,
            bottom: rect.y + rect.height,
            right: rect.x + rect.width
        }
    }

    var anchorEl = {
        clientWidth: rect.width,
        clientHeight: rect.height,
        getBoundingClientRect,
    }

    return {anchorEl: anchorEl, placement: 'top'}
}

const Help = (props) => {
    const handleClickAway = () => {
        props.showShapeHelp(null);
    }

    const handleKey = (event) => {
        let key = event.key.toLowerCase();
        if (key == 'escape') {
            handleClickAway();
            return true;
        }
        return false;
    }

    React.useEffect(() => {
        keyboardInput.addListener(handleKey);
        return () => {
            keyboardInput.removeListener(handleKey);
        }
    }, [])

    let rect = props.input.fromRect
    rect.y -= 4;
    let pos = getTopPlacement(rect);
    let helpText = props.input.content;
    helpText = helpText.replace(" tap ", " click ");

    return (
        <ClickAwayListener onClickAway={handleClickAway}>
            <Popper open={true} {...pos}>
                <div className="shape_help_paper">
                    {<div dangerouslySetInnerHTML={{ __html: helpText }} />}
                </div>
            </Popper>
        </ClickAwayListener>
    );
}

export default Help;