import React, { useState } from "react";
import InputField from "../../components/InputField/InputField";
import { Link } from "react-router-dom";
import MiniLoader from "../../components/Loaders/MiniLoader";
import { login } from "../../utils/ApiRequests";
import { useHistory } from "react-router-dom";
import { toast } from "react-toastify";
import {
  setExpiryTimeToStorage,
  setRefreshTokenToStorage
} from "../../utils/ApiUtils";

const OTPLogin = () => {
  const [loginForm, setloginForm] = useState({
    email: ""
  });
  const [loading, setLoading] = useState(false);
  const history = useHistory();
  const [successText, showSuccessText] = useState(false);
  const [error, setError] = useState(null);
  //   const [step, setStep] = useState(1);

  //   I am here

  const handleChange = e => {
    const name = e.target.name;
    const value = e.target.value;
    const newEntry = { [name]: value };
    setloginForm({ ...loginForm, ...newEntry });
  };

  const proceed = () => {
    if (loginForm.email === "") {
      return false;
    }
    return true;
  };

  const handleSubmit = e => {
    e.preventDefault();
    setLoading(true);

    login(
      successText
        ? { email: loginForm.email, code_type: "otp", code: loginForm.code }
        : {
            email: loginForm.email,
            type: "otp"
          }
    )
      .then(res => {
        showSuccessText(true);

        if (loginForm.code) {
          toast.success("Login Successful");
          setRefreshTokenToStorage(res.data.refresh_token);
          setExpiryTimeToStorage(res.data.expires_at);
          history.push("/docs/mydocs");
        }
      })
      .catch(err => {
        setError(err?.response?.data?.message);
      })
      .finally(() => setLoading(false));
  };

  return (
    <div className="w-full min-h-screen flex justify-center items-center ">
      <form
        //   className=""
        onSubmit={handleSubmit}
        className="w-full lg:w-3/5 px-5 lg:px-10 py-3 lg:py-6 shadow-md"
      >
        <h1 className="font-medium text-xl  text-center mb-7">
          Sign in to Lekh{" "}
        </h1>

        <>
          {!successText && (
            <InputField
              name="email"
              label="Email"
              placeholder="you@address.com"
              required={true}
              type="email"
              handleChange={handleChange}
              value={loginForm.email}
            />
          )}

          {successText && (
            <>
              <InputField
                name="code"
                label="OTP"
                placeholder="000000"
                required={true}
                type="number"
                handleChange={handleChange}
                value={loginForm.code}
              />

              <p className="-mt-3 font-medium">
              Please enter the code you received in your email or open the link from the email.
              </p>
              <p className="-mt-3 italic text-sm">
              (Please check the <strong>"All Mail"</strong> folder if you don’t find the email in Inbox)
              </p>
            </>
          )}
          {error && !loading && (
            <span className="-mt-3 block italic text-red-500 text-sm">
              {error}
            </span>
          )}
        </>

        <div className="flex ">
          {loading ? (
            <MiniLoader />
          ) : (
            <div className="flex items-center gap-5">
              <button
                type="submit"
                className={`py-2 lg:py-3.5 px-12 font-Poppins bg-lkhRed text-white rounded-3xl outline-none border-0 ${
                  !proceed() ? "bg-lkhGrey cursor-not-allowed" : ""
                }`}
                disabled={!proceed()}
              >
                {successText ? "Login" : "Proceed"}
              </button>
            </div>
          )}
        </div>

        <div className="flex items-center gap-2 mt-5 lg:flex-row flex-col">
          <button
            className="flex items-center space-x-2 py-1 bg-white border-1 outline-none px-4 text-lg"
            onClick={e => {
              history.push("/login");
            }}
          >
            {/* <GrMail /> */}
            <span>Return to Sign In</span>
          </button>

          {/* <GoogleLogin
            render={render => (
              <button className="flex items-center space-x-2 py-1 bg-white border-1 outline-none px-4 text-lg">
                <img
                  src={require("../../assests/icons/google.svg").default}
                  alt="Google"
                />{" "}
                <span>Sign in with Google</span>
              </button>
            )}
          /> */}
        </div>

        <div className="space-y-1 mt-2">
          <Link to="/forgot" className="text-lg font-medium block">
            Forgot Password?
          </Link>

          <p className="text-lg font-medium m-0">
            Don't have an account?{" "}
            <Link to="/sign-up" className="font-normal">
              Sign up here
            </Link>
          </p>
        </div>
      </form>
    </div>
  );
};

export default OTPLogin;
