import { AiOutlineClose } from "react-icons/ai";
import { Dialog, IconButton } from "@material-ui/core";
import { useEffect, useState, useRef } from "react";
import MiniLoader from "../Loaders/MiniLoader";
import docsManager from "../../utils/DocsManager";
import userInfo from "../../utils/UserInfo";
import { AiFillFolder, AiOutlineRight } from "react-icons/ai";
import { BiArrowBack } from "react-icons/bi";
import uiConfig from "../../common/UIConfig";
import { constant } from "../../utils/ApiConstants";
import messaging from "../../common/Messaging";

const LekhItemPicker = ({ close, pick, defaultTitle, pickText, folderOnly }) => {
  var [loading, setLoading] = useState(false);
  const [data, setData] = useState();
  const [parent, setParent] = useState(userInfo?.data?.userid);
  const [loginState, setLoginState] = useState(uiConfig.isUsingLekhBackend()? userInfo.state: 'init');
  const [error, setError] = useState(null);
  const [parentDoc, setParentDoc] = useState(null);
  const [selectedIdx, setSelectedIdx] = useState(-1);

  const [cache, setCache] = useState(new Map());
  const [childCache, setChildCache] = useState(new Map());

  const refreshToken = useRef('dummy');

  const signingIn = loginState == 'loading';
  const signedIn = loginState == 'available';
  loading = loading || signingIn;
  
  useEffect(() => {
    if (!parent) {
      return;
    }
    setLoading(true);
    getDoc(parent)
    .then(resp=>{
      setParentDoc(resp);
    })
    .then(()=> getChildren(parent || userInfo?.data?.userid))
    .then(res => {
      setData(res);
      setLoading(false);
    })
    .catch(err => console.log(err.response));
  }, [parent]);

  useEffect(() => {
    fetchAccessToken();
    messaging.event.addListener(fetchAccessToken);
    return () => messaging.event.removeListener(fetchAccessToken);
  }, []);

  var title = "";
  var pickClass = '';
  if (signedIn && parentDoc) {
    title = parentDoc.name;
  } else {
    title = defaultTitle;
  }

  if (!folderOnly && selectedIdx < 0) {
    pickClass = "text-white";
  }

  var signInMessage = (<>
    <div className="text-center font-bold">Please <a className="text-blue-600" href={constant.appUrl + '#/login'} target="_blank">sign in</a></div>
    <br/>
    <div>You need to be signed-in into the Lekh account for importing / exporting.</div>
    </>);

  const fetchAccessToken = () => {
    if (!uiConfig.isUsingLekhBackend()) {
      var rtoken = messaging.data;
      if (rtoken == refreshToken.current || !rtoken) {
        return;
      }
      refreshToken.current = rtoken;
      setLoginState('loading');
      userInfo.getAccessTokenOnDemand(rtoken).then(result => {
        if (result) {
          setSelectedIdx(-1);
          setParent(userInfo?.data?.userid);
          setLoginState('available');
        } else {
          setLoginState('error');
        }
      });
    }
  }

  const getDoc = async (docid) => {
    if (!docid || docid == userInfo?.data?.userid) {
      return {docid: docid, name: 'My Documents', parent: null}
    }
    if (cache.has(docid)) {
      return cache.get(docid);
    }
    var doc = await docsManager.getADoc(docid);
    cache.set(docid, doc);
    return doc;
  }

  const getChildren = async (docid) => {
    if (childCache.has(docid)) {
      return childCache.get(docid);
    }
    if (folderOnly) {
      var children = await docsManager.getChildFolders(docid);  
    } else {
      var children = await docsManager.getChildItems(docid);
    }
    
    childCache.set(docid, children);
    for (let item of children) {
      cache.set(item.docid, item);
    }
    return children;
  }

  const handleSubmit = e => {
    e.preventDefault();
    if (!folderOnly) {
      if (selectedIdx < 0) {
        return;
      }
      pick(data[selectedIdx].docid);
    } else {
      let docid = parentDoc.docid;
      setLoading(true);
      docsManager.getChildItems(docid).then(children => {
        pick(parentDoc.docid, children);
      }).catch(e=> {
        setLoading(false);
      });
    }
  };

  var iframe = null;
  if (!uiConfig.isUsingLekhBackend()) {
    var prefix = '';
    if (constant.appUrl.startsWith('http://localhost')) {
      prefix = '/integ/ac/confluence/res'
    }
    const iframeUrl = constant.appUrl + prefix + "/atlassian_util.html";
    iframe = <iframe style={{display:'block'}} width="0" height="0" frameBorder="0" scrolling="no" vspace="0" hspace="0" marginHeight="0" marginWidth="0" src={iframeUrl}></iframe>
  }

  return (
    <Dialog open={true} className="px-5" aria-labelledby="Share">
      <form
        className={`py-2 h-72 w-64 lg:w-96 bg-white`}
        onSubmit={handleSubmit}
      >
        {iframe}
        <div className="flex items-center justify-between my-2 px-3">
          {parent != userInfo?.data?.userid ? (
            <BiArrowBack
              className="text-xl cursor-pointer"
              onClick={() => {
                setParent(parentDoc.parent);
                setSelectedIdx(-1);
              }}
            />
          ) : (
            <div />
          )}

          <h4 className="text-center m-0 w-44 truncate">
            {title}
          </h4>

          <AiOutlineClose
            className="text-xl cursor-pointer"
            onClick={() => close()}
          />
        </div>
        <hr />

        {loading && (
          <div className="mt-6">
            <MiniLoader />
          </div>
        )}
        {!loading && !signedIn && signInMessage}
        {!loading && signedIn && (
          <>
            <div className="h-40 space-y-2 py-2 overflow-y-auto px-3">
              {data?.map((m, i) => {
                let isFolder = m.type == 'folder';
                let backClass = 'hover:bg-gray-200';
                if (selectedIdx == i) {
                  backClass = 'bg-gray-200';
                }
                const onClick = () => {
                  if (isFolder) {
                    setSelectedIdx(-1);
                    setParent(m.docid);
                  } else {
                    setSelectedIdx(i);
                  }
                }
                return (<div
                  className={`move-grid text-lg cursor-pointer ` + backClass}
                  key={i}
                >
                  <div
                    className="flex items-center space-x-2 cursor-pointer"
                    onClick={onClick}
                  >
                    {isFolder &&<AiFillFolder />}
                    {!isFolder && (
                      <IconButton color="inherit" className="w-3">
                        <img
                            src={require("../../assests/icons/lekhboard-small.svg").default}
                            className="w-3" />
                      </IconButton>)}
                    <span className="w-40  lg:w-44 truncate">{m.name}</span>
                  </div>
                  {isFolder && <AiOutlineRight
                    className="cursor-pointer text-right ml-auto"
                    onClick={() => {setParent(m.docid); setSelectedIdx(-1)}}
                  />}
                </div>
              )})}
            </div>
            <hr />
            <div className="text-right mt-3 px-3">
              <button
                type="submit"
                className={pickClass + " font-medium lg:text-lg px-3 py-1 outline-none border-none border-0 mx-auto"}
              >
                {pickText}
              </button>
            </div>
          </>
        )}
      </form>
    </Dialog>
  );
};

export default LekhItemPicker;
