import React from "react";
import ReactDOM from "react-dom";
import "./index.css";
import "./App.css";
import AppComponent from "./App"
import Viewer from "./common/Viewer";
import { HashRouter } from "react-router-dom";
import reportWebVitals from "./reportWebVitals";
import Routes from "./routes/Routes";
import qs from 'qs';
import docInfo from './utils/DocInfo'
import {hello} from './utils/ApiRequests'
import integ from "./integ/integ";
import uiConfig from "./common/UIConfig";

var openDoc = false;
var viewer = false;
var params = qs.parse(window.location.search, { ignoreQueryPrefix: true })

var docid = params.docid;
var _new = params._new;
var offline = params.offline;
var ref = params.ref;

if (integ.isIntegration(params)) {
  uiConfig.initForIntegMode(params);
  openDoc = true;
  viewer = uiConfig.isIntegViewer();
} else {
  if (offline) {
    uiConfig.initForOfflineMode();
  } else {
    uiConfig.initForLekhBackendMode();
  }

  if (ref) {
    hello(ref);
    if (!docid && !_new && window.location.pathname == '/' && window.location.hash == '') {
      window.history.replaceState({}, "", "/")
    }
  }

  if (window.location.pathname == '/' && (!!docid || _new == 'true' || offline == 'true')) {
    docInfo.docid = docid;
    openDoc = true;
  }
}

ReactDOM.render(
  <React.StrictMode>
    {openDoc && !viewer && <AppComponent />}
    {openDoc && viewer && <Viewer />}
    {!openDoc &&<HashRouter>
      <Routes />
    </HashRouter>}
  </React.StrictMode>,
  document.getElementById("root")
);

// If you want to start measuring performance in your app, pass a function
// to log results (for example: reportWebVitals(console.log))
// or send to an analytics endpoint. Learn more: https://bit.ly/CRA-vitals
reportWebVitals();
