import OfflineList from "./OfflineList";
import OfflineViewer from "./OfflineViewer";
import SessionList from "./SessionList";
import SessionViewer from "./SessionViewer";

const DocsLogRoutes = [
    { path: "/docslog/sessionlist", component: SessionList, exact: true },
    { path: "/docslog/sessionviewer", component: SessionViewer, exact: true },
    { path: "/docslog/offlinelist", component: OfflineList, exact: true },
    { path: "/docslog/offlineviewer", component: OfflineViewer, exact: true }
]

export default DocsLogRoutes;