import './PageList.css'

import React, { useState } from "react"
import Popper from '@material-ui/core/Popper';
import ClickAwayListener from '@material-ui/core/ClickAwayListener'
import { AiOutlineClose } from "react-icons/ai";
import { AiOutlinePlus } from "react-icons/ai";
import { MdDragHandle } from "react-icons/md";
import { HiOutlineDotsHorizontal } from "react-icons/hi";
import pageManager from './PageManager';
import { DragDropContext, Droppable, Draggable } from "react-beautiful-dnd";
import { Dialog } from "@material-ui/core";
import InputField from "../InputField/InputField";
import keyboardInput from '../../common/KeyboardInput';

var showingDialogForKeyboardInput = false;

const RenameModal = ({close, orig, rename}) => {
    const [newName, setName] = useState(orig);
  
    React.useEffect(() => {
        showingDialogForKeyboardInput = keyboardInput.showingDialog;
        keyboardInput.showingDialog = true;
        return () => {
            keyboardInput.showingDialog = showingDialogForKeyboardInput;
        }
    }, [])

    const handleSubmit = e => {
      e.preventDefault();
    };
  
    return (
      <Dialog open={true} className="px-5">
        <form
          action=""
          className="py-2 w-64 lg:w-96 bg-white "
          onSubmit={handleSubmit}
        >
          <div className="flex justify-between items-center my-2 px-2">
            <div />
            <h4 className="text-center m-0">Rename</h4>
  
            <AiOutlineClose className="text-xl cursor-pointer" onClick={close} />
          </div>
  
          <hr className="-mb-5" />
  
          <div className="px-2">
            <InputField
              // label="Rename"
              placeholder="Name"
              handleChange={e => setName(e.target.value)}
              value={newName}
            />
            <div className="text-right">
                <button
                  type="submit"
                  className="text-white font-medium lg:text-lg px-3 py-1 bg-lkhRed outline-none border-none border-0 mx-auto"
                  onClick={()=>rename(newName)}
                >
                  Rename
                </button>
            </div>
          </div>
        </form>
      </Dialog>
    );
};

const ConfirmModal = ({cancel, accept}) => {
    return (<Dialog open={true}>
    <div className="w-64 lg:w-72 py-8 px-5 space-y-10">
      <p className="font-sans text-center text-lg">
        Are you sure you want to delete?
      </p>

      <div className="grid grid-cols-2 gap-5">
        <button
          className="button border border-lkhRed text-center py-3 bg-white "
          onClick={() => cancel()}
        >
          Cancel
        </button>

        <button
          onClick={() => accept()}
          className="button bg-lkhRed text-white border-0"
        >
          Delete
        </button>
      </div>
    </div>
  </Dialog>)
}


const PageContextMenu = (props) => {
    const [showRenameDialog, setShowRenameDialog] = useState(false);
    const [showDeleteDialog, setShowDeleteDialog] = useState(false);

    var page = pageManager.getPages()[props.idx];

    const onRename = (newName) => {
        setShowRenameDialog(false);
        pageManager.rename(props.idx, newName)
        window.setTimeout(()=> {
            props.close();
        }, 100);
    }

    const onDelete = () => {
        setShowDeleteDialog(false);
        pageManager.remove(props.idx);
        window.setTimeout(()=> {
            props.close();
        }, 100);
    }

    const onDpulicte = () => {
        pageManager.duplicate(props.idx);
        window.setTimeout(()=> {
            props.close();
        }, 100);
    }

    return (
        <>
        {showRenameDialog && <RenameModal orig={page.name} rename={onRename} close={()=>setShowRenameDialog(false)}></RenameModal>}
        {showDeleteDialog && <ConfirmModal accept={onDelete} cancel={()=>setShowDeleteDialog(false)}></ConfirmModal>}
        <div className="absolute top-0 right-3 py-3 px-7 shadow-lg bg-white space-y-4 text-left z-20">
            <p className="text-sm cursor-pointer m-0 mb-2" onClick={onDpulicte}>
            Duplicate
            </p>
            <p className="text-sm cursor-pointer m-0 mb-2" onClick={() => setShowRenameDialog(true)}>
            Rename
            </p>
            <p className="text-sm cursor-pointer m-0 mb-2" onClick={() => setShowDeleteDialog(true)}>
            Delete
            </p>
        </div>
        </>
    )
}

const useRefState = initialValue => {
    const [state, setState] = React.useState(initialValue)
    const stateRef = React.useRef(state)
    React.useEffect(
        () => { stateRef.current = state },
        [state]
    )
    return [state, stateRef, setState];
}

const PageList = (props) => {
    const [counter, counterRef, setCounter] = useRefState(0);
    const [anchorEl, setAnchorEl] = useState(null);
    const [showMenu, setShowMenu] = useState(false);
    const [menuIdx, setMenuIdx] = useState(0);

    pageManager.load();

    React.useEffect(() => {
        window.setTimeout(()=> {
            var idx = pageManager.current();
            var item = pageManager.getPages()[idx];
            var row = document.getElementById('pages-list-row-' + item.id);
            if (row) {
                row.scrollIntoView({block: "nearest"});
            }
        }, 100);
        window.wsmanager.event.addListener(onWebsocketUpdate);
        return () => {
            window.wsmanager.event.removeListener(onWebsocketUpdate);
        }
    }, [])

    const onWebsocketUpdate = (data) => {
        try {
            var msg = JSON.parse(data);
            if (msg.updates.type == 'addpages' || msg.updates.type == 'delpages' || msg.updates.type == 'changepageattr') {
                reload();
            }
        } catch (e) {
            console.log(e);
        }
    }

    const reload = function () {
        setCounter(counterRef.current + 1);
    }

    const addPage = () => {
        pageManager.add();
        window.setTimeout(()=> {
            var listDiv = document.getElementById("page-list-container");
            listDiv.scrollTop = listDiv.scrollHeight;
        }, 200);
        reload();
    }

    const setCurrentPage = (idx) => {
        if (showMenu) {
            return;
        }
        pageManager.setCurrent(idx);
        window.layoutCanvas()
        reload();
    }

    const onDragStart = (evt) => {
        setShowMenu(false);
    }

    const onDragEnd = (evt) => {
        if (!evt.source || !evt.destination) {
            return;
        }
        pageManager.changeOrder(evt.source.index, evt.destination.index);
        reload();
    }

    const getRenderItem = (items) => (provided, snapshot, rubric) => {
        var idx = rubric.source.index;
        var item = items[idx];
        var className = "pages-list-row";
        if (idx == pageManager.current()) {
            className += " bg-gray-200"
        }
        var rowId = 'pages-list-row-' + item.id;
        return (
        <div ref={provided.innerRef}
            {...provided.draggableProps}
            id={rowId}
            className={className} onClick={()=>{setCurrentPage(idx)}}>
            <div className="flex w-full">
                <div {...provided.dragHandleProps}><MdDragHandle  className="text-xl ml-2"/></div>
                <div className="pages-list-page-name ml-2">{item.name}</div>
                <HiOutlineDotsHorizontal className="text-xl cursor-pointer pages-list-page-option mr-2" onClick={(e)=>onPageOption(e, idx)} />
            </div>
        </div>
        );
    }
    const renderItem = getRenderItem(pageManager.getPages());

    const onPageOption = (e, idx) => {
        setAnchorEl(e.currentTarget);
        setMenuIdx(idx);
        setShowMenu(true)
    }

    const onClickAway = () => {
        if (!showMenu) {
            props.close()
        }
    }

    const closeContextMenu = () => {
        setShowMenu(false);
    }

    var position = props.pos;
    if (!position) {
        position = {style: { position: 'fixed', top: '50%', left: '50%', transform: 'translate(-50%, -50%)' }}
    }

    return (
    <>
    {showMenu && <ClickAwayListener onClickAway={()=>setShowMenu(false)}>
        <Popper open={true} placement={'right'} anchorEl={anchorEl}>
            <PageContextMenu close={closeContextMenu} idx={menuIdx}></PageContextMenu>
        </Popper>
    </ClickAwayListener>}
    <ClickAwayListener onClickAway={onClickAway}>
        <Popper open={true} {...position}>
            <div className="page-list-container shadow-xl">
                <div className="flex justify-between items-center my-2 px-2">
                    <div />
                    <h4 className="text-center m-0">Pages</h4>
                    <AiOutlinePlus className="text-xl -mr-10 cursor-pointer" onClick={addPage} />
                    <AiOutlineClose className="text-xl cursor-pointer" onClick={props.close} />
                </div>
                <hr/>
                <DragDropContext onDragEnd={onDragEnd} onDragStart={onDragStart}>
                    <Droppable droppableId="list" renderClone={renderItem}>
                        {provided => (
                        <div id="page-list-container" className="pages-list w-full" ref={provided.innerRef} {...provided.droppableProps}>
                            {pageManager.getPages().map((item, idx) => {
                                return (
                                    <Draggable draggableId={item.id} index={idx} key={item.id}>
                                        {renderItem}
                                    </Draggable>)
                            })}
                            {provided.placeholder}
                        </div>
                        )}
                    </Droppable>
                </DragDropContext>
            </div>
        </Popper>
    </ClickAwayListener> </>)
}

export default PageList;