
import { Dialog } from "@material-ui/core";
import DialogTitle from '@material-ui/core/DialogTitle';
import DialogActions from '@material-ui/core/DialogActions';
import DialogContent from '@material-ui/core/DialogContent';
import TextField from '@material-ui/core/TextField';
import Button from '@material-ui/core/Button';
import React, {useState} from "react";

export const FileNameInputDialog = ({title, cancelText, okText, onCancel, onAccept, initialValue }) => {
    const [value, setValue] = useState(initialValue);
    var okDisabled = value.trim() == '';
    return (
    <Dialog onClose={()=>{}} aria-labelledby="simple-dialog-title" open={true} style={{zIndex:1550}}>
        <DialogTitle id="simple-dialog-title">{title}</DialogTitle>
        <DialogContent>
            <TextField
                autoFocus
                margin="dense"
                id="name"
                fullWidth
                variant="outlined"
                value={value}
                onChange={(e) => setValue(e.target.value)}
            />
        </DialogContent>
        <DialogActions>
            <Button onClick={onCancel}>{cancelText}</Button>
            <Button onClick={()=>onAccept(value)} disabled={okDisabled}>{okText}</Button>
        </DialogActions>
    </Dialog>
    );
};

export const ConfirmDialog = ({title, msg, okText, cancelText, onAccept, onCancel }) => {
    return (
    <Dialog onClose={()=>{}} aria-labelledby="simple-dialog-title" open={true} style={{zIndex:1550}}>
        <DialogTitle id="simple-dialog-title">{title}</DialogTitle>
        <DialogContent>
            <div>{msg}</div>
        </DialogContent>
        <DialogActions>
            <Button onClick={onCancel}>{cancelText}</Button>
            <Button onClick={onAccept}>{okText}</Button>
        </DialogActions>
    </Dialog>
    );
};

export const MessageDialog = ({title, msg, okText, onAccept }) => {
    return (
    <Dialog onClose={()=>{}} aria-labelledby="simple-dialog-title" open={true} style={{zIndex:1550}}>
        <DialogTitle id="simple-dialog-title">{title}</DialogTitle>
        <DialogContent>
            <div>{msg}</div>
        </DialogContent>
        <DialogActions>
            <Button onClick={onAccept}>{okText}</Button>
        </DialogActions>
    </Dialog>
    );
};

